import * as yup from "yup";

const schema = yup
  .object({
    nombre: yup.string().required("Complete"),
    iso: yup.string().required("Complete"),
    simbolo: yup.string().required("Complete"),
    sunat_codigo: yup.string().required("Complete"),
    dolar: yup.string().required("Complete"),
    empresa_id: yup.string().required("Complete"),
  })
  .required();

export default schema;
