import { useEffect, useContext, useState, useCallback } from "react";
import {
  Button,
  Grid,
  Image,
  Card,
  Icon,
  List,
  Table,
  Feed,
  Label,
  Menu,
} from "semantic-ui-react";
import { Link, useLocation, useNavigate } from "react-router-dom";
import Moment from "react-moment";
import EmpresaContext from "../../context/EmpresaContext";
import clienteService from "../../services/cliente";
import ventaService from "../../services/ventas";
import { updated } from "../../libs/updated";
import { LabelUpdate } from "../../components/LabelUpdate";

import useWindowSize from "../../hooks/useWindowSize";

import Localbase from "localbase";
import InputSearch from "../../components/InputSearch";

const Stadicticas = ({ state, resolve }) => {
  const [estadisticas, setEstadisticas] = useState({});
  useEffect(() => {
    console.log("component did amount");
    setEstadisticas(resolve(state));

    return () => setEstadisticas({});
  }, [state]);

  return (
    <Card>
      <Card.Content>
        <Card.Header content={estadisticas?.title} />
        <Card.Meta content={estadisticas?.mensaje} />

        {estadisticas.detalles &&
          Object.keys(estadisticas.detalles).map((v, index) => {
            return (
              <Card.Description
                content={`Total: ${
                  estadisticas.detalles[v].simbolo
                } ${estadisticas.detalles[v].total?.toFixed(2)}
                  (${estadisticas.detalles[v].iso})
                `}
              />
            );
          })}
      </Card.Content>
    </Card>
  );
};

const Contactos = () => {
  let navigate = useNavigate();
  const location = useLocation();
  const { empresa } = useContext(EmpresaContext);
  const { windowSize: size } = useWindowSize();

  // const [actualData, setActualData] = useState([]);
  const [selected, setSelected] = useState(false);
  const [contactos, setContactos] = useState([]);
  const [ventas, setVentas] = useState([]);

  const [searchInput, setSearchInput] = useState(false);

  const db = new Localbase("db");

  const [query, setQuery] = useState({
    page: 0,
    limit: 5000,
    order: { id: "ASC" },
  });

  const get = useCallback(async (reload = false, qm = false) => {
    const empresa_id = empresa.empresa.id;
    // const local_id = empresa.id;
    const fetch = await clienteService.show(empresa_id, qm);
    setContactos(fetch.reverse());
  }, []);

  const handleSetContacto = (contacto) => {
    setSelected(contacto);
    getVentas(contacto);
  };

  const getVentas = async (contacto) => {
    console.log("contacto", contacto.id);
    const query = {};
    query.filter = [["cliente_id", "=", contacto.id]];
    query.limit = 50;
    query.order = { id: "DESC" };
    const ventas = await ventaService.show(
      empresa.empresa.id,
      empresa.id,
      1,
      query,
      false
    );

    setVentas(ventas || []);
  };

  const handleNewContact = () => {
    navigate("/welcome/clientes/create", {
      state: {
        backgroundLocation: location,
        model: { empresa_id: empresa.empresa.id },
        empresa,
      },
    });
  };

  const handleEditContact = () => {
    navigate("/welcome/clientes/" + selected.id, {
      state: {
        backgroundLocation: location,
        model: selected,
        empresa,
      },
    });
  };

  /**
   *
   * @param {*} ventas
   *
   * @returns {title : string ,mensaje: string ,descripcion: array}
   */
  const resolverStadistica = (ventas) => {
    const res = {};
    res.title = "Ventas";
    res.mensaje = "Ultimos 50 Documento";
    res.detalles = {};

    for (let i = 0; i < ventas.length; i++) {
      const el = ventas[i];
      if (!res.detalles.hasOwnProperty(el.moneda.nombre)) {
        res.detalles[el.moneda.nombre] = {
          simbolo: el.moneda.simbolo,
          total: el.total_documento_fin,
          iso: el.moneda.iso,
        };
      } else {
        res.detalles[el.moneda.nombre].total += el.total_documento_fin;
      }
    }
    return res;
  };

  const resolverStadisticaPorCobrar = (ventas) => {
    const res = {};
    res.title = "Por Cobros";
    res.mensaje = "Ultimos 50 Documento, ";
    res.detalles = {};

    let nroCobrosPendientes = 0;

    for (let i = 0; i < ventas.length; i++) {
      const el = ventas[i];

      if (
        el.tipo_operacion_id === 1 &&
        el.forma_pago_id === 2 &&
        el.total_documento_fin - el.pago_status > 0.001
      ) {
        if (!res.detalles.hasOwnProperty(el.moneda.nombre)) {
          res.detalles[el.moneda.nombre] = {
            iso: el.moneda.iso,
            simbolo: el.moneda.simbolo,
            total: el.total_documento_fin - el.pago_status,
          };
          nroCobrosPendientes++;
        } else {
          res.detalles[el.moneda.nombre].total +=
            el.total_documento_fin - el.pago_status;
          nroCobrosPendientes++;
        }
      }
    }
    res.mensaje += nroCobrosPendientes + " Pendiente(s)";
    return res;
  };

  useEffect(() => {
    db.collection("contactos")
      .get()
      .then((contactos) => {
        setContactos(contactos.reverse());
        get(false, "");
      });
    return () => false;
  }, []);

  // useEffect(() => {
  //   console.log("those");
  //   console.log(qs);
  // }, []);

  useEffect(() => {
    if (location?.state?.reload) {
      db.collection("contactos")
        .get()
        .then((contactos) => {
          setContactos(contactos.reverse());
          get(false, "");
          setSelected({ ...location.state.reload.cliente_id });
          location.state = null;
        });

      return () => false;
    }
  }, [location?.state]);

  useEffect(() => {
    (async () => {
      const contactos = await db.collection("contactos").get();

      if (!!searchInput) {
        const result = contactos.filter((list) => {
          console.log(list);
          if (
            list.nombre.indexOf(searchInput) >= 0 ||
            String(list.numero_documento).indexOf(searchInput) >= 0 ||
            list.direccion.indexOf(searchInput) >= 0
          ) {
            return list;
          }
        });

        setContactos(result);
      } else {
        setContactos(contactos);
      }
    })();
  }, [searchInput]);

  return (
    <>
      {size.width > 700 && (
        <>
          <Grid.Column width={8}>
            <Grid.Row>
              <List relaxed="very" style={{ marginTop: "1rem" }}>
                <List.Item>
                  <List.Content floated="left">
                    <List.Content floated="right">
                      <InputSearch
                        searchInput={searchInput}
                        setSearchInput={setSearchInput}
                        placeholder="Buscar Contacto"
                      />
                    </List.Content>
                  </List.Content>
                  <List.Content floated="right">
                    {/* <Filter
                  child={<FilterCliente query={query} setQuery={setQuery} />}
                /> */}
                    <Button
                      size={"mini"}
                      color={"teal"}
                      onClick={handleNewContact}
                    >
                      Nuevo
                    </Button>
                  </List.Content>
                </List.Item>
              </List>
            </Grid.Row>

            <Grid.Row>
              <Grid.Column
                style={{
                  overflowY: "scroll",
                  height: "83vh",
                  marginTop: "1rem",
                }}
              >
                <Table celled selectable>
                  <Table.Header>
                    <Table.Row>
                      <Table.HeaderCell>Nombre</Table.HeaderCell>
                      <Table.HeaderCell>Documento</Table.HeaderCell>
                      <Table.HeaderCell>Direccion</Table.HeaderCell>
                    </Table.Row>
                  </Table.Header>
                  <Table.Body>
                    {contactos.map((v, index) => {
                      return (
                        <Table.Row
                          onClick={() => {
                            handleSetContacto(v);
                          }}
                        >
                          <Table.Cell width={3}>
                            {v.await && ""}
                            {v.nombre}
                            &nbsp;
                            {updated(v.updated_at) && (
                              <LabelUpdate index={index} />
                            )}
                          </Table.Cell>
                          <Table.Cell width={3}>
                            {v?.tipo_documento?.nombre} {v.numero_documento}
                          </Table.Cell>
                          <Table.Cell width={10}>{v?.direccion}</Table.Cell>
                        </Table.Row>
                      );
                    })}
                  </Table.Body>
                </Table>
              </Grid.Column>
            </Grid.Row>
          </Grid.Column>
          <Grid.Column width={5}>
            {selected && (
              <>
                <Card.Group
                  doubling
                  itemsPerRow={1}
                  stackable
                  style={{ marginTop: "5vh" }}
                >
                  <Card>
                    <Card.Content>
                      <>
                        <Card.Header>
                          <Image
                            srcSet={selected?.photo}
                            alt={selected.nombre_comercial}
                            as="div"
                            size="tiny"
                            circular
                          />
                          &nbsp; {selected?.nombre}{" "}
                          {updated(selected?.updated_at) && (
                            <LabelUpdate index={selected?.id} />
                          )}
                        </Card.Header>
                        <Card.Description>
                          <b>Nombre Comercial</b> : {selected?.nombre_comercial}{" "}
                        </Card.Description>
                        <Card.Description>
                          <b>Documento </b>: {selected?.tipo_documento?.nombre}{" "}
                          {selected?.numero_documento}
                        </Card.Description>
                        <Card.Description>
                          <b>Direccin </b>: {selected?.direccion}
                        </Card.Description>
                        <Card.Description>
                          <b>Email </b>: {selected?.email}
                        </Card.Description>
                        <Card.Description>
                          <b>Telfono | WhatApp</b>: {selected?.telefono}
                        </Card.Description>
                        <Card.Description>
                          <b>Telfono Segundario </b>: {selected?.celular}
                        </Card.Description>
                      </>
                    </Card.Content>

                    <Card.Content extra>
                      <Button primary onClick={handleEditContact}>
                        Editar
                      </Button>
                      <a
                        href={`https://wa.me/${selected.telefono}?text=Hola`}
                        className="button ui green"
                      >
                        Enviar Whatapp
                      </a>
                    </Card.Content>
                  </Card>
                </Card.Group>

                {!!selected && !!ventas.length && (
                  <>
                    {!!selected && (
                      <Card.Group itemsPerRow={1} stackable>
                        <Stadicticas
                          className="mt-1"
                          state={ventas}
                          resolve={resolverStadistica}
                        />

                        <Stadicticas
                          className="mt-1"
                          state={ventas}
                          resolve={resolverStadisticaPorCobrar}
                        />
                      </Card.Group>
                    )}
                    <Card.Group itemsPerRow={1} stackable>
                      <Card style={{ overflowY: "scroll", height: "25vh" }}>
                        <Card.Content>
                          <Table unstackable>
                            <Table.Body>
                              {ventas.map((v, index) => (
                                <Table.Row>
                                  <Table.Cell>
                                    <Label
                                      ribbon
                                      color={
                                        v.tipo_operacion_id === 1
                                          ? v.forma_pago_id === 1
                                            ? ""
                                            : v.pago_status <
                                              v.total_documento_fin
                                            ? "orange"
                                            : v.pago_status ===
                                              v.total_documento_fin
                                            ? ""
                                            : "blue"
                                          : ""
                                      }
                                    >
                                      {v.tipo_operacion_id === 1
                                        ? v.forma_pago_id === 1
                                          ? "Pagado"
                                          : v.pago_status <
                                            v.total_documento_fin
                                          ? "X Pago"
                                          : v.pago_status ===
                                            v.total_documento_fin
                                          ? "Pagado"
                                          : "Pagado"
                                        : "Pagado"}
                                    </Label>
                                    <Link
                                      to={`/space/ventas/${v.id}`}
                                      state={{
                                        model: v,
                                      }}
                                    >
                                      {v?.serie?.serie}-{v?.correlativo}{" "}
                                      &nbsp;&nbsp;&nbsp;
                                    </Link>
                                  </Table.Cell>
                                  <Table.Cell textAlign="right">
                                    {`${
                                      v?.moneda?.simbolo
                                    } ${v.total_documento_fin?.toFixed(2)} (${
                                      v?.moneda.iso
                                    }) `}
                                  </Table.Cell>
                                  <Table.Cell textAlign="right">
                                    {v?.moneda?.simbolo}{" "}
                                    {v.tipo_operacion_id === 1
                                      ? v.forma_pago_id === 1
                                        ? 0
                                        : v.pago_status < v.total_documento_fin
                                        ? v.total_documento_fin - v.pago_status
                                        : v.pago_status ===
                                          v.total_documento_fin
                                        ? 0
                                        : 0
                                      : 0}{" "}
                                    {v?.moneda.iso}
                                  </Table.Cell>
                                </Table.Row>
                              ))}
                            </Table.Body>
                          </Table>
                        </Card.Content>
                      </Card>
                    </Card.Group>
                  </>
                )}
              </>
            )}
          </Grid.Column>{" "}
        </>
      )}

      {size.width < 699 && (
        <>
          {!!!selected && (
            <Grid.Column width={16}>
              <Grid.Row>
                <List relaxed="very" style={{ marginTop: "1rem" }}>
                  <List.Item>
                    <List.Content floated="left">
                      <Button
                        size={"small"}
                        onClick={handleNewContact}
                        style={{ marginRight: "2px", marginLeft: "2px" }}
                      >
                        Nuevo
                      </Button>
                    </List.Content>
                    <List.Content floated="right">
                      <InputSearch
                        searchInput={searchInput}
                        setSearchInput={setSearchInput}
                        placeholder="Buscar Contacto"
                      />
                    </List.Content>
                  </List.Item>
                </List>
              </Grid.Row>

              <Grid.Row>
                <Grid.Column
                  style={{
                    overflowY: "scroll",
                    height: "83vh",
                    marginTop: "1rem",
                  }}
                >
                  <Table celled selectable>
                    <Table.Body>
                      {contactos.map((v, index) => {
                        return (
                          <Table.Row
                            onClick={() => {
                              handleSetContacto(v);
                            }}
                          >
                            <Table.Cell width={3}>
                              {v.await && ""}
                              {v.nombre}
                              &nbsp;
                              {updated(v.updated_at) && (
                                <LabelUpdate index={index} />
                              )}
                            </Table.Cell>
                            <Table.Cell width={3}>
                              {v?.tipo_documento?.nombre} {v.numero_documento}
                            </Table.Cell>
                            <Table.Cell width={10}>{v?.direccion}</Table.Cell>
                          </Table.Row>
                        );
                      })}
                    </Table.Body>
                  </Table>
                </Grid.Column>
              </Grid.Row>
            </Grid.Column>
          )}
          {!!selected && (
            <Grid.Column width={16}>
              <>
                <Grid.Row style={{ marginTop: 10, marginLeft: 12 }}>
                  <span className="float left">
                    <Button
                      size="mini"
                      icon="arrow circle left"
                      content="Regresar"
                      onClick={async () => {
                        await setSelected(undefined);
                      }}
                    />
                  </span>
                </Grid.Row>
                <Grid.Row>
                  <Card.Group
                    doubling
                    itemsPerRow={1}
                    stackable
                    style={{ marginTop: "1vh" }}
                  >
                    <Card>
                      <Card.Content>
                        <>
                          <Card.Header>
                            <Image
                              srcSet={selected?.photo}
                              alt={selected.nombre_comercial}
                              as="div"
                              size="tiny"
                              circular
                            />
                            &nbsp; {selected?.nombre}{" "}
                            {updated(selected?.updated_at) && (
                              <LabelUpdate index={selected?.id} />
                            )}
                          </Card.Header>
                          <Card.Description>
                            <b>Nombre Comercial</b> :{" "}
                            {selected?.nombre_comercial}{" "}
                          </Card.Description>
                          <Card.Description>
                            <b>Documento </b>:{" "}
                            {selected?.tipo_documento?.nombre}{" "}
                            {selected?.numero_documento}{" "}
                          </Card.Description>
                          <Card.Description>
                            <b>Direccin </b>: {selected?.direccion}
                          </Card.Description>
                          <Card.Description>
                            <b>Email </b>: {selected?.email}
                          </Card.Description>
                          <Card.Description>
                            <b>Telfono </b>: {selected?.telefono}
                          </Card.Description>
                          <Card.Description>
                            <b>Celular </b>: {selected?.celular}
                          </Card.Description>
                        </>
                      </Card.Content>

                      <Card.Content extra>
                        <Button primary onClick={handleEditContact}>
                          Editar
                        </Button>
                        <a
                          href={`https://wa.me/${selected.telefono}?text=Hola`}
                          className="button ui green"
                        >
                          Enviar Whatapp
                        </a>
                      </Card.Content>
                    </Card>
                  </Card.Group>
                </Grid.Row>

                {!!selected && !!ventas.length && (
                  <>
                    <Card.Group
                      itemsPerRow={1}
                      stackable
                      style={{ marginTop: 10 }}
                    >
                      <Stadicticas
                        className="mt-1"
                        state={ventas}
                        resolve={resolverStadistica}
                      />
                      <Stadicticas
                        className="mt-1"
                        state={ventas}
                        resolve={resolverStadisticaPorCobrar}
                      />
                    </Card.Group>
                    <Card.Group itemsPerRow={1} stackable>
                      <Card style={{ overflowY: "scroll", height: "25vh" }}>
                        <Card.Content>
                          <Table unstackable>
                            <Table.Body>
                              {ventas.map((v, index) => (
                                <Table.Row>
                                  <Table.Cell>
                                    <Label
                                      ribbon
                                      color={
                                        v.tipo_operacion_id === 1
                                          ? v.forma_pago_id === 1
                                            ? ""
                                            : v.pago_status <
                                              v.total_documento_fin
                                            ? "orange"
                                            : v.pago_status ===
                                              v.total_documento_fin
                                            ? ""
                                            : "blue"
                                          : ""
                                      }
                                    >
                                      {v.tipo_operacion_id === 1
                                        ? v.forma_pago_id === 1
                                          ? "Pagado"
                                          : v.pago_status <
                                            v.total_documento_fin
                                          ? "X Pagar"
                                          : v.pago_status ===
                                            v.total_documento_fin
                                          ? "Pagado"
                                          : "Pagado"
                                        : "Pagado"}
                                    </Label>
                                    <Link
                                      to={`/space/ventas/${v.id}`}
                                      state={{
                                        model: v,
                                      }}
                                    >
                                      {v?.serie?.serie}-{v?.correlativo}{" "}
                                      &nbsp;&nbsp;&nbsp;
                                    </Link>
                                  </Table.Cell>
                                  <Table.Cell textAlign="right">
                                    {`${
                                      v?.moneda?.simbolo
                                    } ${v.total_documento_fin?.toFixed(2)} (${
                                      v?.moneda.iso
                                    }) `}
                                  </Table.Cell>
                                  <Table.Cell textAlign="right">
                                    {v?.moneda?.simbolo}{" "}
                                    {v.tipo_operacion_id === 1
                                      ? v.forma_pago_id === 1
                                        ? (0).toFixed(2)
                                        : v.pago_status < v.total_documento_fin
                                        ? (
                                            v.total_documento_fin -
                                            v.pago_status
                                          ).toFixed(2)
                                        : v.pago_status ===
                                          v.total_documento_fin
                                        ? (0).toFixed(2)
                                        : (0).toFixed(2)
                                      : (0).toFixed(2)}
                                    {" ("}
                                    {v?.moneda.iso}
                                    {") "}
                                  </Table.Cell>
                                </Table.Row>
                              ))}
                            </Table.Body>
                          </Table>
                        </Card.Content>
                      </Card>
                    </Card.Group>
                  </>
                )}
              </>
            </Grid.Column>
          )}
        </>
      )}
    </>
  );
};

export default Contactos;
