import React, { useContext, useState } from "react";

import { Form, TextArea, Button, Table, Icon } from "semantic-ui-react";
import { yupResolver } from "@hookform/resolvers/yup";
import { useForm, Controller } from "react-hook-form";
import schema from "./schema";
import FormFielDropdownSarch from "../DropdownSearch2";
import settingDropdowns from "../../libs/settingDropdowns";
import EmpresaContext from "../../context/EmpresaContext";
import MailService from "../../services/email";
import { useNavigate, useLocation } from "react-router-dom";
import { toast } from "react-toastify";
const FormularioEmail = ({ state, handleChangeVenta }) => {
  let navigate = useNavigate();
  let location = useLocation();
  const { control, handleSubmit, setValue, watch } = useForm({
    resolver: yupResolver(schema),
    defaultValues: state,
  });

  const watcher = watch();
  const close = () => {
    navigate(-1);
  };

  const onSubmit = async (data) => {
    return data;
  };

  const handleSendMail = async (e) => {
    e.preventDefault();
    close();

    toast.promise(MailService.send({ ...watcher, ...{ files } }), {
      pending: "Enviando Correo",
      success: "Correo Enviado ",
      error: "Correo no enviado ",
    });
  };

  const { empresa } = useContext(EmpresaContext);
  const [files, setFiles] = useState([]);

  useState(() => {
    if (!!state?.sunat_documento) {
      let f = [
        {
          icon: "file code outline",
          name: `${state.sunat_documento}.xml`,
          path: `${empresa.empresa.id}/xml/R-${state.sunat_documento}.xml`,
          enviable: true,
        },
        {
          icon: "file archive outline",
          name: `${state.sunat_documento}.zip`,
          path: `${empresa.empresa.id}/resp/R-${state.sunat_documento}.zip`,
          enviable: true,
        },
      ];

      if (!f.length) {
        f = [];
      }

      const fileName = `${empresa.empresa.ruc}-${state.serie.sunat_codigo}-${state.serie.serie}-${state.correlativo}.pdf`;

      if (state.file) {
        f.push({
          icon: "file pdf outline",
          name: fileName,
          enviable: false,
        });
      }

      setFiles(f);
    } else {
      let f = [];

      const fileName = `${empresa.empresa.ruc}-${state.serie.sunat_codigo}-${state.serie.serie}-${state.correlativo}.pdf`;

      if (state.file) {
        f.push({
          icon: "file pdf outline",
          name: fileName,
          enviable: false,
        });
      }

      setFiles(f);
    }
    return () => {
      console.log("unmounted send mailer");
    };
  }, state);

  return (
    <>
      <Form onSubmit={handleSubmit(onSubmit)} noValidate>
        <Form.Group widths={16}>
          <FormFielDropdownSarch
            model={settingDropdowns.clientes}
            empresaContexto={empresa}
            control={control}
            venta={state}
            handleChangeVenta={handleChangeVenta}
            setValue={setValue}
            width={16}
          />
          {/* <FormFielController
            name={"to"}
            title={"Para"}
            control={control}
            errors={errors}
            width={16}
          /> */}
          {/* <FormFielController
            name={"cuenta_detraccion"}
            title={"Cuenta Detraccin"}
            control={control}
            errors={errors}
            width={5}
          /> */}
        </Form.Group>
        <Form.Group widths={16}>
          <Form.Field width={16}>
            <Controller
              name={"message"}
              control={control}
              render={({ field: { onChange, onBlur, value } }) => (
                <>
                  <label>Mensaje</label>
                  <TextArea
                    rows={12}
                    value={value}
                    onBlur={onBlur}
                    onChange={onChange}
                    placeholder="Escribe tu mensaje"
                  />
                </>
              )}
            />
          </Form.Field>
        </Form.Group>

        <Table unstackable>
          <Table.Header>
            <Table.Row>
              <Table.HeaderCell>Archivo</Table.HeaderCell>
              <Table.HeaderCell>Nombre</Table.HeaderCell>
            </Table.Row>
          </Table.Header>

          <Table.Body>
            {files.map((v, index) => (
              <Table.Row>
                <Table.Cell>
                  <Icon name={v.icon} />
                </Table.Cell>
                <Table.Cell>{v.name}</Table.Cell>
              </Table.Row>
            ))}
          </Table.Body>
        </Table>
        <Button
          content="Enviar Correo"
          color="primary"
          fluid
          onClick={handleSendMail}
        />
      </Form>
      {/* <pre>{JSON.stringify(watcher, null, 2)}</pre> */}
    </>
  );
};
export default FormularioEmail;
