import React, { useEffect, useState, useContext } from "react";
import { useNavigate, useLocation } from "react-router-dom";
import { useForm } from "react-hook-form";
import { Form, Button, Message, Segment ,Table ,Checkbox ,Icon} from "semantic-ui-react";
import { yupResolver } from "@hookform/resolvers/yup";
import monedaService from "../../services/moneda";
import FormFielController from "../FormFieldController";
import schema from "./schema";
import handleBackendErrors from "../../libs/handleBackendErrors";
import EmpresaContext from "../../context/EmpresaContext";

const FormularioMoneda = ({ state }) => {
  let navigate = useNavigate();
  let location = useLocation();
  const { empresa, setEmpresa } = useContext(EmpresaContext);
  const [isLoadding, setIsloadding] = useState(false);
  const [isBackendError, setIsBackendError] = useState(false);
  const {
    control,
    handleSubmit,
    formState: { errors },
    reset,
  } = useForm({ resolver: yupResolver(schema), defaultValues: state });

  const [data, setData] = useState();
  useEffect(
    async () => {
      if (!!state && state.id) {
        let data = await monedaService.find(state.id);
        return setData(data);
      } else {
        return false;
      }
    },
    { data }
  );

  const close = (moneda) => {
    navigate(location.state.backgroundLocation.pathname, {
      state: { reload: { moneda_id: moneda } },
    });
  };

  const onSubmit = async (data) => {
    console.log(data);
    setIsloadding(true);
    if (!!data && data.id) {
      // update
      const update = await monedaService.update(data, data.id);
      close(update);
      // verificar si es la empresa actual en el contexto del la instancia de local/empresa
    } else {
      //create
      const create = await monedaService.create(data);
      setIsloadding(false);
      if (create?.errors) {
        setIsBackendError(handleBackendErrors(create));
      } else {
        setIsBackendError(null);
        close(create);
      }
      return create;
    }
  };
  return (
    <Form onSubmit={handleSubmit(onSubmit)} noValidate>
      <Form.Group>
        <FormFielController
          name={"nombre"}
          title={"Nombre"}
          control={control}
          errors={errors}
          width={4}
        />
        <FormFielController
          name={"iso"}
          title={"ISO"}
          control={control}
          errors={errors}
          width={3}
        />
        <FormFielController
          name={"simbolo"}
          title={"Simbolo"}
          control={control}
          errors={errors}
          width={3}
        />
        <FormFielController
          name={"sunat_codigo"}
          title={"Codigo Sunat"}
          control={control}
          errors={errors}
          width={3}
        />
        <FormFielController
          name={"dolar"}
          title={"Dolar"}
          control={control}
          errors={errors}
          width={3}
        />
      </Form.Group>




      {Object.keys(errors)?.length ? (
          <>
        <Message
          color="orange"
          header="Advertencia"
          list={["Verifique que todos los campos requeridos esten completos."]}
        />
        <Segment>
            {JSON.stringify(errors,null,2)}
        </Segment>
        </>
      ) : (
        <></>
      )}

      {isBackendError?.length && (
        <Message
          color="red"
          header="Verifique los siguientes campos"
          list={isBackendError}
        />
      )}

      <Button
        loading={isLoadding}
        disabled={isLoadding}
        content="Guardar"
        primary
      />
      <Button
        as="a"
        content="Deshacer Cambios"
        secondary
        onClick={() => {
          reset(state);
        }}
      />
    </Form>
  );
};

export default FormularioMoneda;
