
import * as yup from "yup";

const schema = yup
.object({
  ruc: yup.string().required("Complete"),
  nombre: yup.string().required("Complete"),
  nombre_comercial: yup
    .string()
    .required("Complete")
    .min(2, "Minimo 2 letras")
    .max(50, "Max 50 Letras"),
  direccion: yup.string().required("Complete"),
  ubigeo_id: yup.string().required("Complete"),
  telefono: yup.string().required("Complete"),
  celular: yup.string().required("Complete"),
  email: yup.string().email().required("Complete"),
  decimales: yup.string().required("Complete"),
  // certificado: yup.string().required("Complete"),
  // certificado_clave: yup.string().required("Complete"),
  // usuario_sunat: yup.string().required("Complete"),
  // usuario_sunat_clave: yup.string().required("Complete"),
  por_detraccion: yup.string().required("Complete"),
  por_retencion: yup.string().required("Complete"),
  igv: yup.string().required("Complete"),
  agente: yup.string().required("Complete"),
  por_percepcion: yup.string().required("Complete"),
  limite_detraccion: yup.string().required("Complete"),
  country_id: yup.string().required("Complete"),
})
.required();

export default schema