import React, {
  useState,
  useRef,
  useImperativeHandle,
  forwardRef,
} from "react";
import { useEffect } from "react";
import { Link, useLocation } from "react-router-dom";
import { Dropdown, Icon, Header, Button } from "semantic-ui-react";
import productoService from "../services/productos";

import _ from "lodash";

const parseData = (data, key) => {
  const res = data.map((e, index) => {
    if (key.concat) {
      const arr = key.concat.map((i) => {
        return e[i];
      });

      return {
        text: arr.join("-"),
        value: e[key.id],
        key: index,
        model: e,
      };
    } else {
      return { text: e[key.text], value: e[key.id], key: index, model: e };
    }
  });

  return res;
};
/**
 *
 * @param {object} empresa
 * @param {*} key
 * @param {*} endPoint
 * @param {*} params
 * @param {*} searchQuery
 * @param {*} moreRouteParams
 * @param {*} venta
 * @returns
 */
const getOptions = async (
  empresa,
  key,
  endPoint,
  params,
  searchQuery = null,
  moreRouteParams,
  venta
) => {
  let query;

  console.log({ searchQuery });

  if (params?.empresa_id) {
    endPoint = endPoint + "/" + empresa.empresa.id;
  }
  if (params?.local_id) {
    endPoint = endPoint + "/" + empresa.id;
  }

  if (moreRouteParams) {
    const values = Object.keys(moreRouteParams).map((v) => {
      return venta[v];
    });
    for (let i = 0; i < values; i++) {
      endPoint = endPoint + "/" + values[i];
    }
  }

  if (typeof searchQuery === "string" && searchQuery.length < 3) {
    return false;
  }

  if (typeof searchQuery === "string" && searchQuery.length > 3) {
    query = { filter: [[key.text, "like", "%" + searchQuery + "%"]] };
  }

  if (typeof searchQuery === "object") {
    query = searchQuery;
  }

  query = searchQuery ? query : null;

  console.log(query, searchQuery);

  const response = await productoService.show(endPoint, query);
  let data = await parseData(response, key);
  return data;
};

const SearchProductos = (
  { model, moreRouteParams, empresaContexto, venta, setItemSelected, width },
  ref
) => {
  const location = useLocation();

  const [state, setState] = useState({
    isFetching: false,
    multiple: false,
    searchQuery: null,
    value: "",
    options: [],
  });

  const handleSearchChange = async (e, element) => {
    console.log({ e }, { element });

    setState({ isFetching: true });
    const options = await getOptions(
      empresaContexto,
      model.keys,
      model.endPoint,
      model.params,
      element.searchQuery,
      moreRouteParams,
      venta
    );
    setState({ ...state, ...{ options, isFetching: false } });
  };

  const handleChange = (e, element) => {
    const selected = element.options.find((e) => e.value === element.value);
    setItemSelected(selected);
  };

  const inputRef = useRef(null);

  const open = () => {
    console.log(inputRef);
    // debugger;
    inputRef.current.open();
  };

  useImperativeHandle(ref, () => ({
    // methods connected to `ref`
    open: () => {
      setTimeout(() => {
        open();
      }, 200);
    },
  }));

  return (
    <Dropdown
      id={"search_producto"}
      ref={inputRef}
      fluid
      selection
      clearable
      multiple={false}
      search
      options={state.options}
      placeholder=" Buscar Producto  Servicio"
      onChange={handleChange}
      onSearchChange={_.debounce(handleSearchChange, 1000)}
      loading={state.isFetching}
      noResultsMessage={
        <Link
          to={"/welcome/productos/create"}
          state={{
            backgroundLocation: location,
            model: {
              local_id: empresaContexto.id,
              empresa_id: empresaContexto.empresa.id,
            },
          }}
        >
          Agregar Nuevo Producto  Servicio
        </Link>
      }
    />
  );
};

export default forwardRef(SearchProductos);
