import { useEffect, useContext, useState } from "react";
import {
  Label,
  Button,
  Grid,
  Header,
  Segment,
  Icon,
  List,
  Popup,
  Dropdown,
  Pagination,
} from "semantic-ui-react";
import { Link, useLocation, useNavigate } from "react-router-dom";
import Moment from "react-moment";
import CreateButton from "../../components/CreateButton";
import ConverterButton from "../../components/ConverterButton";
import Filter from "../../components/Filter";
import ventaService from "../../services/ventas";
import FormularioCompra from "../../components/compras/formulario";
import EmpresaContext from "../../context/EmpresaContext";
import { PDFDownloadLink, pdf } from "@react-pdf/renderer";
import { updated } from "../../libs/updated";
import { LabelUpdate } from "../../components/LabelUpdate";
import ViewFormatA4 from "../../components/ViewFormatA4";

const Compras = () => {
  const location = useLocation();
  const { empresa, impresion } = useContext(EmpresaContext);
  const [actualData, setActualData] = useState({ current_page: 1 });
  const [ventas, setVentas] = useState([]);
  const [selected, setSelected] = useState(null);
  const [editionMode, setEditionMode] = useState(false);
  const [qrCodeBase64, setQrCodeBase64] = useState(false);

  const [pdfBlob, setPdfBlob] = useState(null);

  const [formatPrint, setFormatPrint] = useState({
    status: false,
    format: "A4",
  });

  const getBlob = async () => {
    const blob = pdf(
      <ViewFormatA4
        empresa={empresa}
        status={selected}
        qrCodeBase64={qrCodeBase64}
        impresion={impresion}
      />
    ).toBlob();

    let bl = await blob;

    const file = new File(
      [bl],
      `${empresa.empresa.ruc}-${selected?.serie?.sunat_codigo}-${selected?.serie?.serie}-${selected?.correlativo}.pdf`,
      {
        type: "application/pdf",
        lastModified: Date.now(),
      }
    );
    setPdfBlob(file);
  };

  const viewPrintFormat = (status, type) => {
    setFormatPrint({ status, format: type });
  };

  const get = async (page, reload = false) => {
    const empresa_id = empresa.empresa.id;
    const local_id = empresa.id;
    const fetch = await ventaService.show(empresa_id, local_id, page);
    setActualData(fetch);
    setVentas(fetch.data);
  };

  const handlePaginationChange = (e, element) => {
    get(element.activePage);
  };

  // const reload = async () => {
  //   await setActualData({ current_page: 0 });
  //   await setVentas([]);
  //   await get();
  //   console.log("reload from ventas");
  // };

  const handleChangeVenta = (ventaModificada) => {
    console.log(ventaModificada);
    const change = ventas.map((venta) => {
      if (parseInt(venta.id) === parseInt(ventaModificada.id)) {
        return ventaModificada;
      } else {
        return venta;
      }
    });
    setVentas(change);
  };

  const handleSelectDocument = async (venta) => {
    await setSelected(null);
    await setSelected(venta);

    console.log(venta);
  };

  const download = (type) => {
    ventaService.download(selected, type);
  };

  const sendToSunat = async (id, index) => {
    const ventaChange = ventas.find((v, k) => v.id === id);
    ventaChange.send_status = true;

    const newVentas = ventas.map((venta, index) => {
      if (venta.id === ventaChange.id) {
        return ventaChange;
      } else {
        return venta;
      }
    });

    setVentas(newVentas);

    /**PROMESA */
    const response = await ventaService.sendSunat(ventaChange);

    const ventaWithResponse = { ...ventaChange, ...response };

    /**END PROMESA */
    ventaWithResponse.send_status = false;
    const HandleVentas = ventas.map((venta, index) => {
      if (venta.id === ventaWithResponse.id) {
        return ventaWithResponse;
      } else {
        return venta;
      }
    });
    setVentas(HandleVentas);
  };

  useEffect(() => {
    get(1);
    return () => {
      setVentas([]);
    };
  }, []);

  useEffect(() => {
    setFormatPrint({
      status: false,
      format: "A4",
    });

    location.state = selected;

    if (!selected) {
      setEditionMode(false);
    }

    setTimeout(() => {
      const qrCodeCanvas = document?.querySelector("canvas");
      setQrCodeBase64(qrCodeCanvas?.toDataURL("image/jpg", 0.3));
      getBlob();
    }, 500);
    return () => {};
  }, [selected]);

  // if (!!location?.state && location?.state?.reload) {
  //   reload();
  //   location.state = null;
  // }

  return (
    <>
      <Grid.Column width={5}>
        <List relaxed="very" style={{ marginTop: "1rem" }}>
          <List.Item>
            <List.Content floated="right">
              <Filter />
              <CreateButton empresaContext={empresa} />
            </List.Content>
            <List.Content>
              <List.Header as="a">
                <Link
                  to={`/welcome/empresas/${empresa.empresa.id}`}
                  state={{
                    backgroundLocation: location,
                    model: empresa.empresa,
                  }}
                >
                  {empresa.empresa.nombre}
                </Link>
              </List.Header>
              {empresa.nombre}
            </List.Content>
          </List.Item>
        </List>
        <List celled relaxed="very">
          {ventas?.map((venta, index) => (
            <List.Item
              key={index}
              onClick={() => {
                handleSelectDocument(venta);
              }}
              style={{
                backgroundColor:
                  venta?.id === selected?.id ? "rgb(0 181 173 / 12%)" : "#fff",
              }}
            >
              <List.Content style={{ width: "135px" }} floated="right">
                <Label.Group tag>
                  <Label as="a">
                    {venta?.moneda?.simbolo}{" "}
                    {venta.total_documento_fin?.toFixed(3)}
                  </Label>
                </Label.Group>
                {/* <Button
                  size="tiny"
                    onClick={(e) => {
                      e.stopPropagation();
                      sendToSunat(venta.id);
                    }}
                  >
                    <img
                      class="ui avatar image"
                      src={SUNAT}
                      alt=""
                      style={{
                        height: "25px",
                        width: "25px",
                        backgroundColor: "#e8e8e8",
                        padding: "3px",
                      }}
                    />
                  </Button> */}

                <Popup
                  content={"Click para enviar comprobante a Sunat"}
                  key={index}
                  header={"Pendiente por enviar"}
                  trigger={
                    venta.tipo_operacion_id !== 2 &&
                    venta.serie.abbr !== "CT" &&
                    venta.serie.abbr !== "NV" ? (
                      <Button
                        icon={
                          venta.sunat_code === "0"
                            ? "check"
                            : venta.sunat_code >= "2000"
                            ? "times"
                            : "cloud upload"
                        }
                        size="mini"
                        onClick={(e) => {
                          e.stopPropagation();
                          sendToSunat(venta.id, index);
                        }}
                        color={
                          venta.sunat_code === "0"
                            ? "green"
                            : venta.sunat_code >= "2000"
                            ? "red"
                            : "primary"
                        }
                        loading={venta.send_status}
                        disabled={venta.send_status || venta.sunat_code === "0"}
                      />
                    ) : (
                      false
                    )
                  }
                />
                {venta?.is_nulled ? (
                  <Label as="span" content="Anulado" color="red" />
                ) : (
                  <> &nbsp;</>
                )}
              </List.Content>

              {/* <List.Content floated="right">
                  <Button circular as="span" size="mini" icon="settings" />
                  <Button circular as="span" size="mini" icon="settings" />
                  <Button circular as="span" size="mini" icon="money" />
                </List.Content> */}

              <List.Content>
                <List.Header as="a">
                  {venta?.serie?.abbr} {venta?.serie?.serie}{" "}
                  {venta?.correlativo}{" "}
                  {updated(venta.updated_at) && <LabelUpdate index={index} />}
                </List.Header>
                <List.Item>
                  <strong>{venta?.cliente?.nombre}</strong>
                </List.Item>
                <List.Item>
                  Fecha Emision:{" "}
                  <Moment format="DD/MM/YYYY">{venta.fecha_emision}</Moment>
                </List.Item>
              </List.Content>
              {/* <List.Content>
                  {JSON.stringify(venta)}
                </List.Content> */}
            </List.Item>
          ))}
        </List>
        {!!ventas.length && (
          <Pagination
            onPageChange={handlePaginationChange}
            defaultActivePage={actualData.current_page}
            pointing
            secondary
            totalPages={actualData.last_page}
          />
        )}
      </Grid.Column>
      <Grid.Column width={10}>
        {!selected ? (
          <Segment placeholder style={{ marginTop: "6vh" }}>
            <Header icon>
              <Icon name="file outline" />
              No tienes un documento seleccionado
            </Header>
          </Segment>
        ) : (
          <>
            <List relaxed="very" style={{ marginTop: "1rem" }}>
              <List.Item>
                <List.Content floated="right">
                  <Button
                    size="mini"
                    content="Imprimir"
                    icon="print"
                    labelPosition="left"
                    style={{ marginRight: "2px", marginLeft: "2px" }}
                    onClick={() => {
                      viewPrintFormat(!formatPrint.status, "A4");
                    }}
                    active={formatPrint.status}
                  />

                  {!!!selected.is_nulled || !!!selected ? (
                    <>
                      <Button
                        size="mini"
                        content="Editar"
                        icon="pencil"
                        labelPosition="left"
                        style={{ marginRight: "2px", marginLeft: "2px" }}
                        active={editionMode}
                        onClick={() => {
                          setEditionMode(!editionMode);
                        }}
                      />
                    </>
                  ) : (
                    <></>
                  )}
                </List.Content>
              </List.Item>
            </List>
            <Button
              circular
              size="mini"
              icon
              onClick={() => {
                setSelected(null);
              }}
            >
              <Icon name="times" />
            </Button>
            <Link
              to={`/space/compras/1`}
              state={{
                model: selected,
              }}
            >
              <Button circular size="mini" icon>
                <Icon name="expand" />
              </Button>
            </Link>
            {selected.is_nulled ? (
              <Label as="span" content="Anulado" icon="ban" color="red" />
            ) : (
              <></>
            )}

            <FormularioCompra
              selected={selected}
              handleChangeVenta={handleChangeVenta}
              editionMode={editionMode}
              formatPrint={formatPrint}
              setFormatPrint={setFormatPrint}
            />
          </>
        )}
      </Grid.Column>
    </>
  );
};

export default Compras;
