import React, { useEffect, useState, useContext } from "react";
import { useNavigate, useLocation } from "react-router-dom";
import { useForm } from "react-hook-form";
import { Form, Button, Message, Segment } from "semantic-ui-react";
import { yupResolver } from "@hookform/resolvers/yup";
import precioService from "../../services/precio";
import FormFielController from "../FormFieldController";
import schema from "./schema";
import handleBackendErrors from "../../libs/handleBackendErrors";
import EmpresaContext from "../../context/EmpresaContext";
import FormFielDropdownSarch from "../DropdownSearch2";
import settingDropdowns from "../../libs/settingDropdowns";
import Precio from "../../libs/calcularListaPrecio";

const FormularioPrecio = ({ state }) => {
  let navigate = useNavigate();
  let location = useLocation();
  const { empresa, setEmpresa } = useContext(EmpresaContext);
  const [isLoadding, setIsloadding] = useState(false);
  const [isBackendError, setIsBackendError] = useState(false);
  const {
    control,
    handleSubmit,
    setValue,
    watch,
    formState: { errors },
  } = useForm({ resolver: yupResolver(schema), defaultValues: state });
  // const watcher = watch();
  const watcher = watch();
  const precio = new Precio();
  React.useEffect(() => {
    const subscription = watch((value, { name, type }) => {
      let item = [];

      if (name === "precio_venta" && type === "change") {
        item = precio.precioVenta(value);
      }

      if (name === "costo_venta" && type === "change") {
        item = precio.costoVenta(value);
      }

      const itemKeys = Object.keys(item);
      for (let i = 0; i < itemKeys.length; i++) {
        const value = itemKeys[i];
        setValue(value, item[value]);
      }
    });
    return () => subscription.unsubscribe();
  }, [watch]);

  const [data, setData] = useState();
  useEffect(
    () => {
      (async function () {
        if (!!state && state.id) {
          let data = await precioService.find(state.id);
          return setData(data);
        } else {
          return false;
        }
      })();
    },
    { data }
  );

  const close = (precio,push) => {
    navigate(location.state.backgroundLocation.pathname, {
      state: { reload: { precio_id: precio, push} },
    });
  };

  const onSubmit = async (data) => {
    setIsloadding(true);
    if (!!data && data.id) {
      // update
      const update = await precioService.update(
        data,
        empresa.empresa.id,
        data.id
      );
      close(update,false);
    } else {
      //create
      const create = await precioService.create(data);
      setIsloadding(false);
      if (create?.errors) {
        setIsBackendError(handleBackendErrors(create));
      } else {
        setIsBackendError(null);
        close(create,true);
      }
      return create;
    }
  };
  return (
    <Form onSubmit={handleSubmit(onSubmit)} noValidate>
      <Form.Group>
        <FormFielDropdownSarch
          model={settingDropdowns.presentacion}
          empresaContexto={empresa}
          control={control}
          venta={state}
          setValue={setValue}
          width={6}
        />
        <FormFielDropdownSarch
          model={settingDropdowns.moneda}
          empresaContexto={empresa}
          control={control}
          venta={state}
          setValue={setValue}
          width={3}
        />
      </Form.Group>
      <Form.Group>
        <FormFielController
          name={"costo_compra"}
          title={"Costo de Compra"}
          control={control}
          errors={errors}
          width={3}
          readOnly={true}
        />
        <FormFielController
          name={"igv"}
          title={"% de IGV"}
          control={control}
          errors={errors}
          width={3}
          readOnly={true}
        />
        <FormFielController
          name={"por_utilidad"}
          title={"% de Utilidad"}
          type={"number"}
          control={control}
          errors={errors}
          width={3}
          readOnly={true}
        />
        <FormFielController
          name={"utilidad"}
          title={
            !!watcher?.moneda?.simbolo
              ? "Utilidad " + watcher?.moneda?.simbolo
              : "Utilidad"
          }
          control={control}
          errors={errors}
          width={3}
          readOnly={true}
          type={"number"}
        />
        <FormFielController
          name={"costo_venta"}
          title={"Costo de Venta"}
          control={control}
          errors={errors}
          width={3}
          type={"number"}
        />
        <FormFielController
          name={"precio_venta"}
          title={"Precio Venta"}
          control={control}
          errors={errors}
          width={3}
          type={"number"}
        />
      </Form.Group>

      {Object.keys(errors)?.length ? (
        <Message
          color="orange"
          header="Advertencia"
          list={["Verifique que todos los campos requeridos esten completos."]}
        />
      ) : (
        <></>
      )}
      {isBackendError?.length && (
        <Message
          color="red"
          header="Verifique los siguientes campos"
          list={isBackendError}
        />
      )}

      <Button loading={isLoadding} secondary>
        Guardar
      </Button>
    </Form>
  );
};
export default FormularioPrecio;
