import React, { useEffect, useState } from "react";
import { Link, Navigate, useParams, useLocation } from "react-router-dom";
import Header from "../partials/Header";
import { Message } from "semantic-ui-react";
import CryptoJS from "crypto-js";
import ventas from "../services/ventas";
import Rating from "../assets/rating.png";
import { PDFDownloadLink } from "@react-pdf/renderer";
import ViewFormatA4 from "../components/ViewFormatA4";
function useQuery() {
  const { search } = useLocation();
  return React.useMemo(() => new URLSearchParams(search), [search]);
}

function Comprobante() {
  const [state, setState] = useState(false);
  const [search, setSearch] = useState(false);
  const [venta, setVenta] = useState(false);
  const query = useQuery();
  const token = query.get("tk");

  const [form, setForm] = useState({
    ruc: "",
    documento: "",
    serie: "",
    correlativo: "",
    monto: "",
  });

  const handleForm = (e) => {
    const input = {};
    input[e.target.name] = e.target.value;
    setForm({ ...form, ...input });
  };

  const reset = () => {
    setSearch(true);
    setVenta(false);
    setState(false);
    setForm({
      ruc: "",
      documento: "",
      serie: "",
      correlativo: "",
      monto: "",
    });
  };

  const searcher = async () => {
    const res = await ventas.consultarSearch(form);
    if (!!res.id) {
      console.log(res);
      setSearch(false);
      setVenta(res);
      setState(true);
    }
  };
  const download = (selected, type) => {
    ventas.downloadExternal(selected, type);
  };

  useState(() => {
    if (!!token) {
      setSearch(false);
      try {
        const tk = token.replaceAll(" ", "+");
        const bytes = CryptoJS.AES.decrypt(
          tk,
          "S2bBho4gEUOok7o3IFiUu4BMcGhz5wR2Eu35YTks"
        );
        const desifrado = JSON.parse(bytes.toString(CryptoJS.enc.Utf8));

        (async () => {
          try {
            const res = await ventas.consultar(desifrado);
            setState(true);
            setVenta(res);
          } catch (error) {
            setState(false);
          }
        })();
      } catch (error) {
        setState(false);
        setSearch(false);
      }
    } else {
      setSearch(true);
    }
  }, []);

  return (
    <div className="flex flex-col min-h-screen overflow-hidden">
      {/*  Site header */}
      <Header />
      {/*  Page content */}
      <main className="flex-grow">
        <section className="bg-gradient-to-b from-gray-100 to-white">
          <div className="max-w-6xl mx-auto px-4 sm:px-6">
            <div className="pt-32 pb-12 md:pt-40 md:pb-20">

            <div className="max-w-3xl mx-auto text-center pb-12 md:pb-20">
                <h1 className="h1">
                  Consulta de comprobante
                </h1>
              </div>
              {/* Form */}

              {search ? (
                <SearchFormComprobante
                  handleForm={handleForm}
                  model={form}
                  searcher={searcher}
                />
              ) : state ? (
                <div className="max-w-sm mx-auto">
                  <div className="flex flex-wrap -mx-3 ">
                    <div className="w-full px-3">
                      {/* 1st item */}
                      <div className="relative flex flex-col items-center p-6 bg-white rounded shadow-xl">
                        <h4 className="text-xl font-bold leading-snug tracking-tight mb-1">
                          Comprobante Vlido 
                        </h4>
                        <pre>
                          Documento: {venta?.serie?.descripcion}{" "}
                          {venta?.serie?.serie} {venta?.correlativo}
                        </pre>
                        <pre>Cliente: {venta?.cliente?.nombre_comercial}</pre>
                        <pre>
                          Monto Total: {venta?.moneda?.simbolo}{" "}
                          {venta?.total_documento?.toFixed(2)} {"("}
                          {venta?.moneda?.iso}
                          {")"}
                        </pre>

                        <pre>Observacin:</pre>
                        {/* <span className="text-gray-600" dangerouslySetInnerHTML={{__html: venta?.detalle.replaceAll('<br/>', '\n')}} ></span> */}
                        <pre>{venta?.detalle}</pre>
                      </div>
                    </div>
                  </div>
                  {venta.sunat_status === "ACEPTADA" && (
                    <>
                      <div className="flex flex-wrap -mx-3 mt-6">
                        <div className="w-full px-3">
                          <button
                            onClick={() => {
                              download(venta, "xml");
                            }}
                            className="btn text-white bg-blue-600 hover:bg-blue-700 w-full"
                          >
                            Descargar XML
                          </button>
                        </div>
                      </div>

                      <div className="flex flex-wrap -mx-3 mt-6">
                        <div className="w-full px-3">
                          <button
                            onClick={() => {
                              download(venta, "cdn");
                            }}
                            className="btn text-white bg-blue-600 hover:bg-blue-700 w-full"
                          >
                            Descargar CDN
                          </button>
                        </div>
                      </div>
                    </>
                  )}

                  <div className="flex flex-wrap -mx-3 mt-6">
                    <div className="w-full px-3">
                      <button
                        onClick={reset}
                        className="btn text-white bg-green-600 hover:bg-green-700 w-full"
                      >
                        Buscar otro Documento
                      </button>
                    </div>
                  </div>
                </div>
              ) : (
                <div className="max-w-sm mx-auto">
                  <div className="flex flex-wrap -mx-3 ">
                    <div className="w-full px-3">
                      {/* 1st item */}
                      <div className="relative flex flex-col items-center p-6 bg-white rounded shadow-xl">
                        <h4 className="text-xl font-bold leading-snug tracking-tight mb-1">
                          Comprobante Error 
                        </h4>
                        <pre>Documento no encontrado</pre>
                      </div>
                    </div>
                  </div>
                  <div className="flex flex-wrap -mx-3 mt-6">
                    <div className="w-full px-3">
                      <button
                        onClick={reset}
                        className="btn text-white bg-green-600 hover:bg-green-700 w-full"
                      >
                        Buscar Otro Documento
                      </button>
                    </div>
                  </div>
                </div>
              )}
            </div>
          </div>
        </section>
      </main>
    </div>
  );
}

const SearchFormComprobante = ({ model, handleForm, searcher }) => {
  return (
    <div className="max-w-sm mx-auto">
      <div className="flex flex-wrap -mx-3 mb-4">
        <div className="w-full px-3">
          <label
            className="block text-gray-800 text-sm font-medium mb-1"
            htmlFor="name"
          >
            Nro Documento Empresa Emisora
            <span className="text-red-600">*</span>
          </label>
          <input
            id="ruc"
            name="ruc"
            type="text"
            className="form-input w-full text-gray-800"
            placeholder="RUC/RIF/RUN Empresa emisora"
            required
            onChange={handleForm}
            value={model?.name}
          />
        </div>
        <div className="w-full px-3">
          <label
            className="block text-gray-800 text-sm font-medium mb-1"
            htmlFor="name"
          >
            Tipo de Documento <span className="text-red-600">*</span>
          </label>
          <input
            id="documento"
            name="documento"
            type="text"
            className="form-input w-full text-gray-800"
            placeholder="FA/BO/CT/GR de tu documento a Consultar"
            required
            onChange={handleForm}
            value={model?.name}
          />
        </div>
        <div className="w-full px-3">
          <label
            className="block text-gray-800 text-sm font-medium mb-1"
            htmlFor="name"
          >
            Serie<span className="text-red-600">*</span>
          </label>
          <input
            id="serie"
            name="serie"
            type="text"
            className="form-input w-full text-gray-800"
            placeholder="B001/F001/CT01/GR01 de tu documento a Consultar"
            required
            onChange={handleForm}
            value={model?.name}
          />
        </div>
        <div className="w-full px-3">
          <label
            className="block text-gray-800 text-sm font-medium mb-1"
            htmlFor="name"
          >
            Correlativo<span className="text-red-600">*</span>
          </label>
          <input
            id="correlativo"
            name="correlativo"
            type="text"
            className="form-input w-full text-gray-800"
            placeholder="Ingresa el correlativo"
            required
            onChange={handleForm}
            value={model?.name}
          />
        </div>
        <div className="w-full px-3">
          <label
            className="block text-gray-800 text-sm font-medium mb-1"
            htmlFor="name"
          >
            Monto Total<span className="text-red-600">*</span>
          </label>
          <input
            id="monto"
            name="monto"
            type="text"
            className="form-input w-full text-gray-800"
            placeholder="Ingresa el Monto Total"
            required
            onChange={handleForm}
            value={model?.name}
          />
        </div>
      </div>
      <div className="flex flex-wrap -mx-3 mt-6">
        <div className="w-full px-3">
          <button
            onClick={() => searcher(model)}
            className="btn text-white bg-blue-600 hover:bg-blue-700 w-full"
          >
            Buscar Comprobante
          </button>
        </div>
      </div>
    </div>
  );
};

export default Comprobante;
