import React, { useState, useEffect } from "react";
import { toast } from "react-toastify";
const Context = React.createContext({});

export function VentaContextProvider({ children }) {
  const [documento, setDocumento] = useState({});
  const [ventaDefaultContent, setVentaDefaultContent] = useState(
    JSON.parse(localStorage.getItem("venta_default_content"))
  );

  // useEffect(() => {
  //   console.log({ documento });
  //   return () => {
  //     console.log("desmontado");
  //   };
  // }, [documento]);

  const handleDefaultVenta = () => {
    const data = {};
    data.cliente = documento.cliente;
    data.cliente_id = documento.cliente_id;
    data.almacen = documento.almacen;
    data.almacen_id = documento.almacen_id;
    data.forma_pago = documento.forma_pago;
    data.forma_pago_id = documento.forma_pago_id;
    data.moneda = documento.moneda;
    data.moneda_id = documento.moneda_id;
    localStorage.setItem("venta_default_content", JSON.stringify(data));
    setVentaDefaultContent(data);
    console.log(data);
    toast.success("Ajustes Guardados");
  };

  return (
    <Context.Provider
      value={{
        documento,
        setDocumento,
        ventaDefaultContent,
        handleDefaultVenta,
      }}
    >
      {children}
    </Context.Provider>
  );
}

export default Context;
