import React from "react";
import { useForm } from "react-hook-form";

import { Controller } from "react-hook-form";
import { Form, Input } from "semantic-ui-react";

function getValue(value) {
  if (!!!value) {
    return new Date().toISOString().split("T")[0];
  } else {
    return new Date(value).toISOString().split("T")[0];
  }
}

const FormFielController = ({
  name,
  title,
  type,
  control,
  errors,
  width,
  readOnly,
}) => (
  <Controller
    name={name}
    control={control}
    render={({ field: { onChange, onBlur, value } }) => {
      return (
        <Form.Field
          id={name}
          width={width || 2}
          required={errors[name]?.type === "required" ? true : false}
          control={Input}
          label={title}
          value={type === "date" ? getValue(value) : value}
          placeholder={title}
          type={type}
          error={
            errors[name] && {
              content: errors[name].message,
              pointing: "above",
            }
          }
          readOnly={readOnly || false}
          onBlur={onBlur}
          onChange={onChange}
        />
      );
    }}
  />
);
export default FormFielController;
