import React, {
  useEffect,
  useState,
  useContext,
  useRef,
  createRef,
} from "react";
import { useNavigate, useLocation } from "react-router-dom";
import { Controller, useForm } from "react-hook-form";
import QRCode from "qrcode.react";
import CriptoJs from "crypto-js";
import {
  Confirm,
  Form,
  Button,
  Message,
  Grid,
  Table,
  Input,
  Segment,
  Icon,
  Checkbox,
  Dropdown,
  Accordion,
  TextArea,
  Menu,
  Popup,
  Header,
  Ref,
} from "semantic-ui-react";

import { yupResolver } from "@hookform/resolvers/yup";
import ventaSevice from "../../services/ventas";
import FormFielController from "../FormFieldController";
import EmpresaContext from "../../context/EmpresaContext";
import schema from "./schema";
import handleBackendErrors from "../../libs/handleBackendErrors";

import settingDropdowns from "../../libs/settingDropdowns";

import FormFielDropdownSarch from "../DropdownSearch2";
import SearchProductos from "../../components/SearchProducto";

import Calcular from "../../libs/calcularPrecios";
import { toast } from "react-toastify";
import ViewFormatA4 from "../ViewFormatA4";
import { PDFViewer, PDFDownloadLink, pdf } from "@react-pdf/renderer";

import ventaContext from "../../context/VentaContext";
import formatFecha from "../../libs/formatFecha";
import Precio from "../../libs/calcularListaPrecio";
import precioService from "../../services/precio";
import FormularioPago from "./formulario_pago";
import useWindowSize from "../../hooks/useWindowSize";

import currency from "../../libs/currency";

import FormularioMovil from "./formulario_movil";

const FormularioVenta = ({
  loading,
  selected,
  handleChangeVenta,
  editionMode,
  setEditionMode,
  state,
  formatPrint,
  setFormatPrint,
  response,
}) => {
  let dropdownItem = createRef();
  let navigate = useNavigate();
  let location = useLocation();

  const { windowSize: size } = useWindowSize();

  const { empresa, setEmpresa, impresion } = useContext(EmpresaContext);
  const { setDocumento, ventaDefaultContent } = useContext(ventaContext);
  const [isLoadding, setIsloadding] = useState(false);
  const [isBackendError, setIsBackendError] = useState(false);
  const [openConfimation, setOpenConfirmation] = useState(false);
  const [addItemStatus, setAddItemStatus] = useState(false);
  const [qrCodeBase64, setQrCodeBase64] = useState(false);
  const [activeItem, setActiveItem] = useState(2);
  const [activeItemOptions, setActiveItemOptions] = useState(false);
  const [modoDescuento, setModoDescuento] = useState(false);

  const [openConfimationListPrice, setOpenConfimationListPrice] =
    useState(false);

  const [data, setData] = useState(null);

  const [itemSelected, setItemSelected] = useState(false);
  const [precioSelected, setPrecioSelected] = useState([{ key: 0 }]);
  const [productoChoose, setProductoChoose] = useState({
    model: { precio_venta: 0, cantidad: 0, descuento: 0 },
  });
  const [listDetalles, setListDetalles] = useState([]);
  const edition = [];
  const inputRef = useRef(null);

  const handleCreateNewPrice = async (e, element) => {
    const lastProduct = watcher.detalles[watcher.detalles.length - 1];
    lastProduct.moneda_id = watcher.moneda_id;
    lastProduct.costo_compra = lastProduct.precio_compra;
    const item = _precio.precioVenta(lastProduct);
    const create = await precioService.create(item);
    console.log({ create });

    setOpenConfimationListPrice(false);
  };

  const handleClick = (e, titleProps) => {
    const { index } = titleProps;
    const { activeIndex } = activeItem;
    const newIndex = activeIndex === index ? -1 : index;

    setActiveItem(newIndex);
  };

  if (!!!selected) {
    selected = !!state
      ? { ...state, ...ventaDefaultContent }
      : {
          serie: { tipo_operacion_id: 1 }, //TODO: tipo de operacion debe venir del parametro de la ruta
          tipo_operacion_id: 1,
          detalles: [],
          documento_relacion_search: undefined,
          documento_relacion_search_id: undefined,
        };
  }

  const {
    control,
    handleSubmit,
    formState: { errors },
    setValue,
    reset,
    watch,
  } = useForm({
    resolver: yupResolver(schema),
    defaultValues: { ...selected } || state.model,
  });

  const watcher = watch(); // you can also target specific fields by their names

  useEffect(() => {
    const subscription = watch((value) => {
      setDocumento(value);
    });
    return () => subscription.unsubscribe();
  }, [watch, setDocumento]);

  const _precio = new Precio();

  const calcular = new Calcular(
    "venta",
    empresa.empresa.limite_detraccion,
    empresa.empresa.por_detraccion,
    empresa.empresa.por_percepcion,
    null,
    18,
    8,
    watcher.id
  );

  const openSearchProducto = () => {
    SearchProductosRef.current.open();
    // inputRef.current.open();
  };

  useEffect(() => {
    const precios = itemSelected?.model?.precios.map((e, index) => {
      const obj = {
        key: index,
        text: e?.presentacion?.abbr,
        value: e?.id,
        model: e,
      };
      return obj;
    });
    setPrecioSelected(precios);
    return () => {
      setPrecioSelected(false);
    };
  }, [itemSelected]);

  // useEffect(() => {
  //   if (itemSelected) {
  //     handleProductoChoose(null, { name: "cantidad", value: 1.0 });
  //     return () => {
  //       handleProductoChoose(false);
  //     };
  //   }
  // }, [itemSelected]);

  useEffect(() => {
    if (selected) {
      setTimeout(() => {
        setFormatPrint({ status: false });
        const qrCodeCanvas = document?.querySelector("canvas");
        console.log(qrCodeCanvas);
        setQrCodeBase64(qrCodeCanvas?.toDataURL("image/jpg", 0.3));
        setData(selected);
        setDocumento(selected);
        console.log(qrCodeCanvas?.toDataURL("image/jpg", 0.3));
      }, 500);
    }

    return () => {
      setData(false);
    };
  }, []);

  // useEffect(() => {
  //   const subscription = watch((value, { name, type }) => {
  //     if (name === "serie_id") {
  //       console.log(value?.serie);
  //       setTimeout(() => {
  //         setValue("correlativo", value?.serie?.correlativo);
  //       }, 500);
  //     }
  //   });
  //   return () => subscription.unsubscribe();
  // }, [watch, setValue]);

  const watchDetalles = (detalles, descuento = 0) => {
    const totales = calcular.totales(detalles, descuento);
    Object.keys(totales).map((v) => {
      setValue(v, totales[v]);
      return v;
    });

    // .then((totales) => {
    // Object.keys(totales).map((v, index) => {
    //   setValue(v, totales[v]);
    //   // });
    // });
  };

  const deleteItem = (key) => {
    const lista = watcher.detalles;
    lista.splice(key, 1);
    setValue("detalles", lista);
    watchDetalles(watcher.detalles, watcher.total_descuento_global);
    // console.log("delete firts", lista);
  };

  const valid = ({ productoChoose, itemSelected }) => {
    return new Promise((res, reject) => {
      if (
        !!productoChoose &&
        !!itemSelected &&
        !!itemSelected.model &&
        !!productoChoose.model.costo_venta &&
        !!productoChoose.model.cantidad
      ) {
        res({ productoChoose, itemSelected });
      } else {
        reject(false);
      }
    });
  };

  const clearPresentationItem = () => {
    const element = dropdownItem.current.querySelector(
      '[aria-selected="true"]'
    );
    if (element) {
      dropdownItem.current.querySelector(".clear").click();
    }
  };
  const handleKeyPressTab = (e) => {
    if (e.key === "Tab" || e.key === "Enter") {
      addItem();
      clearPresentationItem();
      openSearchProducto();
    }
  };
  const addItem = async () => {
    setAddItemStatus(true);
    await valid({ productoChoose, itemSelected }).then(
      (res) => {
        const item = productoChoose.model;
        item.codigo = productoChoose.model.codigo;
        item.monto_igv = productoChoose.model.igv;
        item.precio_unitario = productoChoose.model.costo_venta;
        item.monto_precio_unitario = productoChoose.model.costo_venta;
        item.producto = {
          id: itemSelected.model.id,
          nombre: itemSelected.model.nombre,
          peso: itemSelected.model.peso,
          afecto: itemSelected.model.afecto,
        };

        if (!!!item.precios.length) {
          setOpenConfimationListPrice(true);
        }

        const lista = watcher.detalles || [];

        lista.push(item);
        setValue("detalles", lista);
        watchDetalles(watcher.detalles);
        setAddItemStatus(false);
        setProductoChoose({
          model: { precio_venta: 0, cantidad: 0, descuento: 0 },
        });
      },
      (err) => {
        toast.error("complete el formulario de ingreso de producto");
        setAddItemStatus(false);
      }
    );
  };

  const close = () => {
    if (!!!location.state?.backgroundLocation) {
      setIsloadding(false);
      return false;
    }

    navigate(location.state.backgroundLocation.pathname, {
      state: { reload: { ventas: true } },
    });
  };

  const handleProductoChoose = (e, element) => {
    if (!!!element) {
      return false;
    }

    if (element.name === "precio_venta") {
      if (!!!productoChoose && !!!productoChoose.model) {
        productoChoose.model.cantidad = 0;
      }
    }

    const input = {};
    input[element.name] = element.value;
    const model = { ...productoChoose.model, ...input };
    // console.log(">>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>");
    // console.log(itemSelected.model);
    // console.log(model);
    // console.log(">>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>");
    const newModel = calcular.item(itemSelected.model, model);

    // READY

    // console.log(
    //   model.costo_venta,
    //   model.por_utilidad,
    //   model.igv,
    //   model.monto_base_igv,
    //   model.monto_igv
    // );
    const item = productoChoose;
    item.model = newModel;

    setProductoChoose({ ...item });
  };

  const handleProductoChooseDescuentoEfectivo = (e, { name, value }) => {
    const input = {};
    input[name] = Number(value);
    const model = { ...productoChoose.model, ...input };
    const combineModel = { ...itemSelected.model, ...model };
    let newModel = calcular.descuentoEfectivo(combineModel);
    const item = productoChoose;
    item.model = newModel;
    item.model = { ...item.model, ...input };
    setProductoChoose({ ...item });
    // handleProductoChoose(e, {name,value});
  };

  const handleProductoChooseDescuentoPorcentaje = (e, { name, value }) => {
    const input = {};
    input[name] = Number(value);
    const model = { ...productoChoose.model, ...input };
    const combineModel = { ...itemSelected.model, ...model };

    const newModel = calcular.descuentoPorcentaje(combineModel);
    const item = productoChoose;
    item.model = newModel;
    item.model = { ...item.model, ...input };
    setProductoChoose({ ...item });
    // handleProductoChoose(e, { name, value });
  };

  const handleInsertDocumentRelation = () => {
    if (
      !watcher.documento_relacion_search &&
      !watcher.documento_relacion_search_id
    ) {
      toast.error("Seleccione un documento vlido");
      return false;
    }
    const set = watcher.documento_relacion || [];
    const documento_relacion = {
      documento_relacion_id: watcher.documento_relacion_search.id,
      documento_relacion: watcher.documento_relacion_search,
    };

    set.push(documento_relacion);

    setValue("documento_relacion", set);
    setValue("documento_relacion_search", 0);
    setValue("documento_relacion_search_id", 0);
  };
  const handleDeleteDocumentRelation = (index) => {
    const lista = watcher.documento_relacion;
    lista.splice(index, 1);
    setValue("documento_relacion", lista);
  };
  const updateCorrelativo = () => {
    setValue("correlativo", parseFloat(watcher?.correlativo) + 1);
  };
  const resetFormAfterSave = () => {
    updateCorrelativo();
    setValue("detalles", []);
    setValue("pagos", []);
    setValue("detalle", false);
    setValue("vendedor_id", undefined);
    setValue("total_afecto", 0);
    setValue("total_descuento_global", 0);
    setValue("total_descuento_lineal", 0);
    setValue("total_detraccion", 0);
    setValue("total_documento", 0);
    setValue("total_documento_fin", 0);
    setValue("total_exonerado", 0);
    setValue("total_igv", 0);
    setValue("total_inafecto", 0);
    setValue("total_isc", 0);
    setValue("total_percepcion", 0);
    setValue("total_peso", 0);
  };
  const onSubmit = async (data) => {
    if (!!data && data.id) {
      setIsloadding(true);
      // update
      const update = await toast.promise(ventaSevice.update(data, data.id), {
        pending: "Actualizando...",
        success: "Documento Actualizado ",
        error: "Error de documento ",
      });

      if (!!!state) {
        handleChangeVenta(data);
        if (empresa.empresa.id === update.id) {
          empresa.empresa = update;
          await setEmpresa(empresa);
          await setIsloadding(false);
          close();
        } else {
          close();
          setIsloadding(false);
        }
      } else {
        setIsloadding(false);
      }
      if (impresion?.after_save) {
        setFormatPrint({ status: !formatPrint?.status, from: "update" });
      }
      // verificar si es la empresa actual en el contexto del la instancia de local/empresa
    } else {
      //create
      // const create = await ventaSevice.create(data);

      const create = await toast.promise(ventaSevice.create(data), {
        pending: "Guardando...",
        success: "Documento Guardado ",
        error: "Error de documento ",
      });

      setIsloadding(false);
      if (create?.errors) {
        if (state?.id) {
          handleChangeVenta(data);
        }
        setIsBackendError(handleBackendErrors(create));
      } else {
        // resetFormAfterSave();
        /**Clear All Form Except (Serie, Cliente ,) */
        /**END Clear */
        if (impresion?.after_save) {
          setFormatPrint({ status: !formatPrint?.status, from: "create" });
        } else {
          resetFormAfterSave();
        }
        setIsBackendError(null);
        close();
      }
      return create;
    }
  };

  const handleChangeVentaItemList = (element, index) => {
    console.log(element, index);
    const items = watcher.detalles;
    const item = watcher.detalles[index];

    if (element.name === "cantidad") {
      item.cantidad = element.value || undefined;
    }

    if (element.name === "precio_venta") {
      items[index].precio_venta = element.value || undefined;
    }
    item.is_detail = true;
    const newItem = calcular.item(items[index], item);
    console.log(newItem);
    items[index] = newItem;
    setValue("detalles", items);

    watchDetalles(items, 0);
    // calcular.totales(items);
  };

  const changeSerie = ({ options, value }) => {
    // const selected = options[value - 1];

    const selected = options.find((v, k) => v.value === value);
    setValue("correlativo", selected?.model?.correlativo || 1);
  };

  const settStayle = () => {
    if (!!!location.pathname.search("/space/ventas/")) {
      return { overflowY: "scroll", height: "95vh", marginTop: "1vh" };
    } else {
      return { overflowY: "scroll", height: "84vh", marginTop: "1vh" };
    }
  };

  const undoChanges = (event) => {
    reset(selected);
  };

  const SearchProductosRef = useRef();

  const [token, setToken] = useState(false);

  useEffect(() => {
    const tk = CriptoJs.AES.encrypt(
      JSON.stringify({
        empresa_id: empresa.empresa.id,
        local_id: empresa.id,
        tipo_operacion_id: watcher.tipo_operacion_id,
        serie_id: watcher?.serie?.id,
        serie: watcher?.serie?.serie,
        correlativo: watcher?.correlativo,
        total_documento_fin: watcher?.total_documento_fin,
      }),
      "S2bBho4gEUOok7o3IFiUu4BMcGhz5wR2Eu35YTks"
    ).toString();
    setToken(tk);
  }, []);

  return (
    <div style={settStayle()}>
      {formatPrint && !!!formatPrint?.status ? (
        <>
          {size.width > 700 && (
            <Form onSubmit={handleSubmit(onSubmit)} noValidate>
              <Segment loading={loading}>
                <Form.Group widths={3}>
                  {!editionMode ? (
                    <Form.Input
                      fluid
                      label="Serie"
                      placeholder="Seleccione Serie"
                      value={
                        watcher?.serie?.descripcion +
                        "-" +
                        watcher?.serie?.serie
                      }
                      readOnly
                      width={4}
                    />
                  ) : (
                    <FormFielDropdownSarch
                      model={settingDropdowns.serie}
                      moreRouteParams={{
                        tipo_operacion_id: selected?.serie?.tipo_operacion_id,
                      }}
                      empresaContexto={empresa}
                      control={control}
                      venta={selected}
                      handleChangeVenta={handleChangeVenta}
                      setValue={setValue}
                      width={4}
                      onListenChange={changeSerie}
                    />
                  )}
                  {!editionMode ? (
                    <Form.Input
                      fluid
                      label="Correlativo"
                      placeholder="Selecione"
                      value={watcher?.correlativo}
                      readOnly
                      width={2}
                    />
                  ) : (
                    <FormFielController
                      name={"correlativo"}
                      title={"Correlativo"}
                      control={control}
                      errors={errors}
                      type="number"
                      width={2}
                    />
                  )}

                  {!editionMode ? (
                    <Form.Input
                      fluid
                      label="Cliente"
                      placeholder="Selecione"
                      value={watcher?.cliente?.nombre}
                      readOnly
                      width={10}
                    />
                  ) : (
                    <FormFielDropdownSarch
                      model={settingDropdowns.clientes}
                      empresaContexto={empresa}
                      control={control}
                      venta={selected}
                      handleChangeVenta={handleChangeVenta}
                      setValue={setValue}
                      width={10}
                    />
                  )}
                </Form.Group>
                <Form.Group>
                  {!editionMode ? (
                    <Form.Input
                      fluid
                      label="Almacen"
                      placeholder="Selecione"
                      value={watcher?.almacen?.nombre}
                      readOnly
                      width={4}
                    />
                  ) : (
                    <FormFielDropdownSarch
                      model={settingDropdowns.almacen}
                      empresaContexto={empresa}
                      control={control}
                      venta={selected}
                      handleChangeVenta={handleChangeVenta}
                      setValue={setValue}
                      width={4}
                    />
                  )}
                  {!editionMode ? (
                    <Form.Input
                      fluid
                      label="Condicin de Pago"
                      placeholder="Selecione"
                      value={watcher?.forma_pago?.nombre}
                      readOnly
                      width={3}
                    />
                  ) : (
                    <FormFielDropdownSarch
                      model={settingDropdowns.forma_pago}
                      empresaContexto={empresa}
                      control={control}
                      venta={selected}
                      handleChangeVenta={handleChangeVenta}
                      setValue={setValue}
                      width={3}
                    />
                  )}
                  {!editionMode ? (
                    <Form.Input
                      fluid
                      label="Fecha de Emisin"
                      placeholder="Selecione"
                      value={formatFecha.format(watcher?.fecha_emision)}
                      readOnly
                      width={3}
                    />
                  ) : (
                    <FormFielController
                      name={"fecha_emision"}
                      title={"Fecha Emisin"}
                      control={control}
                      errors={errors}
                      type="date"
                      width={3}
                    />
                  )}

                  {!editionMode ? (
                    <Form.Input
                      fluid
                      label="Fecha de Registro"
                      placeholder="Selecione"
                      value={formatFecha.format(watcher?.fecha_registro)}
                      readOnly
                      width={3}
                    />
                  ) : (
                    <FormFielController
                      name={"fecha_registro"}
                      title={"Fecha Registro"}
                      control={control}
                      errors={errors}
                      type="date"
                      width={3}
                    />
                  )}
                  {!editionMode ? (
                    <Form.Input
                      fluid
                      label="Moneda"
                      placeholder="Selecione"
                      value={watcher?.moneda.nombre}
                      readOnly
                      width={3}
                    />
                  ) : (
                    <FormFielDropdownSarch
                      model={settingDropdowns.moneda}
                      empresaContexto={empresa}
                      control={control}
                      venta={selected}
                      handleChangeVenta={handleChangeVenta}
                      setValue={setValue}
                      width={3}
                    />
                  )}
                </Form.Group>
                {editionMode && (
                  <Form.Group>
                    <Form.Field width={6}>
                      <SearchProductos
                        empresaContexto={empresa}
                        model={settingDropdowns.producto}
                        setItemSelected={setItemSelected}
                        ref={SearchProductosRef}
                      />
                    </Form.Field>
                    <Form.Field width={2}>
                      <Ref innerRef={dropdownItem}>
                        <Dropdown
                          fluid
                          clearable
                          options={precioSelected}
                          onChange={(e, element) => {
                            const item = element?.options?.find(
                              (item) => element?.value === item.value
                            );
                            setProductoChoose(item);
                          }}
                          selection
                        />
                      </Ref>
                    </Form.Field>
                    <Form.Field width={3}>
                      <Popup
                        content="Precio de Venta (Con IGV Incluido)"
                        trigger={
                          <Input
                            label={
                              !productoChoose?.model?.val_descuento
                                ? { icon: "money" }
                                : {
                                    tag: true,
                                    content: currency(
                                      productoChoose?.model?.precio_venta -
                                        productoChoose?.model?.val_descuento
                                    ),
                                    color: "teal",
                                  }
                            }
                            labelPosition={
                              !productoChoose?.model?.val_descuento
                                ? "right corner"
                                : "right"
                            }
                            type="number"
                            name="precio_venta"
                            value={productoChoose?.model?.precio_venta}
                            onChange={handleProductoChoose}
                            placeholder="Precio"
                          />
                        }
                      />
                    </Form.Field>
                    <Form.Field width={2}>
                      <Popup
                        content="Cantidad de Items"
                        trigger={
                          <Input
                            label={{ icon: "boxes" }}
                            labelPosition="right corner"
                            type="number"
                            name="cantidad"
                            value={productoChoose?.model?.cantidad}
                            placeholder="Cantidad"
                            onChange={handleProductoChoose}
                          />
                        }
                      />
                    </Form.Field>
                    <Form.Field width={2}>
                      <Popup
                        content={`Descuento en ${
                          !modoDescuento ? "Efectivo" : "Porcentaje"
                        }`}
                        trigger={
                          <div>
                            {modoDescuento ? (
                              <Input
                                type="number"
                                name="por_descuento"
                                value={productoChoose?.model?.por_descuento}
                                placeholder="Descuento Porcentaje"
                                onChange={
                                  handleProductoChooseDescuentoPorcentaje
                                }
                                onKeyDown={handleKeyPressTab}
                              >
                                <input />
                                <Button
                                  icon={"percent"}
                                  style={{ marginLeft: "10px" }}
                                  onClick={(e) => {
                                    e.preventDefault();
                                    setModoDescuento(!modoDescuento);
                                  }}
                                />
                              </Input>
                            ) : (
                              <Input
                                type="number"
                                name="val_descuento"
                                value={productoChoose?.model?.val_descuento}
                                placeholder="Descuento Valor"
                                onChange={handleProductoChooseDescuentoEfectivo}
                                onKeyDown={handleKeyPressTab}
                              >
                                <input />
                                <Button
                                  icon={"money"}
                                  style={{ marginLeft: "10px" }}
                                  onClick={(e) => {
                                    e.preventDefault();
                                    setModoDescuento(!modoDescuento);
                                  }}
                                />
                              </Input>
                            )}
                          </div>
                        }
                      />
                    </Form.Field>
                    <Form.Field width={1}>
                      <Button
                        as={"a"}
                        fluid
                        color="teal"
                        animated="vertical"
                        onClick={addItem}
                        disabled={addItemStatus}
                      >
                        <Button.Content hidden>Agregar</Button.Content>
                        <Button.Content visible>
                          <Icon name="arrow down" />
                        </Button.Content>
                      </Button>
                      {/* <Button
                    fluid
                    as="a"
                    content="Add"

                  /> */}
                    </Form.Field>
                  </Form.Group>
                )}

                <Grid>
                  <Grid.Column
                    width={12}
                    style={{ overflowY: "scroll", height: "55vh" }}
                  >
                    <Table compact definition striped>
                      <Table.Header fullWidth>
                        <Table.Row>
                          <Table.HeaderCell width={1} />
                          <Table.HeaderCell width={8}>
                            Producto  Servicio
                          </Table.HeaderCell>
                          <Table.HeaderCell textAlign={"right"} width={2}>
                            <abbr title="Precio de venta con IGV">
                              {watcher?.moneda?.simbolo}
                            </abbr>
                          </Table.HeaderCell>
                          <Table.HeaderCell textAlign={"right"} width={2}>
                            <abbr title="Cantidad">Cant</abbr>
                          </Table.HeaderCell>
                          <Table.HeaderCell textAlign={"right"} width={2}>
                            <abbr title="Descuento">Desc</abbr>
                          </Table.HeaderCell>
                          <Table.HeaderCell
                            textAlign={"right"}
                            width={1}
                          ></Table.HeaderCell>
                        </Table.Row>
                      </Table.Header>

                      <Table.Body>
                        {watcher?.detalles?.map((item, index) => {
                          return (
                            <Table.Row key={index}>
                              <Table.Cell collapsing>
                                <Checkbox slider />
                              </Table.Cell>
                              <Table.Cell>{item.producto.nombre}</Table.Cell>
                              <Table.Cell textAlign={"right"}>
                                {!item.val_descuento ? (
                                  item.precio_venta !== null ? (
                                    <Input
                                      value={item.precio_venta}
                                      onChange={(e, element) =>
                                        handleChangeVentaItemList(
                                          element,
                                          index
                                        )
                                      }
                                      name={"precio_venta"}
                                      disabled={!editionMode}
                                    />
                                  ) : (
                                    (0).toFixed(2)
                                  )
                                ) : (
                                  <>
                                    <s>
                                      {item.precio_venta
                                        ? currency(Number(item?.precio_venta))
                                        : (0).toFixed(2)}
                                    </s>
                                    <span style={{ marginLeft: "10px" }}>
                                      {item.precio_venta
                                        ? currency(
                                            Number(
                                              item?.precio_venta -
                                                item.val_descuento
                                            )
                                          )
                                        : (0).toFixed(2)}
                                    </span>
                                  </>
                                )}
                              </Table.Cell>
                              <Table.Cell style={{ align: "center" }}>
                                <Input
                                  placeholder="Cantidad"
                                  value={item?.cantidad}
                                  onChange={(e, element) =>
                                    handleChangeVentaItemList(element, index)
                                  }
                                  name={"cantidad"}
                                  disabled={!editionMode}
                                />
                              </Table.Cell>
                              <Table.Cell textAlign={"right"}>
                                {item.val_descuento
                                  ? currency(Number(item?.val_descuento))
                                  : (0).toFixed(2)}
                              </Table.Cell>
                              <Table.Cell textAlign={"right"}>
                                <Button
                                  circular
                                  as="a"
                                  size="mini"
                                  onClick={() => {
                                    deleteItem(index);
                                  }}
                                  disabled={!editionMode}
                                  icon="trash"
                                />
                              </Table.Cell>
                            </Table.Row>
                          );
                        })}
                      </Table.Body>
                    </Table>

                    {!!Number(watcher.is_nulled) ? (
                      <Segment placeholder>
                        <Header icon>
                          <Icon name="ban" />
                          Documento sin detalles.
                        </Header>
                      </Segment>
                    ) : (
                      <></>
                    )}
                  </Grid.Column>
                  <Grid.Column floated="right" width={4}>
                    <Table compact="very" color="black">
                      <Table.Body>
                        {true && (
                          <Table.Row>
                            <Table.Cell textAlign="right">Peso </Table.Cell>
                            <Table.Cell textAlign="right">
                              Kg {Number(watcher?.total_peso).toFixed(2)}
                            </Table.Cell>
                          </Table.Row>
                        )}
                        {true && (
                          <Table.Row>
                            <Table.Cell textAlign="right"> Afecto </Table.Cell>
                            <Table.Cell textAlign="right">
                              {watcher?.moneda?.simbolo}{" "}
                              {currency(watcher?.total_afecto)}
                              {" ("}
                              {watcher?.moneda?.iso}
                              {") "}
                            </Table.Cell>
                          </Table.Row>
                        )}
                        {true && (
                          <Table.Row>
                            <Table.Cell textAlign="right">
                              {" "}
                              Inafecto{" "}
                            </Table.Cell>
                            <Table.Cell textAlign="right">
                              {watcher?.moneda?.simbolo}{" "}
                              {currency(watcher?.total_inafecto)}
                              {" ("}
                              {watcher?.moneda?.iso}
                              {") "}
                            </Table.Cell>
                          </Table.Row>
                        )}
                        {true && (
                          <Table.Row>
                            <Table.Cell textAlign="right"> ISC </Table.Cell>
                            <Table.Cell textAlign="right">
                              {watcher?.moneda?.simbolo}{" "}
                              {currency(watcher.total_isc)}
                              {" ("}
                              {watcher?.moneda?.iso}
                              {") "}
                            </Table.Cell>
                          </Table.Row>
                        )}
                        {true && (
                          <Table.Row>
                            <Table.Cell textAlign="right"> IGV </Table.Cell>
                            <Table.Cell textAlign="right">
                              {watcher?.moneda?.simbolo}{" "}
                              {currency(watcher?.total_igv)}
                              {" ("}
                              {watcher?.moneda?.iso}
                              {") "}
                            </Table.Cell>
                          </Table.Row>
                        )}
                        {true && (
                          <Table.Row>
                            <Table.Cell textAlign="right">
                              Descuento Items
                            </Table.Cell>
                            <Table.Cell textAlign="right">
                              {watcher?.moneda?.simbolo}{" "}
                              {currency(watcher?.total_descuento_lineal)}
                              {" ("}
                              {watcher?.moneda?.iso}
                              {") "}
                            </Table.Cell>
                          </Table.Row>
                        )}

                        {true && (
                          <Table.Row>
                            <Table.Cell textAlign="right">
                              Descuento Global
                            </Table.Cell>
                            <Table.Cell textAlign="right">
                              {watcher?.moneda?.simbolo}{" "}
                              {currency(watcher?.total_descuento_global)}
                              {" ("}
                              {watcher?.moneda?.iso}
                              {") "}
                            </Table.Cell>
                          </Table.Row>
                        )}

                        {true && (
                          <Table.Row>
                            <Table.Cell textAlign="right">Documento</Table.Cell>
                            <Table.Cell textAlign="right">
                              {watcher?.moneda?.simbolo}{" "}
                              {currency(watcher?.total_documento)}
                              {" ("}
                              {watcher?.moneda?.iso}
                              {") "}
                            </Table.Cell>
                          </Table.Row>
                        )}

                        {true && (
                          <Table.Row>
                            <Table.Cell textAlign="right">
                              {" "}
                              Percepcin{" "}
                            </Table.Cell>
                            <Table.Cell textAlign="right">
                              {watcher?.moneda?.simbolo}{" "}
                              {currency(watcher?.total_percepcion)}
                              {" ("}
                              {watcher?.moneda?.iso}
                              {") "}
                            </Table.Cell>
                          </Table.Row>
                        )}

                        {true && (
                          <Table.Row>
                            <Table.Cell textAlign="right">
                              Detraccin
                            </Table.Cell>
                            <Table.Cell textAlign="right">
                              {watcher?.moneda?.simbolo}{" "}
                              {currency(watcher?.total_detraccion)}
                              {" ("}
                              {watcher?.moneda?.iso}
                              {") "}
                            </Table.Cell>
                          </Table.Row>
                        )}

                        {true && (
                          <Table.Row>
                            <Table.Cell active textAlign="right">
                              Total
                            </Table.Cell>
                            <Table.Cell active textAlign="right">
                              {!watcher.is_free ? (
                                <strong>
                                  {watcher?.moneda?.simbolo}{" "}
                                  {currency(watcher?.total_documento_fin)}
                                  {" ("}
                                  {watcher?.moneda?.iso}
                                  {") "}
                                </strong>
                              ) : (
                                <strong>
                                  {watcher?.moneda?.simbolo} {(0).toFixed(2)}
                                  {" ("}
                                  {watcher?.moneda?.iso}
                                  {") "}
                                </strong>
                              )}
                            </Table.Cell>
                          </Table.Row>
                        )}
                      </Table.Body>
                    </Table>

                    {editionMode && (
                      <div>
                        <Button
                          loading={isLoadding}
                          disabled={isLoadding}
                          content="Guardar"
                          primary
                          fluid
                        />

                        <Button
                          style={{ marginTop: "1vh" }}
                          fluid
                          as="a"
                          content="Deshacer Cambios"
                          onClick={() => {
                            reset(selected);
                          }}
                        />

                        <Accordion as={Menu} vertical fluid>
                          <Menu.Item>
                            <Accordion.Title
                              active={activeItemOptions === true}
                              content="Mas Opciones"
                              index={0}
                              onClick={() => {
                                setActiveItemOptions(!activeItemOptions);
                              }}
                            />
                            <Accordion.Content
                              active={activeItemOptions === true}
                              content={
                                <Form.Group grouped>
                                  <Form.Checkbox
                                    label="Es gratuita"
                                    name="is_free"
                                    checked={watcher.is_free}
                                    onChange={() => {
                                      setValue("is_free", !watcher.is_free);
                                    }}
                                    type="number"
                                  />
                                  {/* <Form.Checkbox
                                label="Es exportacin"
                                name="is_export"
                                checked={watcher.is_export}
                                onChange={() => {
                                  if (!!!watcher.moneda.dolar) {
                                    toast.error(
                                      "La moneda seleccionada no es valida, solo USD es permitido"
                                    );
                                    return false;
                                  }
                                  setValue("is_export", !watcher.is_export);
                                }}
                              /> */}
                                  {/* <Form.Checkbox
                                label="Es exonerada"
                                name="is_exonerada"
                                checked={watcher.is_exonerada}
                                onChange={() => {
                                  setValue(
                                    "is_exonerada",
                                    !watcher.is_exonerada
                                  );
                                }}
                              /> */}
                                  <Form.Field>
                                    <Input
                                      label={{ content: "Desc" }}
                                      labelPosition="right"
                                      placeholder="Descuento Global"
                                      type="number"
                                      value={watcher.total_descuento_global}
                                      onChange={(e, element) => {
                                        setValue(
                                          "total_descuento_global",
                                          e.target.value
                                        );

                                        watchDetalles(
                                          watcher.detalles,
                                          e.target.value
                                        );
                                      }}
                                    />
                                  </Form.Field>
                                </Form.Group>
                              }
                            />
                          </Menu.Item>
                        </Accordion>
                      </div>
                    )}
                  </Grid.Column>
                </Grid>

                {Object.keys(errors)?.length ? (
                  <Message
                    color="orange"
                    header="Advertencia"
                    list={[
                      "Verifique que todos los campos requeridos esten completos.",
                    ]}
                  />
                ) : (
                  <></>
                )}
                {isBackendError?.length && (
                  <Message
                    color="red"
                    header="Verifique los siguientes campos"
                    list={isBackendError}
                  />
                )}
              </Segment>

              <Confirm
                open={openConfimation}
                content="Seguro de Deshacer los cambios, perderas los datos modificados"
                onCancel={() => setOpenConfirmation(false)}
                onConfirm={undoChanges}
              />

              <Confirm
                open={openConfimationListPrice}
                content="Deseas guardar este precio para futuro uso?"
                onCancel={() => {
                  setOpenConfimationListPrice(false);
                }}
                onConfirm={handleCreateNewPrice}
              />
              {watcher.tipo_operacion_id === 3 && (
                <Segment
                  color={"teal"}
                  inverted
                  style={{ marginTop: "1vh" }}
                  loading={loading}
                >
                  <Form.Group>
                    {!editionMode ? (
                      <Form.Input
                        fluid
                        label="Motivo de la Guia de Remision"
                        placeholder="Selecione"
                        value={
                          watcher?.motivos_sunat_guia_remision?.descripcion
                        }
                        readOnly
                        width={4}
                      />
                    ) : (
                      <FormFielDropdownSarch
                        model={settingDropdowns?.motivos_sunat_guia_remision}
                        empresaContexto={empresa}
                        control={control}
                        venta={selected}
                        handleChangeVenta={handleChangeVenta}
                        setValue={setValue}
                        width={4}
                      />
                    )}
                    {!editionMode ? (
                      <Form.Input
                        fluid
                        label="Transportista"
                        placeholder="Selecione"
                        value={watcher?.transportista?.nombre}
                        readOnly
                        width={6}
                      />
                    ) : (
                      <FormFielDropdownSarch
                        model={settingDropdowns?.transportista}
                        empresaContexto={empresa}
                        control={control}
                        venta={selected}
                        handleChangeVenta={handleChangeVenta}
                        setValue={setValue}
                        width={6}
                      />
                    )}
                    {!editionMode ? (
                      <Form.Input
                        fluid
                        label="Vehculo"
                        placeholder="Selecione"
                        value={watcher?.vehiculo?.descripcion}
                        readOnly
                        width={6}
                      />
                    ) : (
                      <FormFielDropdownSarch
                        model={settingDropdowns?.vehiculo}
                        empresaContexto={empresa}
                        control={control}
                        venta={selected}
                        handleChangeVenta={handleChangeVenta}
                        setValue={setValue}
                        width={6}
                      />
                    )}
                  </Form.Group>
                  <Form.Group>
                    <FormFielController
                      name={"direccion_partida"}
                      title={"Direccion de Partida"}
                      control={control}
                      errors={errors}
                      type="text"
                      width={8}
                      readOnly={!editionMode}
                    />

                    {!editionMode ? (
                      <Form.Input
                        fluid
                        label="Ubigeo de Partida"
                        placeholder="Selecione"
                        value={
                          !!!watcher?.ubigeo_partida
                            ? ""
                            : watcher?.ubigeo_partida?.provincia +
                              "-" +
                              watcher?.ubigeo_partida?.distrito +
                              "-" +
                              watcher?.ubigeo_partida?.departamento
                        }
                        readOnly
                        width={8}
                      />
                    ) : (
                      <FormFielDropdownSarch
                        model={settingDropdowns.ubigeo_partida}
                        empresaContexto={empresa}
                        control={control}
                        venta={selected}
                        handleChangeVenta={handleChangeVenta}
                        setValue={setValue}
                        width={8}
                      />
                    )}
                  </Form.Group>
                  <Form.Group>
                    <FormFielController
                      name={"direccion_destino"}
                      title={"Direccion de Destino"}
                      control={control}
                      errors={errors}
                      type="text"
                      width={8}
                      readOnly={!editionMode}
                    />
                    {!editionMode ? (
                      <Form.Input
                        fluid
                        label="Ubigeo de Destino"
                        placeholder="Selecione"
                        value={
                          !!!watcher?.ubigeo_destino
                            ? ""
                            : watcher?.ubigeo_destino?.provincia +
                              "-" +
                              watcher?.ubigeo_destino?.distrito +
                              "-" +
                              watcher?.ubigeo_destino?.departamento
                        }
                        readOnly
                        width={8}
                      />
                    ) : (
                      <FormFielDropdownSarch
                        model={settingDropdowns.ubigeo_destino}
                        empresaContexto={empresa}
                        control={control}
                        venta={selected}
                        handleChangeVenta={handleChangeVenta}
                        setValue={setValue}
                        width={8}
                      />
                    )}
                  </Form.Group>
                </Segment>
              )}

              <Segment secondary loading={loading}>
                <Header as="h5">
                  <Icon name="list ul" />
                  <Header.Content>Detalles</Header.Content>
                </Header>
                <Accordion exclusive={false} fluid color={"teal"}>
                  <Accordion.Title
                    active={activeItem === 2}
                    index={2}
                    onClick={handleClick}
                  >
                    <Icon name="dropdown" />
                    Detalles de la venta
                  </Accordion.Title>
                  <Accordion.Content active={activeItem === 2}>
                    <Controller
                      name={"detalle"}
                      control={control}
                      render={({ field: { onChange, onBlur, value } }) => (
                        <TextArea
                          value={value}
                          onBlur={onBlur}
                          onChange={onChange}
                          placeholder="Escribe un Comentario (sera agreagado en la impresin del documento)"
                          readOnly={!editionMode}
                        />
                      )}
                    />
                  </Accordion.Content>
                  <Accordion.Title
                    active={activeItem === 0}
                    index={0}
                    onClick={handleClick}
                  >
                    <Icon name="dropdown" />
                    Documentos asociados
                  </Accordion.Title>
                  <Accordion.Content active={activeItem === 0}>
                    {editionMode && (
                      <Form.Group>
                        <FormFielDropdownSarch
                          model={settingDropdowns?.documento_relacion_search}
                          empresaContexto={empresa}
                          control={control}
                          venta={selected}
                          handleChangeVenta={handleChangeVenta}
                          setValue={setValue}
                          width={10}
                        />
                        <Form.Field style={{ marginTop: "24px" }}>
                          <Button
                            primary
                            as="a"
                            onClick={handleInsertDocumentRelation}
                          >
                            Agregar Documento
                          </Button>
                        </Form.Field>
                      </Form.Group>
                    )}

                    {!editionMode && (
                      <Button secondary as="a">
                        Ver Historial
                      </Button>
                    )}

                    <Table compact collapsing>
                      <Table.Header>
                        <Table.Row>
                          <Table.HeaderCell>Documento</Table.HeaderCell>
                          <Table.HeaderCell>Eliminar</Table.HeaderCell>
                        </Table.Row>
                      </Table.Header>

                      <Table.Body>
                        {!!watcher.documento_relacion &&
                          watcher?.documento_relacion.map((v, index) => {
                            return (
                              <Table.Row>
                                <Table.Cell>
                                  {v?.documento_relacion.uniqueserie
                                    .split("-")
                                    .splice(2)
                                    .join("-")}
                                </Table.Cell>
                                <Table.Cell>
                                  <Button
                                    circular
                                    as="a"
                                    size="mini"
                                    onClick={() => {
                                      handleDeleteDocumentRelation(index);
                                    }}
                                    disabled={!editionMode}
                                    icon="trash"
                                  />
                                </Table.Cell>
                              </Table.Row>
                            );
                          })}
                      </Table.Body>
                    </Table>
                  </Accordion.Content>

                  <Accordion.Title
                    active={activeItem === 1}
                    index={1}
                    onClick={handleClick}
                  >
                    <Icon name="dropdown" />
                    Vendedor
                  </Accordion.Title>
                  <Accordion.Content active={activeItem === 1}>
                    <Form.Group>
                      {!editionMode ? (
                        <Form.Input
                          fluid
                          label="Vendedor"
                          placeholder="Selecione"
                          value={watcher?.vendedor?.nombre}
                          readOnly
                          width={10}
                        />
                      ) : (
                        <FormFielDropdownSarch
                          model={settingDropdowns?.vendedor}
                          empresaContexto={empresa}
                          control={control}
                          venta={selected}
                          handleChangeVenta={handleChangeVenta}
                          setValue={setValue}
                          width={10}
                        />
                      )}
                    </Form.Group>
                  </Accordion.Content>

                  <Accordion.Title
                    active={activeItem === 3}
                    index={3}
                    onClick={handleClick}
                  >
                    <Icon name="dropdown" />
                    Informacion Relacionada
                  </Accordion.Title>
                  <Accordion.Content active={activeItem === 3}>
                    <Form.Group>
                      <FormFielController
                        name={"numero_expediente"}
                        title={"Numero de Expediente"}
                        control={control}
                        errors={errors}
                        type="text"
                        width={10}
                        readOnly={!editionMode}
                      />
                    </Form.Group>
                    <Form.Group>
                      <FormFielController
                        name={"orden_compra"}
                        title={"Orden de Compra"}
                        control={control}
                        errors={errors}
                        type="text"
                        width={10}
                        readOnly={!editionMode}
                      />
                    </Form.Group>
                    <Form.Group>
                      <FormFielController
                        name={"codigo_unidad_ejecutora"}
                        title={"Cdigo Unidad Ejecutora"}
                        control={control}
                        errors={errors}
                        type="text"
                        width={10}
                        readOnly={!editionMode}
                      />
                    </Form.Group>
                    <Form.Group>
                      <FormFielController
                        name={"numero_proceso_seleccion"}
                        title={"Nmero de proceso de seleccin"}
                        control={control}
                        errors={errors}
                        type="text"
                        width={10}
                        readOnly={!editionMode}
                      />
                    </Form.Group>
                    <Form.Group>
                      <FormFielController
                        name={"numero_contrato"}
                        title={"Nmero de Contrato"}
                        control={control}
                        errors={errors}
                        type="text"
                        width={10}
                        readOnly={!editionMode}
                      />
                    </Form.Group>
                  </Accordion.Content>
                </Accordion>
                {editionMode && (
                  <div style={{ marginTop: "5vh" }}>
                    <Button
                      loading={isLoadding}
                      disabled={isLoadding}
                      content="Guardar"
                      primary
                    />

                    <Button
                      style={{ marginTop: "1vh" }}
                      as="a"
                      content="Deshacer Cambios"
                      onClick={() => {
                        reset(selected);
                      }}
                    />
                  </div>
                )}
              </Segment>

              {watcher.forma_pago_id !== 1 &&
              watcher.tipo_operacion_id === 1 ? (
                <Segment secondary>
                  <FormularioPago
                    editionMode={editionMode}
                    pagos={watcher.pagos}
                    moneda={watcher.moneda}
                    setChangeFather={setValue}
                    total_operacion={watcher.total_documento_fin}
                  />
                  {editionMode && (
                    <div style={{ marginTop: "5vh" }}>
                      <Button
                        loading={isLoadding}
                        disabled={isLoadding}
                        content="Guardar"
                        primary
                      />

                      <Button
                        style={{ marginTop: "1vh" }}
                        as="a"
                        content="Deshacer Cambios"
                        onClick={() => {
                          reset(selected);
                        }}
                      />
                    </div>
                  )}
                </Segment>
              ) : (
                ""
              )}

              <Segment style={{ marginBottom: "20vh" }} loading={loading}>
                <QRCode
                  value={`${
                    process.env.NODE_ENV !== "development "
                      ? process.env.REACT_APP_PRODUCTION_PUBLIC_FRONT
                      : process.env.REACT_APP_DEVELOPER_PUBLIC_FRONT
                  }/comprobante?tk=${token}`}
                />
                {/* <p>
                  {`${
                    process.env.NODE_ENV !== "development "
                      ? process.env.REACT_APP_PRODUCTION_PUBLIC_FRONT
                      : process.env.REACT_APP_DEVELOPER_PUBLIC_FRONT
                      
                  }/comprobante?tk=${CriptoJs.AES.encrypt(
                    JSON.stringify({
                      empresa_id: empresa.empresa.id,
                      local_id: empresa.id,
                      tipo_operacion_id: watcher.tipo_operacion_id,
                      serie_id: watcher?.serie?.id,
                      serie: watcher?.serie?.serie,
                      correlativo: watcher?.correlativo,
                      total_documento_fin: watcher?.total_documento_fin,
                    }),
                    "S2bBho4gEUOok7o3IFiUu4BMcGhz5wR2Eu35YTks"
                  ).toString()}`}
                </p> */}
              </Segment>
            </Form>
          )}
          {size.width < 699 && (
            <FormularioMovil
              control={control}
              handleSubmit={handleSubmit}
              errors={errors}
              setValue={setValue}
              reset={reset}
              watcher={watcher}
              editionMode={editionMode}
              setEditionMode={setEditionMode}
              settingDropdowns={settingDropdowns}
              empresa={empresa}
              setEmpresa={setEmpresa}
              impresion={impresion}
              selected={selected}
              handleChangeVenta={handleChangeVenta}
              state={state}
              formatPrint={formatPrint}
              setFormatPrint={setFormatPrint}
            />
          )}
        </>
      ) : (
        <>
          {size.width > 700 && (
            <PDFViewer style={{ width: "100%", height: "74vh" }}>
              <ViewFormatA4
                empresa={empresa}
                status={watcher}
                qrCodeBase64={qrCodeBase64}
                impresion={impresion}
              />
            </PDFViewer>
          )}

          {size.width < 699 && (
            <PDFViewer style={{ width: "100%", height: "74vh" }}>
              <ViewFormatA4
                empresa={empresa}
                status={watcher}
                qrCodeBase64={qrCodeBase64}
                impresion={impresion}
              />
            </PDFViewer>
          )}

          <PDFDownloadLink
            document={
              <ViewFormatA4
                empresa={empresa}
                status={watcher}
                qrCodeBase64={qrCodeBase64}
                impresion={impresion}
              />
            }
            className="button ui primary fluid"
            fileName={`${empresa.empresa.ruc}-${watcher.serie?.sunat_codigo}-${watcher.serie?.serie}-${watcher.correlativo}.pdf`}
          >
            Descargar
          </PDFDownloadLink>
          <Button
            style={{ marginTop: "5px" }}
            basic
            fluid
            onClick={() => {
              if (formatPrint.from === "create") {
                setFormatPrint({ status: false });
                resetFormAfterSave();
              } else {
                setFormatPrint({ status: false });
              }
            }}
          >
            {" "}
            Cerrar
          </Button>
        </>
      )}
    </div>
  );
};
export default FormularioVenta;
