import axios from "axios";
function send(data) {
  const path = process.env.NODE_ENV === "development " ? process.env.REACT_APP_DEVELOPER_SERVICE_MAIL : process.env.REACT_APP_PRODUCTION_SERVICE_MAIL

  console.log(process.env.REACT_APP_DEVELOPER_SERVICE_MAIL,process.env.REACT_APP_PRODUCTION_SERVICE_MAIL)


  const formData = new FormData();
  const keys = Object.keys(data);
  for (let i = 0; i < keys.length; i++) {
    const e = keys[i];
    if (
      [
        "id",
        "message",
        "sunat_documento",
        "cliente",
        "empresa_id",
        "file",
        "files",
        "empresa",
        "serie",
        "correlativo",
      ].indexOf(e) >= 0
    ) {
      if (typeof data[e] === "object" && e !== "file") {
        formData.append(e, JSON.stringify(data[e]));
      } else {
        formData.append(e, data[e]);
      }
    }
  }
  console.log(formData);
  return new Promise((resolve, reject) => {
    axios
      .post(path+"/send", formData)
      .then((res) => {
        resolve(res.data);
      })
      .catch((error) => {
        if (error.response) {
          resolve(error.response.data);
        }
      });
  });
}

export default { send };
