class Calcular {
  constructor(
    modo,
    limite_detraccion,
    por_detraccion,
    por_percepcion,
    limite_percepcion,
    por_igv,
    decimales,
    editionMode
  ) {
    this.modo = modo;
    this.limite_detraccion = limite_detraccion;
    this.por_detraccion = por_detraccion;
    this.por_percepcion = por_percepcion;
    this.limite_percepcion = limite_percepcion;
    this.por_igv = por_igv;
    this.decimales = decimales;
    this.total_afecto = 0;
    this.total_inafecto = 0;
    this.total_igv = 0;
    this.total_isc = 0;
    this.total_exonerado = 0;
    this.total_descuento_lineal = 0;
    this.total_descuento_global = 0;
    this.total_documento = 0;
    this.total_percepcion = 0;
    this.total_detraccion = 0;
    this.total_documento_fin = 0;
    this.total_peso = 0;
    this.editionMode = editionMode;
  }

  roundOff(num) {
    /**cantidad de decimales en el background */
    const x = Math.pow(10, 8);
    return Math.round(num * x) / x;
  }

  /**
   * Calcular valores de un item
   * @example
   * // returns 2
   * calcular.item({id:2});
   * @example
   * // returns 3
   * calcular.method({id:5});
   * @returns {Object} Returns the value of x for the equation.
   */
  item(item, precio) {
    let igv = 0;

    if (item.afecto) {
      console.log("afecto");
      item = this.isc(item, precio);
      item = this.igv(item, precio);
      item = this.detraccion(item, precio);
      item.afecto = 1;
      // item = this.descuento(item, precio);
    } else {
      console.log("inafecto");
      item = this.inafecto(item, precio);
      item.afecto = 0;
    }

    /**
     * Afecto
     */

    precio = { ...precio, ...item };
    precio.id = undefined;
    // precio.producto_id = item.id;
    // precio.costo_venta = costo_venta;
    // precio.precio_unitario = costo_venta;
    // precio.monto_base_igv = costo_venta;
    // precio.igv = igv;
    // precio.monto_igv = igv;
    // precio.por_utilidad = por_utilidad;
    // precio.monto_precio_unitario = costo_venta;

    if (!!this.editionMode) {
      //update
      precio.producto_id = item?.id;
      if (precio.is_detail) {
        precio.producto_id = precio?.producto?.id;
        console.log(">>>>>>>>>>>>> UPDATED >>>>>>>>>>>>>>>>>");
      }
    } else {
      //create
      precio.producto_id = item?.id;
      if (precio.is_detail) {
        precio.producto_id = precio?.producto?.id;
      }
      console.log(">>>>>>>>>>>>> CREACION >>>>>>>>>>>>>>>>>");
    }
    console.log(
      ">>>>>>>>>>>>ID",
      precio.producto_id,
      precio?.producto?.id,
      item?.id
    );
    // if (precio.is_detail) {
    //   precio.producto_id = !!this.editionMode ? item?.id : precio?.producto?.id;
    // } else {
    //   precio.producto_id = !!this.editionMode ? precio?.producto?.id : item?.id;
    // }

    precio.total_impuestos = igv;
    precio.val_descuento = precio.val_descuento || 0;
    precio.tipo_afecto_igv = precio.tipo_afecto_igv || 10;
    precio.tipo_producto_id = item.tipo_producto_id;
    precio.precio_unitario = item.precio_venta - item.igv;
    return precio;
  }
  isc(item, precio) {
    if (!!precio && !!precio.por_isc) {
      if (this.modo === "venta") {
        precio.isc = (precio?.precio_venta * item?.por_isc) / 100;
      } else {
        precio.isc = 0;
      }
    } else {
      precio.isc = 0;
    }
    return item;
  }
  igv(item, precio) {
    if (this.modo === "venta") {
      precio.monto_base_igv = precio.precio_venta + precio.isc;
      item.costo_venta = this.roundOff(
        precio.precio_venta / (this.por_igv / 100 + 1)
      );
      item.igv = item.costo_venta * (this.por_igv / 100);
      item.por_utilidad = this.roundOff(
        (parseFloat(item.costo_venta) / precio.costo_compra) * 100 - 100
      );
    } else {
      precio.monto_base_igv = precio.precio_venta;
    }
    item.monto_igv =
      1.18 * precio.monto_base_igv -
      precio.monto_base_igv -
      (precio.val_descuento - precio.val_descuento / 1.18);
    return item;
  }
  inafecto(item, precio) {
    if (this.modo === "venta") {
      precio.monto_base_igv = precio.precio_venta;

      item.costo_venta = this.roundOff(precio.precio_venta);

      item.igv = 0;
      item.por_utilidad = this.roundOff(
        (parseFloat(item.costo_venta) / precio.costo_compra) * 100 - 100
      );
    } else {
      precio.monto_base_igv = precio.precio_venta;
    }
    item.monto_igv = 0;
    item.isc = 0;
    return item;
  }

  detraccion(item, precio) {
    if (item.tipo_producto_id === 2 || item.por_detraccion > 0) {
      item.check_detraccion_amount =
        (this.roundOff(precio.costo_venta) +
          this.roundOff(precio.isc) +
          this.roundOff(precio.igv)) *
        this.roundOff(precio.cantidad);
      if (item.tipo_producto_id === 1) {
        /***
         * PRODUCTOS
         */
        item.detraccion =
          item.check_detraccion_amount *
          this.roundOff(item.por_detraccion / 100);
        return item;
      }

      if (item.tipo_producto_id === 2) {
        /***
         * SERVICIOS
         */
        item.detraccion =
          item.check_detraccion_amount *
          this.roundOff(this.por_detraccion / 100);
        return item;
      }
    } else {
      item.detraccion = 0;
      item.check_detraccion_amount = 0;
      return item;
    }
  }
  descuentoEfectivo(item) {
    item.por_descuento = this.roundOff(
      (100 * item.val_descuento) / (item.costo_venta + item.igv)
    ).toFixed(2);
    return item;
  }

  descuentoPorcentaje(item) {
    item.val_descuento = this.roundOff(
      (item.por_descuento * (item.costo_venta + item.igv)) / 100
    ).toFixed(2);
    return item;
  }

  totales(detalles, descuento_global) {
    // return new Promise((res, reject) => {
    this.total_afecto = 0;
    this.total_inafecto = 0;
    this.total_igv = 0;
    this.total_isc = 0;
    this.total_exonerado = 0;
    this.total_descuento_global = descuento_global;
    this.total_descuento_lineal = 0;
    this.total_documento = 0;
    this.total_percepcion = 0;
    this.total_detraccion = 0;
    this.total_documento_fin = 0;
    this.total_peso = 0;
    this.check_detraccion_amount = 0;

    for (let i = 0; i < detalles.length; i++) {
      const item = detalles[i];
      if (!!item.producto.afecto || !!item.afecto) {
        /**Afecto */

        this.total_afecto += this.roundOff(
          item.precio_unitario * parseFloat(item.cantidad)
        );

        this.total_igv += this.roundOff(item.igv * parseFloat(item.cantidad));
      } else {
        /**Inafecto */
        this.total_inafecto += this.roundOff(
          item.precio_unitario * parseFloat(item.cantidad)
        );
        this.total_igv += this.roundOff(item.igv * parseFloat(item.cantidad));
      }

      /**Peso */
      this.total_peso += this.roundOff(
        item?.producto?.peso * parseFloat(item?.cantidad)
      );

      /**ISC */
      this.total_isc +=
        this.roundOff(item?.isc) * this.roundOff(item?.cantidad);

      /**Detraccion */
      this.check_detraccion_amount += this.roundOff(
        item.check_detraccion_amount
      );

      this.total_detraccion += this.roundOff(item.detraccion);

      /**Descuento Lineal*/
      this.total_descuento_lineal +=
        this.roundOff(item?.val_descuento) * this.roundOff(item?.cantidad);

      /**
       *
       * END FOR
       */
    }

    if (this.check_detraccion_amount > this.limite_detraccion) {
      //this.total_detraccion = this.total_detraccion;
    } else {
      this.total_detraccion = 0;
    }

    this.total_documento = this.roundOff(
      this.roundOff(this.total_igv) +
        this.roundOff(this.total_afecto) +
        this.roundOff(this.total_inafecto) +
        this.roundOff(this.total_isc)
    );

    if (this.por_percepcion > 0) {
      this.total_percepcion =
        this.total_documento * (this.roundOff(this.por_percepcion) / 100);
    } else {
      this.total_percepcion = 0;
    }

    this.total_documento_fin =
      this.total_documento +
      this.total_percepcion -
      this.roundOff(this.total_descuento_global) -
      this.roundOff(this.total_descuento_lineal);

    return {
      total_afecto: this.total_afecto,
      total_inafecto: this.total_inafecto,
      total_igv: this.total_igv,
      total_isc: this.total_isc,
      total_exonerado: this.total_exonerado,
      total_descuento_lineal: this.total_descuento_lineal,
      total_descuento_global: this.roundOff(descuento_global),
      total_documento: this.total_documento,
      total_percepcion: this.total_percepcion,
      total_detraccion: this.total_detraccion,
      total_documento_fin: this.total_documento_fin,
      total_peso: this.total_peso,
    };

    // });
  }
}

export default Calcular;
