import instance from "./_instance";
import qs from "qs";
// Create instance called instance

function show(end_point, query = { order: { id: "DESC" } }) {
  if (!!!query.limit) {
    query.limit = 20;
  }

  console.log(end_point,query.limit);
  const str = qs.stringify(query);
  return new Promise((ok, reject) => {
    instance.get(`${end_point}?${str}`).then(
      (res) => {
        ok(res.data);
      },
      (bad) => {
        reject(new Error("bad request"));
      }
    );
  });
}

export default { show };
