import { useEffect, useContext, useState } from "react";
import {
  Label,
  Button,
  Grid,
  Header,
  Segment,
  Icon,
  List,
  Popup,
  Dropdown,
  Pagination,
  Menu,
  ListContent,
  Dimmer,
  Loader,
  Checkbox,
} from "semantic-ui-react";
import { Link, useLocation, useNavigate } from "react-router-dom";
import Moment from "react-moment";
import CreateButton from "../../components/CreateButton";
import ConverterButton from "../../components/ConverterButton";
import ventaService from "../../services/ventas";
import FormularioVenta from "../../components/ventas/formulario";
import EmpresaContext from "../../context/EmpresaContext";
import { PDFDownloadLink, pdf } from "@react-pdf/renderer";
import { updated } from "../../libs/updated";
import { LabelUpdate } from "../../components/LabelUpdate";
import ViewFormatA4 from "../../components/ViewFormatA4";
import currency from "../../libs/currency";
import Localbase from "localbase";

import useWindowSize from "../../hooks/useWindowSize";
import InputSearch from "../../components/InputSearch";

const Ventas = () => {
  const { windowSize: size } = useWindowSize();
  const location = useLocation();
  const { empresa, impresion } = useContext(EmpresaContext);
  const [ventas, setVentas] = useState([]);
  const [selected, setSelected] = useState(null);
  const [editionMode, setEditionMode] = useState(false);
  const [qrCodeBase64, setQrCodeBase64] = useState(false);
  const navigate = useNavigate();
  const [pdfBlob, setPdfBlob] = useState(null);
  const [isLoading, setIsLoading] = useState(false);

  const [activeFilterPendientePorPagar, setActiveFilterPendientePorPagar] =
    useState(false);
  const [searchInput, setSearchInput] = useState(false);

  const db = new Localbase("db");

  const [formatPrint, setFormatPrint] = useState({
    status: false,
    format: "A4",
  });

  const getBlob = async () => {
    const blob = pdf(
      <ViewFormatA4
        empresa={empresa}
        status={selected}
        qrCodeBase64={qrCodeBase64}
        impresion={impresion}
      />
    ).toBlob();

    let bl = await blob;

    const file = new File(
      [bl],
      `${empresa.empresa.ruc}-${selected?.serie?.sunat_codigo}-${selected?.serie?.serie}-${selected?.correlativo}.pdf`,
      {
        type: "application/pdf",
        lastModified: Date.now(),
      }
    );
    setPdfBlob(file);
  };

  const viewPrintFormat = (status, type) => {
    setFormatPrint({ status, format: type });
  };

  const get = async (page, reload = false) => {
    const empresa_id = empresa.empresa.id;
    const local_id = empresa.id;
    const fetch = await ventaService.show(empresa_id, local_id, page);
    setVentas(fetch);
  };

  const handleChangeVenta = (ventaModificada) => {
    const change = ventas.map((venta) => {
      if (parseInt(venta.id) === parseInt(ventaModificada.id)) {
        return ventaModificada;
      } else {
        return venta;
      }
    });
    setVentas(change);
  };

  const handleSelectDocument = async (venta) => {
    await setSelected(null);
    await setSelected(venta);
  };

  const download = (type) => {
    ventaService.download(selected, type);
  };

  const sendToSunat = async (id, index) => {
    const ventaChange = ventas.find((v, k) => v.id === id);
    ventaChange.send_status = true;

    const newVentas = ventas.map((venta, index) => {
      if (venta.id === ventaChange.id) {
        console.log("respuesta del envio a sunat", venta);
        return ventaChange;
      } else {
        return venta;
      }
    });

    setVentas(newVentas);

    /**PROMESA */
    const response = await ventaService.sendSunat(ventaChange);

    const ventaWithResponse = { ...ventaChange, ...response };

    /**END PROMESA */
    ventaWithResponse.send_status = false;
    const HandleVentas = ventas.map((venta, index) => {
      if (venta.id === ventaWithResponse.id) {
        return ventaWithResponse;
      } else {
        return venta;
      }
    });
    setVentas(HandleVentas);
  };

  const handleFiltrarPendiente = async (active) => {
    db.collection("ventas")
      .get()
      .then((ventas) => {
        if (!!active) {
          const result = ventas.filter((list) => {
            if (
              list.tipo_operacion_id === 1 &&
              list.forma_pago_id === 2 &&
              list.total_documento_fin - list.pago_status > 0.001
            ) {
              return list;
            }
          });
          setActiveFilterPendientePorPagar(!active);
          setVentas(result);
        } else {
          setActiveFilterPendientePorPagar(!active);
          setVentas(ventas);
        }
      });
  };

  useEffect(() => {
    db.collection("ventas")
      .get()
      .then((ventas) => {
        const items = ventas.sort(function (a, b) {
          return b.id - a.id;
        });
        console.log(location);
        if (location?.state?.porcobrar) {
          setIsLoading(true);
          handleFiltrarPendiente(true);
        } else {
          setVentas(items);
          setIsLoading(true);
          get(1);
        }
      });

    return () => {
      setVentas([]);
    };
  }, [location]);

  useEffect(() => {
    (async () => {
      const ventas = await db.collection("ventas").get();
      if (!!searchInput) {
        const result = ventas.filter((list) => {
          if (
            list.cliente.nombre.indexOf(searchInput) >= 0 ||
            list.cliente.nombre_comercial.indexOf(searchInput) >= 0 ||
            list.cliente.direccion.indexOf(searchInput) >= 0 ||
            list.uniqueserie.indexOf(searchInput) >= 0 ||
            list.serie.descripcion.indexOf(searchInput) >= 0
          ) {
            return list;
          }
        });
        setVentas(result);
      } else {
        setVentas(ventas);
      }
    })();
  }, [searchInput]);

  return (
    <>
      {size.width > 700 ? (
        <Grid.Column width={5}>
          <List relaxed="very" style={{ marginTop: "1rem" }}>
            <List.Item>
              <List.Content floated="right">
                <Button
                  size="mini"
                  onClick={() =>
                    handleFiltrarPendiente(activeFilterPendientePorPagar)
                  }
                  active={!activeFilterPendientePorPagar}
                >
                  Por Cobrar
                </Button>

                <CreateButton empresaContext={empresa} />
              </List.Content>
              <List.Content>
                <List.Header as="a">
                  <Link
                    to={`/welcome/empresas/${empresa.empresa.id}`}
                    state={{
                      backgroundLocation: location,
                      model: empresa.empresa,
                    }}
                  >
                    {empresa.empresa.nombre}
                  </Link>
                </List.Header>
                {empresa.nombre}
              </List.Content>
            </List.Item>
          </List>
          {isLoading ? (
            <List
              celled
              relaxed="very"
              style={{ overflowY: "scroll", height: "88vh" }}
            >
              {ventas?.map((venta, index) => (
                <List.Item
                  key={index}
                  onClick={() => {
                    handleSelectDocument(venta);
                  }}
                  style={{
                    backgroundColor:
                      venta?.id === selected?.id
                        ? "rgb(0 181 173 / 12%)"
                        : "#fff",
                  }}
                >
                  <List.Content style={{ width: "200px" }} floated="right">
                    <Label.Group
                      tag
                      color={
                        venta.tipo_operacion_id === 1
                          ? venta.forma_pago_id === 1
                            ? ""
                            : venta.pago_status < venta.total_documento_fin
                            ? "orange"
                            : venta.pago_status === venta.total_documento_fin
                            ? ""
                            : "blue"
                          : ""
                      }
                    >
                      <Label as="a">
                        {venta?.moneda?.simbolo}{" "}
                        {currency(venta.total_documento_fin)}
                        {" ("}
                        {venta.moneda.iso}
                        {") "}
                      </Label>
                    </Label.Group>

                    <Popup
                      content={"Click para enviar comprobante a Sunat"}
                      header={"Pendiente por enviar"}
                      trigger={
                        venta.tipo_operacion_id !== 2 &&
                        venta.serie.abbr !== "CT" &&
                        venta.serie.abbr !== "NV" ? (
                          <Button
                            icon={
                              venta.sunat_code === "0"
                                ? "check"
                                : venta.sunat_code >= "2000"
                                ? "times"
                                : "cloud upload"
                            }
                            size="mini"
                            onClick={(e) => {
                              e.stopPropagation();
                              sendToSunat(venta.id, index);
                            }}
                            color={
                              venta.sunat_code === "0"
                                ? "green"
                                : venta.sunat_code >= "2000"
                                ? "red"
                                : "primary"
                            }
                            loading={venta.send_status}
                            disabled={
                              venta.send_status || venta.sunat_code === "0"
                            }
                          />
                        ) : (
                          false
                        )
                      }
                    />

                    {!!venta.vendedor && (
                      <Popup
                        content={venta?.vendedor?.nombre}
                        header={"Vendedor"}
                        trigger={
                          <Button
                            icon={"user"}
                            size="mini"
                            onClick={(e) => {
                              e.stopPropagation();
                            }}
                            content={venta?.vendedor?.nombre.split(" ")[0]}
                            color={" "}
                          />
                        }
                      />
                    )}

                    {Number(venta?.is_nulled) ? (
                      <Label as="span" content="Anulado" color="red" />
                    ) : (
                      <> &nbsp;</>
                    )}
                  </List.Content>

                  <List.Content>
                    <List.Header as="a">
                      {venta?.serie?.abbr} {venta?.serie?.serie}{" "}
                      {venta?.correlativo}{" "}
                      {updated(venta.updated_at) && (
                        <LabelUpdate index={index} />
                      )}
                    </List.Header>
                    <List.Item>
                      <strong>{venta?.cliente?.nombre}</strong>
                    </List.Item>
                    <List.Item>
                      Fecha Emision:{" "}
                      <Moment format="DD/MM/YYYY">{venta.fecha_emision}</Moment>
                    </List.Item>
                  </List.Content>
                  {/* <List.Content>
            {JSON.stringify(venta)}
          </List.Content> */}
                </List.Item>
              ))}
            </List>
          ) : (
            <Loader active inline="centered" />
          )}
        </Grid.Column>
      ) : (
        <>
          {/**
           * LISTADO DE VENTAS PARA MOVILES
           */}

          {!!!selected && (
            <Grid.Column width={16}>
              <Grid.Row>
                <List relaxed="very" style={{ marginTop: "1rem" }}>
                  <List.Item>
                    <List.Content floated="right">
                      <InputSearch
                        searchInput={searchInput}
                        setSearchInput={setSearchInput}
                        placeholder="Buscar Documento"
                      />
                    </List.Content>
                    <List.Content
                      floated="left"
                      style={{ paddingRight: "2px", paddingLeft: "2px" }}
                    >
                      <CreateButton empresaContext={empresa} />
                      <Button
                        size="mini"
                        onClick={() =>
                          handleFiltrarPendiente(activeFilterPendientePorPagar)
                        }
                        active={!activeFilterPendientePorPagar}
                      >
                        Por Cobrar
                      </Button>
                    </List.Content>
                  </List.Item>
                </List>
              </Grid.Row>
              <Grid.Row
                style={{
                  overflowY: "scroll",
                  height: "83vh",
                  marginTop: "1rem",
                }}
              >
                {isLoading ? (
                  <List celled relaxed="very">
                    {ventas?.map((venta, index) => (
                      <List.Item
                        key={index}
                        onClick={() => {
                          handleSelectDocument(venta);
                        }}
                        style={{
                          backgroundColor:
                            venta?.id === selected?.id
                              ? "rgb(0 181 173 / 12%)"
                              : "#fff",
                        }}
                      >
                        <List.Content
                          style={{ width: "200px" }}
                          floated="right"
                        >
                          <Label.Group
                            tag
                            color={
                              venta.tipo_operacion_id === 1
                                ? venta.forma_pago_id === 1
                                  ? ""
                                  : venta.pago_status <
                                    venta.total_documento_fin
                                  ? "orange"
                                  : venta.pago_status ===
                                    venta.total_documento_fin
                                  ? ""
                                  : "blue"
                                : ""
                            }
                          >
                            <Label as="a">
                              {venta?.moneda?.simbolo}{" "}
                              {currency(venta.total_documento_fin)}
                              {" ("}
                              {venta.moneda.iso}
                              {") "}
                            </Label>
                          </Label.Group>

                          <Popup
                            content={"Click para enviar comprobante a Sunat"}
                            header={"Pendiente por enviar"}
                            trigger={
                              venta.tipo_operacion_id !== 2 &&
                              venta.serie.abbr !== "CT" &&
                              venta.serie.abbr !== "NV" ? (
                                <Button
                                  icon={
                                    venta.sunat_code === "0"
                                      ? "check"
                                      : venta.sunat_code >= "2000"
                                      ? "times"
                                      : "cloud upload"
                                  }
                                  size="mini"
                                  onClick={(e) => {
                                    e.stopPropagation();
                                    sendToSunat(venta.id, index);
                                  }}
                                  color={
                                    venta.sunat_code === "0"
                                      ? "green"
                                      : venta.sunat_code >= "2000"
                                      ? "red"
                                      : "primary"
                                  }
                                  loading={venta.send_status}
                                  disabled={
                                    venta.send_status ||
                                    venta.sunat_code === "0"
                                  }
                                />
                              ) : (
                                false
                              )
                            }
                          />
                          {!!venta.vendedor && (
                            <Popup
                              content={venta?.vendedor?.nombre}
                              header={"Vendedor"}
                              trigger={
                                <Button
                                  icon={"user"}
                                  size="mini"
                                  onClick={(e) => {
                                    e.stopPropagation();
                                  }}
                                  content={
                                    venta?.vendedor?.nombre.split(" ")[0]
                                  }
                                  color={" "}
                                />
                              }
                            />
                          )}

                          {!!Number(venta?.is_nulled) ? (
                            <Label as="span" content="Anulado" color="red" />
                          ) : (
                            <> &nbsp;</>
                          )}
                        </List.Content>

                        <List.Content>
                          <List.Header as="a">
                            {venta?.serie?.abbr} {venta?.serie?.serie}{" "}
                            {venta?.correlativo}{" "}
                            {updated(venta.updated_at) && (
                              <LabelUpdate index={index} />
                            )}
                          </List.Header>
                          <List.Item>
                            <strong>{venta?.cliente?.nombre}</strong>
                          </List.Item>
                          <List.Item>
                            Fecha Emision:{" "}
                            <Moment format="DD/MM/YYYY">
                              {venta.fecha_emision}
                            </Moment>
                          </List.Item>
                        </List.Content>
                      </List.Item>
                    ))}
                  </List>
                ) : (
                  <Loader active inline="centered" />
                )}
              </Grid.Row>
            </Grid.Column>
          )}
          {/**
           * FOMULARIO DE EDICION PARA MOVILES
           */}
          {!!selected && (
            <Grid.Column width={16}>
              <List relaxed="very" style={{ marginTop: "1rem" }}>
                <List.Item>
                  <span className="float left">
                    <Button
                      size="mini"
                      icon="arrow circle left"
                      content="Regresar"
                      style={{ marginRight: "2px", marginLeft: "2px" }}
                      onClick={async () => {
                        await setSelected(undefined);
                      }}
                    />
                  </span>

                  <List.Content floated="right">
                    {!!!Number(selected.is_nulled) || !!!selected ? (
                      <ConverterButton
                        handleChangeVenta={handleChangeVenta}
                        documento={selected}
                        style={{ marginRight: "2px", marginLeft: "2px" }}
                      />
                    ) : (
                      <></>
                    )}

                    {selected && (
                      <Dropdown
                        icon="download"
                        floating
                        button
                        className="button ui mini icon"
                      >
                        <Dropdown.Menu>
                          {selected.sunat_documento && (
                            <Dropdown.Item
                              onClick={() => {
                                download("xml");
                              }}
                            >
                              Descargar XML
                            </Dropdown.Item>
                          )}

                          {selected.sunat_notes && (
                            <Dropdown.Item
                              onClick={() => {
                                download("cdn");
                              }}
                            >
                              Descargar CDN
                            </Dropdown.Item>
                          )}
                        </Dropdown.Menu>
                      </Dropdown>
                    )}

                    <Button
                      size="mini"
                      icon="print"
                      style={{ marginRight: "2px", marginLeft: "2px" }}
                      onClick={() => {
                        viewPrintFormat(!formatPrint.status, "A4");
                      }}
                      active={formatPrint.status}
                    />

                    {!!!Number(selected.is_nulled) || !!!selected ? (
                      <>
                        <Link
                          to={`/welcome/email/create`}
                          state={{
                            backgroundLocation: location,
                            model: { ...selected, ...{ file: pdfBlob } },
                          }}
                          className={"button ui mini"}
                        >
                          <Icon name="mail"></Icon>
                        </Link>

                        <Button
                          size="mini"
                          icon="pencil"
                          color={editionMode ? "orange" : ""}
                          style={{ marginRight: "2px", marginLeft: "2px" }}
                          onClick={() => {
                            setEditionMode(!editionMode);
                          }}
                          active={formatPrint.status}
                        />
                      </>
                    ) : (
                      <></>
                    )}
                  </List.Content>
                </List.Item>
              </List>

              <FormularioVenta
                selected={selected}
                handleChangeVenta={handleChangeVenta}
                editionMode={editionMode}
                setEditionMode={setEditionMode}
                formatPrint={formatPrint}
                setFormatPrint={setFormatPrint}
                response={true}
              />
            </Grid.Column>
          )}
        </>
      )}

      {size.width > 700 && (
        <Grid.Column width={11}>
          {!selected ? (
            <Segment placeholder style={{ marginTop: "6vh" }}>
              <Header icon>
                <Icon name="file outline" />
                No tienes un documento seleccionado
              </Header>
            </Segment>
          ) : (
            <>
              <List relaxed="very" style={{ marginTop: "1rem" }}>
                <List.Item>
                  <List.Content floated="right">
                    {selected && (
                      <Dropdown
                        text="Descargar"
                        icon="download"
                        floating
                        labeled
                        button
                        className="button ui mini icon"
                      >
                        <Dropdown.Menu>
                          {selected.sunat_documento && (
                            <Dropdown.Item
                              onClick={() => {
                                download("xml");
                              }}
                            >
                              Descargar XML
                            </Dropdown.Item>
                          )}

                          {/* <Dropdown.Item>
                        <PDFDownloadLink
                          style={{ color: "#000000DE" }}
                          document={
                            <ViewFormatA4
                              empresa={empresa}
                              status={selected}
                              qrCodeBase64={qrCodeBase64}
                            />
                          }
                          fileName={`${selected.sunat_documento}.pdf`}
                        >
                          {({ blob, url, loading, error }) =>
                            loading
                              ? "Cargando documento..."
                              : "Descargar PDF"
                          }
                        </PDFDownloadLink>
                      </Dropdown.Item> */}
                          {selected.sunat_notes && (
                            <Dropdown.Item
                              onClick={() => {
                                download("cdn");
                              }}
                            >
                              Descargar CDN
                            </Dropdown.Item>
                          )}
                        </Dropdown.Menu>
                      </Dropdown>
                    )}

                    <Button
                      size="mini"
                      content="Imprimir"
                      icon="print"
                      labelPosition="left"
                      style={{ marginRight: "2px", marginLeft: "2px" }}
                      onClick={() => {
                        viewPrintFormat(!formatPrint.status, "A4");
                      }}
                      active={formatPrint.status}
                    />

                    {!!!Number(selected.is_nulled) || !!!selected ? (
                      <>
                        <Button
                          size="mini"
                          content="Editar"
                          icon="pencil"
                          labelPosition="left"
                          style={{ marginRight: "2px", marginLeft: "2px" }}
                          active={editionMode}
                          onClick={() => {
                            setEditionMode(!editionMode);
                          }}
                        />
                        <Link
                          to={`/welcome/email/create`}
                          state={{
                            backgroundLocation: location,
                            model: { ...selected, ...{ file: pdfBlob } },
                          }}
                          className={"button ui mini"}
                        >
                          Enviar
                        </Link>
                        <ConverterButton
                          handleChangeVenta={handleChangeVenta}
                          documento={selected}
                          style={{ marginRight: "2px", marginLeft: "2px" }}
                        />
                      </>
                    ) : (
                      <></>
                    )}
                  </List.Content>
                </List.Item>
              </List>
              <Button
                circular
                size="mini"
                icon
                onClick={() => {
                  setSelected(null);
                }}
              >
                <Icon name="times" />
              </Button>
              <Link
                to={`/space/ventas/1`}
                state={{
                  model: selected,
                }}
              >
                <Button circular size="mini" icon>
                  <Icon name="expand" />
                </Button>
              </Link>
              {Number(selected.is_nulled) ? (
                <Label as="span" content="Anulado" icon="ban" color="red" />
              ) : (
                <></>
              )}

              <FormularioVenta
                selected={selected}
                handleChangeVenta={handleChangeVenta}
                editionMode={editionMode}
                formatPrint={formatPrint}
                setFormatPrint={setFormatPrint}
                response={false}
              />
            </>
          )}
        </Grid.Column>
      )}

      {/* formulario de edicion en tlfno */}
    </>
  );
};

export default Ventas;
