import React from "react";
import {
  Grid,
} from "semantic-ui-react";

import Navbar from "../../components/Navbar";
import { Outlet } from "react-router-dom";

const Layout = () => {
  return (
    <>
      <Navbar />
      <Grid>
        <Grid.Row style={{ height: "80rem" }}>
          <Grid.Column width={16}>
            <Outlet />
          </Grid.Column>
        </Grid.Row>
      </Grid>
    </>
  );
};

export default Layout;
