import React, { useContext } from "react";
import { Link, useNavigate, Navigate, useLocation } from "react-router-dom";
import { Menu, Dropdown, Button, Icon, Image } from "semantic-ui-react";

import UserContext from "../context/UserContext";
import EmpresaContext from "../context/EmpresaContext";
import useUser from "../hooks/useUser";
import { toast } from "react-toastify";
import { useState } from "react";
import { useEffect } from "react";
import ventaContext from "../context/VentaContext";
import useWindowSize from "../hooks/useWindowSize";

const Navbar = () => {
  const location = useLocation();
  const { me } = useContext(UserContext);
  const { empresa, impresion, setImpresion } = useContext(EmpresaContext);
  const { handleDefaultVenta } = useContext(ventaContext);
  const { isLogged, logout } = useUser();
  const [showImpresionSettingsButton, setShowImpresionSettingsButton] =
    useState(false);

  const navigate = useNavigate();

  const LoginOut = () => {
    logout();
    navigate({ pathname: "/ingresar" });
  };

  const settPrintAfterSave = (boo) => {
    localStorage.setItem(
      "print_setting",
      JSON.stringify({
        ...impresion,
        ...{ after_save: boo },
      })
    );
    setImpresion({
      ...impresion,
      ...{ after_save: boo },
    });
  };

  const settPrintSize = (size) => {
    localStorage.setItem(
      "print_setting",
      JSON.stringify({
        ...impresion,
        ...{ size },
      })
    );
    setImpresion({
      ...impresion,
      ...{ size },
    });
  };

  const { windowSize: size } = useWindowSize();

  useEffect(() => {
    if (
      !!!location.pathname.search("/space/ventas/") ||
      !!!location.pathname.search("/workspace/ventas")
    ) {
      setShowImpresionSettingsButton(true);
    } else {
      setShowImpresionSettingsButton(false);
    }
  }, [location]);

  return (
    <>
      {!isLogged && <Navigate to="/ingresar" />}
      {size.width > 700 && (
        <Menu fixed="top" inverted color="blue">
          <Link to="/workspace/app">
            <Menu.Item as="a">Inicio</Menu.Item>
          </Link>
          <Link to="/welcome/empresas">
            <Menu.Item as="a">Empresas</Menu.Item>
          </Link>
          {
            !!empresa && <>
              <Link to="/workspace/ventas">
                <Menu.Item as="a">Ventas</Menu.Item>
              </Link>
              <Link to="/workspace/contactos">
                <Menu.Item as="a">Contactos</Menu.Item>
              </Link>
              <Link to="/workspace/productos">
                <Menu.Item as="a">Productos</Menu.Item>
              </Link>
              {/* <Link to="/workspace/app">
          <Menu.Item as="a">Espacio de Trabajo</Menu.Item>
        </Link> */}
              <Link to="/workspace/util">
                <Menu.Item as="a">Utilidades</Menu.Item>
              </Link>
              <Link to="/workspace/reportes">
                <Menu.Item as="a">Reportes</Menu.Item>
              </Link>{" "}
            </>
          }

          <Menu.Menu position="right">
            {showImpresionSettingsButton && (
              <Menu.Item>
                <Dropdown
                  as={"a"}
                  text="Ajustes de Documento"
                  style={{ color: "white", fontSize: "13px" }}
                >
                  <Dropdown.Menu>
                    <Dropdown.Header
                      icon="print"
                      content="Configuracin de Impresin"
                    />
                    <Dropdown.Item
                      active={impresion?.after_save}
                      onClick={() => {
                        settPrintAfterSave(!impresion?.after_save);
                      }}
                    >
                      Mostrar impresion despues de generar el documento
                    </Dropdown.Item>
                    <Dropdown.Item
                      active={impresion?.size === "A4"}
                      onClick={() => {
                        settPrintSize("A4");
                      }}
                    >
                      Impresin A4
                    </Dropdown.Item>
                    <Dropdown.Item
                      active={impresion?.size === "88mm"}
                      onClick={() => {
                        settPrintSize("88mm");
                      }}
                    >
                      Impresin Ticket 88 mm
                    </Dropdown.Item>
                    <Dropdown.Item
                      disabled
                      active={impresion?.size === "70mm"}
                      onClick={() => {
                        settPrintSize("70mm");
                      }}
                    >
                      Impresin Ticket 80 mm
                    </Dropdown.Item>
                    <Dropdown.Item
                      disabled
                      active={impresion?.size === "80mm"}
                      onClick={() => {
                        settPrintSize("80mm");
                      }}
                    >
                      Impresin Ticket 70 mm
                    </Dropdown.Item>
                    <Dropdown.Divider></Dropdown.Divider>
                    <Dropdown.Item
                      onClick={() => {
                        handleDefaultVenta();
                      }}
                    >
                      Ajustes Predeterminados
                    </Dropdown.Item>
                  </Dropdown.Menu>
                </Dropdown>
              </Menu.Item>
            )}

            {!isLogged ? (
              <Link to="/ingresar">
                <Menu.Item as="a" name="Signup">
                  Sign Up
                </Menu.Item>
              </Link>
            ) : (
              <>
                <Menu.Item as="a" name="profile">
                  {me.name}
                </Menu.Item>
                <Menu.Item as="a" name="Logout" onClick={LoginOut}>
                  Cerrar Sesin
                </Menu.Item>
              </>
            )}
          </Menu.Menu>
        </Menu>
      )}

      {size.width < 699 && (
        <Menu fixed="top" secondary>
          <Dropdown item icon="sidebar">
            <Dropdown.Menu>
              <Link to="/workspace/app" className="item">
                Dashboard
              </Link>
              <Link to="/welcome/empresas" className="item">
                Empresa
              </Link>
              <Dropdown.Divider />
              <Link to="/workspace/ventas" className="item">
                Ventas
              </Link>
              <Link to="/workspace/contactos" className="item">
                Contactos
              </Link>
              <Link to="/workspace/productos" className="item">
                Productos
              </Link>
              <Dropdown.Divider />
              <Link to="/workspace/util" className="item">
                Utilidades
              </Link>
              <Link to="/workspace/reportes" className="item">
                Reportes
              </Link>
              <Dropdown.Divider />
              <Link to="/loginout" className="item">
                Cerrar Sesin
              </Link>
            </Dropdown.Menu>
          </Dropdown>

          <Menu.Menu position="right">
            <Image
              style={{ marginTop: 5, marginRight: 5 }}
              srcSet={"/logo32.png"}
              alt={"logo fivip"}
              as="div"
            />
          </Menu.Menu>
        </Menu>
      )}
    </>
  );
};

export default Navbar;
