import React, { useContext } from "react";
import { useEffect } from "react";
import { useState } from "react";
import { Link } from "react-router-dom";
import { Button, Dropdown, Confirm } from "semantic-ui-react";

import empresaContext from "../context/EmpresaContext";
import ventasService from "../services/ventas";

const ConverterButton = ({ documento, handleChangeVenta }) => {
  const { empresa } = useContext(empresaContext);
  const [open, setOpen] = useState(false);

  return (
    <>
      {documento && (
        <Dropdown as={"a"} text="Convertir" className="button ui mini teal">
          <Dropdown.Menu>
            {
              <Dropdown.Item
                onClick={() => {
                  setOpen(!open);
                }}
              >
                <i className="delete icon"></i>
                Anular
              </Dropdown.Item>
            }

            {documento.tipo_operacion_id !== 1 && (
              <Link
                className="item"
                to="/space/ventas/create/1"
                state={{
                  model: {
                    ...documento,
                    documento_relacion: documento.documento_relacion.concat({
                      documento_relacion_id: documento?.id,
                      documento_relacion: documento,
                    }),
                    serie: undefined,
                    correlativo: undefined,
                    id: undefined,
                    empresa_id: empresa.empresa.id,
                    local_id: empresa.id,
                    tipo_operacion_id: 1,
                    fecha_emision: (new Date()).toLocaleDateString('es-CL').split('-').reverse().join('-'),
                    fecha_registro: (new Date()).toLocaleDateString('es-CL').split('-').reverse().join('-'),
                    transportista_id: null,
                    ubigeo_partida: null,
                    ubigeo_destino: null,
                  },
                }}
              >
                <i className="file alternate outline icon" />
                Venta
              </Link>
            )}

            {documento.tipo_operacion_id !== 2 &&
              documento.tipo_operacion_id !== 3 && (
                <Link
                  className="item"
                  to="/space/ventas/create/3"
                  state={{
                    model: {
                      ...documento,
                      documento_relacion: documento.documento_relacion.concat({
                        documento_relacion_id: documento.id,
                        documento_relacion: documento,
                      }),
                      serie: undefined,
                      correlativo: undefined,
                      id: undefined,
                      empresa_id: empresa.empresa.id,
                      local_id: empresa.id,
                      tipo_operacion_id: 3,
                      fecha_emision: (new Date()).toLocaleDateString('es-CL').split('-').reverse().join('-'),
                      fecha_registro: (new Date()).toLocaleDateString('es-CL').split('-').reverse().join('-'),
                      transportista_id: null,
                      ubigeo_partida: null,
                      ubigeo_destino: null,
                    },
                  }}
                >
                  <i className="file alternate outline icon" />
                  Guia de Remisin
                </Link>
              )}
            <Link
              className="item"
              to="/space/ventas/create/4"
              state={{
                model: {
                  ...documento,
                  documento_relacion: documento.documento_relacion.concat({
                    documento_relacion_id: documento.id,
                    documento_relacion: documento,
                  }),
                  serie: undefined,
                  correlativo: undefined,
                  id: undefined,
                  empresa_id: empresa.empresa.id,
                  local_id: empresa.id,
                  tipo_operacion_id: 4,
                  fecha_emision: (new Date()).toLocaleDateString('es-CL').split('-').reverse().join('-'),
                  fecha_registro: (new Date()).toLocaleDateString('es-CL').split('-').reverse().join('-'),
                  transportista_id: null,
                  ubigeo_partida: null,
                  ubigeo_destino: null,
                },
              }}
            >
              <i className="file alternate outline icon" />
              Nota de Crdito
            </Link>
          </Dropdown.Menu>
        </Dropdown>
      )}

      <Confirm
        open={open}
        content={`Seguro que deseas anular este documento? -Esta operacion es irreversible recuerde que puede editar este documento`}
        onCancel={() => {
          setOpen(!open);
        }}
        onConfirm={async () => {
          const anulacion = await ventasService.anular(documento);
          handleChangeVenta(anulacion);
          console.log(anulacion);
          setOpen(!open);
        }}
      />
    </>
  );
};

export default ConverterButton;
