import React from "react";
import { useNavigate } from "react-router-dom";
import { Modal } from "semantic-ui-react";

function ModalRouter({ setOpen, open, size, dimmer, setTitle, children }) {
  let navigate = useNavigate();
  const close = () => {
    navigate(-1);
  };
  return (
    <Modal
      closeIcon
      centered={true}
      dimmer="blurring"
      open={true}
      onClose={close}
      size={size}
    >
      <Modal.Header>{setTitle}</Modal.Header>
      <Modal.Content scrolling style={{ minHeight: "400px" }}>
        {children}
      </Modal.Content>
    </Modal>
  );
}

export default ModalRouter;
