import * as yup from "yup";

const schema = yup
  .object({
    tipo_documento_identy_id: yup.string().required("Complete"),
    numero_documento: yup.string().required("Complete"),
    nombre: yup.string().required("Complete"),
    nombre_comercial: yup
      .string()
      .required("Complete")
      .min(2, "Minimo 2 letras")
      .max(50, "Max 50 Letras"),
    direccion: yup.string().required("Complete"),
    ubigeo_id: yup.string().required("Complete"),
    empresa_id: yup.string().required("Complete")
  })
  .required();

export default schema;
