import React, { useEffect } from "react";

import {
  Routes,
  Route,
  Link,
  useLocation,
  useNavigate,
} from "react-router-dom";

import Home from "./pages/Home";
import LayoutDashboard from "./pages/Dashboard/_Layout";
import LayoutWelcome from "./pages/Welcome/Layout";
import Login from "./pages/Login.jsx";
import Welcome from "./pages/Welcome/Welcome.jsx";
import ModalRouter from "./components/ModalRouter.jsx";

import Dashboard from "./pages/Dashboard/Dashboard";
import Ventas from "./pages/Dashboard/Ventas";

/**
 * Modeles
 */

import FormularioEmpresa from "./components/empresa/formulario";
import FormularioLocal from "./components/local/formulario";
import FormularioCliente from "./components/cliente/formulario";
import FormularioMoneda from "./components/moneda/formulario";
import FormularioVehiculo from "./components/vehiculo/formulario";
import FormularioVenta from "./components/ventas/formulario";
import FormularioProducto from "./components/producto/formulario";

import LayoutFullScreen from "./pages/Dashboard/_LayoutFullScreen";
import Contactos from "./pages/Dashboard/Contactos";
import Productos from "./pages/Dashboard/Productos";
import FormularioPrecio from "./components/precio/formulario";
import { useState } from "react";
import { VentaContextProvider } from "./context/VentaContext";
import FormularioEmail from "./components/email/formulario";
import Reports from "./pages/Dashboard/Reports";
import FormularioReportVenta from "./components/ventas/report";
import Compras from "./pages/Dashboard/Compras";
import FormularioCompra from "./components/compras/formulario";
import TestMobil from "./pages/Dashboard/TestMobil";
import useUser from "./hooks/useUser";
import FormularioBanco from "./components/banco/formulario";
import Utilidades from "./pages/Dashboard/Utilidades";

import SignIn from "./pages/SignIn";
import SignUp from "./pages/SignUp";
import Comprobante from "./pages/Comprobante";

/**
 * End Modeles
 */

const Routers = () => {
  const [formatPrint, setFormatPrint] = useState({ status: false });
  let location = useLocation();
  let state = location.state || { backgroundLocation: location };
  return (
    <>
      <Routes
        location={
          state?.backgroundLocation ? state?.backgroundLocation : location
        }
      >
        <Route path="/" element={<Home />} />
        <Route path="/signup2" element={<Login />} />

        <Route path="/registro" element={<SignUp />} />
        <Route path="/ingresar" element={<SignIn />} />
        <Route path="/comprobante" element={<Comprobante />} />

        <Route path="/loginout" element={<LoginOut />} />
        <Route path="test-mobil" element={<TestMobil />} />
        <Route path="welcome" element={<LayoutWelcome />}>
          <Route path="empresas" element={<Welcome />} />
        </Route>
        <Route path="/workspace" element={<LayoutDashboard />}>
          <Route path="app" element={<Dashboard />} />
          <Route path="ventas" element={<Ventas />} />
          <Route path="compras" element={<Compras />} />

          <Route
            path="ventas/:id"
            fullwidth={true}
            element={
              <FormularioVenta state={state?.model} editionMode={true} />
            }
          />

          <Route path="contactos" element={<Contactos />} />
          <Route path="productos" element={<Productos />} />
          <Route path="reportes" element={<Reports />} />
          <Route path="util" element={<Utilidades />} />
          <Route path="profile" element={<Profile />} />
        </Route>

        <Route path="/space" element={<LayoutFullScreen />}>
          <Route
            path="productos/create/:id"
            element={
              <VentaContextProvider>
                <FormularioVenta state={state?.model} editionMode={true} />
              </VentaContextProvider>
            }
          />
          <Route
            path="productos/:id"
            element={
              <VentaContextProvider>
                <FormularioVenta state={state?.model} editionMode={true} />
              </VentaContextProvider>
            }
          />

          <Route
            path="compras/create/:id"
            element={
              <FormularioCompra
                formatPrint={formatPrint}
                setFormatPrint={setFormatPrint}
                state={state?.model}
                editionMode={true}
              />
            }
          />
          <Route
            path="compras/:id"
            element={
              <FormularioCompra
                formatPrint={formatPrint}
                setFormatPrint={setFormatPrint}
                state={state?.model}
                editionMode={true}
              />
            }
          />

          <Route
            path="ventas/create/:id"
            element={
              <FormularioVenta
                formatPrint={formatPrint}
                setFormatPrint={setFormatPrint}
                state={state?.model}
                editionMode={true}
              />
            }
          />
          <Route
            path="ventas/:id"
            element={
              <FormularioVenta
                formatPrint={formatPrint}
                setFormatPrint={setFormatPrint}
                state={state?.model}
                editionMode={true}
              />
            }
          />
        </Route>

        <Route path="*" element={<NoMatch />} />
      </Routes>

      {state?.backgroundLocation && (
        <Routes>
          <Route
            path="reportes/ventas"
            element={
              <ModalRouter size={"large"} setTitle="Reporte de Venta">
                <FormularioReportVenta state={state?.model} />
              </ModalRouter>
            }
          />
          <Route
            path="reportes/productos"
            element={
              <ModalRouter size={"large"} setTitle="Reporte de Productos">
                <FormularioReportVenta state={state?.model} />
              </ModalRouter>
            }
          />
          <Route
            path="reportes/contactos"
            element={
              <ModalRouter size={"large"} setTitle="Reporte de Contactos">
                <FormularioReportVenta state={state?.model} />
              </ModalRouter>
            }
          />
          <Route
            path="welcome/email/create"
            element={
              <ModalRouter size={"large"} setTitle="Nuevo Correo">
                <FormularioEmail state={state?.model} />
              </ModalRouter>
            }
          />
          <Route
            path="welcome/empresas/create"
            element={
              <ModalRouter size={"large"} setTitle="Crea Nueva Empresa">
                <FormularioEmpresa />
              </ModalRouter>
            }
          />
          <Route
            path="welcome/empresas/:id"
            element={
              <ModalRouter size={"large"} setTitle="Editar Empresa">
                <FormularioEmpresa state={state?.model} />
              </ModalRouter>
            }
          />
          <Route
            path="welcome/locals/create"
            element={
              <ModalRouter size={"large"} setTitle="Crea Nuevo Local">
                <FormularioLocal state={state?.model} />
              </ModalRouter>
            }
          />
          <Route
            path="welcome/locals/:id"
            element={
              <ModalRouter size={"large"} setTitle="Editar Local">
                <FormularioLocal state={state?.model} />
              </ModalRouter>
            }
          />

          <Route
            path="welcome/clientes/create"
            element={
              <ModalRouter size={"large"} setTitle="Crea Nuevo Cliente">
                <FormularioCliente state={state?.model} />
              </ModalRouter>
            }
          />
          <Route
            path="welcome/clientes/:id"
            element={
              <ModalRouter size={"large"} setTitle="Editar Contacto">
                <FormularioCliente state={state?.model} />
              </ModalRouter>
            }
          />

          <Route
            path="welcome/moneda/create"
            element={
              <ModalRouter size={"small"} setTitle="Crea Nueva Moneda">
                <FormularioMoneda state={state?.model} />
              </ModalRouter>
            }
          />
          <Route
            path="welcome/moneda/:id"
            element={
              <ModalRouter size={"small"} setTitle="Editar Moneda">
                <FormularioMoneda state={state?.model} />
              </ModalRouter>
            }
          />

          <Route
            path="welcome/banco/create"
            element={
              <ModalRouter size={"small"} setTitle="Crea Nuevo Banco">
                <FormularioBanco state={state?.model} />
              </ModalRouter>
            }
          />
          <Route
            path="welcome/banco/:id"
            element={
              <ModalRouter size={"small"} setTitle="Editar Banco">
                <FormularioBanco state={state?.model} />
              </ModalRouter>
            }
          />

          <Route
            path="welcome/vehiculo/create"
            element={
              <ModalRouter size={"small"} setTitle="Crea Nuevo Vehiculo">
                <FormularioVehiculo state={state?.model} />
              </ModalRouter>
            }
          />
          <Route
            path="welcome/vehiculo/:id"
            element={
              <ModalRouter size={"small"} setTitle="Editar Vehiculo">
                <FormularioVehiculo state={state?.model} />
              </ModalRouter>
            }
          />

          <Route
            path="welcome/ventas/create"
            element={
              <ModalRouter size={"fullscreen"} setTitle="Title">
                <FormularioMoneda state={state?.model} />
              </ModalRouter>
            }
          />
          <Route
            path="welcome/ventas/:id"
            element={
              <ModalRouter size={"fullscreen"} setTitle="Title">
                {/* <FormularioVenta state={state?.model} editionMode={true} /> */}
              </ModalRouter>
            }
          />

          <Route
            path="welcome/productos/create"
            element={
              <ModalRouter
                size={"large"}
                setTitle="Crea Nuevo Producto  Servicio"
              >
                <FormularioProducto state={state?.model} />
              </ModalRouter>
            }
          />
          <Route
            path="welcome/productos/:id"
            element={
              <ModalRouter size={"large"} setTitle="Editar Producto  Servicio">
                <FormularioProducto state={state?.model} />
              </ModalRouter>
            }
          />

          <Route
            path="welcome/precios/create"
            element={
              <ModalRouter size={"small"} setTitle="Crea Nuevo Precio">
                <FormularioPrecio state={state?.model} />
              </ModalRouter>
            }
          />
          <Route
            path="welcome/precios/:id"
            element={
              <ModalRouter size={"small"} setTitle="Editar Precio">
                <FormularioPrecio state={state?.model} />
              </ModalRouter>
            }
          />
        </Routes>
      )}
    </>
  );
};

function Profile() {
  return (
    <div>
      <h2>Profile</h2>
    </div>
  );
}

function NoMatch() {
  return (
    <div>
      <h2>Nothing to see here!</h2>
      <p>
        <Link to="/">Go to the home page</Link>
      </p>
    </div>
  );
}

function LoginOut() {
  const navigate = useNavigate();
  const { logout } = useUser();

  useEffect(() => {
    logout();
    navigate({ pathname: "/ingresar" });
  });

  return true;
}

export default Routers;
