import React, { useEffect, useState, useContext } from "react";
import { useNavigate, useLocation } from "react-router-dom";
import { useForm } from "react-hook-form";
import { Form, Button, Message, Checkbox, Segment } from "semantic-ui-react";
import { yupResolver } from "@hookform/resolvers/yup";
import productoServicio from "../../services/productos";
import FormFielController from "../FormFieldController";
import schema from "./schema";
import handleBackendErrors from "../../libs/handleBackendErrors";
import EmpresaContext from "../../context/EmpresaContext";
import FormFielDropdownSarch from "../DropdownSearch2";
import settingDropdowns from "../../libs/settingDropdowns";

const FormularioProducto = ({ state }) => {
  let navigate = useNavigate();
  let location = useLocation();
  const { empresa, setEmpresa } = useContext(EmpresaContext);
  const [isLoadding, setIsloadding] = useState(false);
  const [isBackendError, setIsBackendError] = useState(false);
  const {
    control,
    handleSubmit,
    watch,
    setValue,
    formState: { errors },
  } = useForm({ resolver: yupResolver(schema), defaultValues: state });

  const [data, setData] = useState();
  const watcher = watch();

  useEffect(
    () => {
      (async function () {
        if (!!state && state.id) {
          let data = await productoServicio.find(state.id);
          return setData(data);
        } else {
          return false;
        }
      })();
    },
    { data }
  );

  const close = (producto) => {
    navigate(location.state.backgroundLocation.pathname, {
      state: { reload: { producto_id: producto } },
    });
  };

  const onSubmit = async (data) => {
    setIsloadding(true);
    if (!!data && data.id) {
      const update = await productoServicio.update(data, data.id);
      if (update?.errors) {
        setIsBackendError(handleBackendErrors(update));
      } else {
        // db.collection("contactos").add(data);
        // console.log("debugger");
        setIsBackendError(null);
        close(update);
      }
    } else {
      productoServicio.create(data).then(
        (create) => {
          setIsloadding(false);
          if (create?.errors) {
            setIsBackendError(handleBackendErrors(create));
          } else {
            setIsBackendError(null);
            close(create);
            return create;
          }
        },
        (errors) => {
          close(data);
        }
      );
    }
  };
  return (
    <Form onSubmit={handleSubmit(onSubmit)} noValidate>
      {/* <Segment>{JSON.stringify(watcher)}</Segment> */}
      <FormFielDropdownSarch
        model={settingDropdowns.tipo_producto}
        empresaContexto={empresa}
        control={control}
        venta={watcher}
        setValue={setValue}
        width={16}
      />

      <Form.Group>
        <FormFielController
          name={"nombre"}
          title={"Descripcin"}
          control={control}
          errors={errors}
          width={8}
        />
        <FormFielController
          name={"codigo"}
          title={"Cdigo"}
          control={control}
          errors={errors}
          width={4}
        />
        <FormFielController
          name={"codigo_barras"}
          title={"Cdigo de Barras"}
          control={control}
          errors={errors}
          width={4}
        />
      </Form.Group>
      <Form.Group>
        <FormFielDropdownSarch
          model={settingDropdowns.presentacion}
          empresaContexto={empresa}
          control={control}
          venta={watcher}
          setValue={setValue}
          width={8}
        />
        <FormFielController
          name={"peso"}
          title={"Peso KG"}
          control={control}
          errors={errors}
          width={4}
          type={"number"}
        />
      </Form.Group>
      <Form.Group>
        <FormFielController
          name={"precio_compra"}
          title={"Costo de Compra (SIN IGV)"}
          control={control}
          errors={errors}
          width={4}
          type={"number"}
        />

        <FormFielController
          name={"por_detraccion"}
          title={"% Detraccin"}
          control={control}
          errors={errors}
          width={4}
          type={"number"}
        />

        <FormFielController
          name={"por_isc"}
          title={"% ISC"}
          control={control}
          errors={errors}
          width={4}
          type={"number"}
        />

        <Form.Field>
          <label> &nbsp;</label>
          <Checkbox
            label="Afecto"
            checked={watcher.afecto}
            onChange={() => {
              setValue("afecto", !watcher.afecto);
            }}
          />
        </Form.Field>
      </Form.Group>

      <Form.Group>
        <FormFielDropdownSarch
          model={settingDropdowns.categoria}
          empresaContexto={empresa}
          control={control}
          venta={watcher}
          setValue={setValue}
          width={8}
        />

        <FormFielDropdownSarch
          model={settingDropdowns.subcategoria}
          empresaContexto={empresa}
          control={control}
          venta={watcher}
          setValue={setValue}
          width={8}
        />
      </Form.Group>

      <Form.Group>
        <FormFielDropdownSarch
          model={settingDropdowns.marca}
          empresaContexto={empresa}
          control={control}
          venta={watcher}
          setValue={setValue}
          width={8}
        />

        <FormFielDropdownSarch
          model={settingDropdowns.linea}
          empresaContexto={empresa}
          control={control}
          venta={watcher}
          setValue={setValue}
          width={8}
        />
      </Form.Group>

      {Object.keys(errors)?.length ? (
        <Message
          color="orange"
          header="Advertencia"
          list={["Verifique que todos los campos requeridos esten completos."]}
        />
      ) : (
        <></>
      )}

      {isBackendError?.length && (
        <Message
          color="red"
          header="Verifique los siguientes campos"
          list={isBackendError}
        />
      )}
      <br />
      <br />
      <br />
      <Button loading={isLoadding} primary id={"formulario_guardar"}>
        Guardar
      </Button>
    </Form>
  );
};
export default FormularioProducto;
