import { Card, Container, Grid, Header } from "semantic-ui-react";

import { Link, useLocation, useNavigate } from "react-router-dom";
const Reports = () => {
  const location = useLocation();
  const navigate = useNavigate();

  const openModal = (url) => {
    navigate("/reportes/" + url, {
      state: {
        backgroundLocation: location,
      },
    });
  };

  return (
    <Container>
      <Grid.Column width={16} style={{ marginTop: "10px", padding: 10 }}>
        <Grid.Row>
          <Header as="h1">Reportes</Header>
        </Grid.Row>
        <Grid.Row style={{ marginTop: "10px" }}>
          <Card.Group>
            <Card
              onClick={() => {
                openModal("ventas");
              }}
              header="Ventas"
              description="Genera el Reporte de Venta por Periodos de Fechas, Operacion, Clientes en formato Excel"
            />
            {/* <Card
              // onClick={() => {
              //   openModal("contactos");
              // }}
              header="Contactos"
              description="Descarga el Listado de Contactos en formato Excel"
            />
            <Card
              // onClick={() => {
              //   openModal("productos");
              // }}
              header="Productos"
              description="Descarga el listado de Productos en formato Excel"
            /> */}
          </Card.Group>
        </Grid.Row>
      </Grid.Column>
    </Container>
  );
};

export default Reports;
