import instance from "./_instance";
import fileDownload from "js-file-download";
import qs from "qs";
import Path from "./_path";

import Localbase from "localbase";
// import axios from "axios";

const db = new Localbase("db");
db.config.debug = false;

// Create instance called instance

function show(
  empresa_id,
  local_id,
  page = 1,
  query = { limit: 100000, order: { id: "DESC" } },
  storage = true
) {
  const str = qs.stringify(query);
  return new Promise((ok, reject) => {
    instance.get(`/venta/${empresa_id}/${local_id}?page=${page}&${str}`).then(
      (res) => {
        if (storage) {
          db.collection("ventas")
            .set(res.data.data)
            .then((ventas) => {
              ok(ventas.data);
            });
        } else {
          ok(res.data.data);
        }
      },
      (bad) => {
        reject(false);
      }
    );
  });
}

function find(empresa_id, local_id, id) {
  return new Promise((resolve, reject) => {
    instance.get(`/venta/${empresa_id}/${local_id}/${id}`).then(
      (res) => {
        resolve(res.data);
      },
      (err) => {
        reject(false);
      }
    );
  });
}

function create(data) {
  return new Promise((resolve, reject) => {
    instance
      .post("venta", data)
      .then(async (res) => {
        const body = await db.collection("ventas").add(res.data);
        resolve(body.data);
      })
      .catch((error) => {
        if (error.response) {
          resolve(error.response.data);
        }
      });
  });
}

function anular(data) {
  console.log(data);
  return new Promise((resolve, reject) => {
    instance
      .delete(`venta/${data.empresa_id}/${data.local_id}/${data.id}`, data)
      .then((res) => {
        resolve(res.data);
      })
      .catch((error) => {
        if (error.response) {
          resolve(error.response.data);
        }
      });
  });
}

function update(data, id) {
  data.transportista_id =
    data.transportista_id === null ? undefined : data.transportista_id;
  data.ubigeo_destino_id =
    data.ubigeo_destino_id === null ? undefined : data.ubigeo_destino_id;
  data.ubigeo_partida_id =
    data.ubigeo_partida_id === null ? undefined : data.ubigeo_partida_id;
  return new Promise((resolve, reject) => {
    instance
      .put("venta/" + data.empresa_id + "/" + data.local_id + "/" + id, data)
      .then(async (res) => {
        const body = await db
          .collection("ventas")
          .doc({ id: res.data.id })
          .update(res.data);
        resolve(body.data);
      });
  });
}

function showDetalles(
  empresa_id,
  local_id,
  page = 1,
  query = { limit: 20, order: { id: "DESC" } }
) {
  const str = qs.stringify(query);
  return new Promise((ok, reject) => {
    instance
      .get(`/venta-detalle/${empresa_id}/${local_id}?page=${page}&${str}`)
      .then(
        (res) => {
          ok(res.data);
        },
        (bad) => {
          reject(false);
        }
      );
  });
}

function sendSunat(data) {
  console.log(data);
  return new Promise((resolve, reject) => {
    instance
      .get(`venta/enviar/${data.empresa_id}/${data.local_id}/${data.id}`, data)
      .then((res) => {
        resolve(res.data);
      })
      .catch((error) => {
        if (error.response) {
          resolve(error.response.data);
        }
      });
  });
}

async function download(data, type) {
  fetch(
    `${Path()}/venta/download/${data.empresa_id}/${data.local_id}/${
      data.id
    }/${type}`,
    {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
        Authorization: "Bearer " + localStorage.getItem("jwt"),
      },
    }
  )
    .then((response) => response.blob())
    .then((response) => {
      const extension = type === "cdn" ? "zip" : type;
      fileDownload(response, `${data.sunat_documento}.${extension}`);
    });
}

async function downloadExternal(data, type) {
  fetch(
    `${Path()}/downloadExternal/${data.empresa_id}/${data.local_id}/${
      data.id
    }/${type}`,
    {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
        Authorization: "Bearer " + localStorage.getItem("jwt"),
      },
    }
  )
    .then((response) => response.blob())
    .then((response) => {
      const extension = type === "cdn" ? "zip" : type;
      fileDownload(response, `${data.sunat_documento}.${extension}`);
    });
}

async function reporte(data, query) {
  const _query = qs.stringify(query);
  console.log(">>>>>>>>>>>>>>>>>>>>>>>>>>>>>", data, _query);

  fetch(`${Path()}/venta/${data.empresa.id}/${data.id}/reporte?${_query}`, {
    method: "GET",
    headers: {
      "Content-Type": "application/json",
      Authorization: "Bearer " + localStorage.getItem("jwt"),
    },
  })
    .then((response) => response.blob())
    .then((response) => {
      fileDownload(response, `${"reporte-ventas"}.xlsx`);
    });
}

async function consultar(query) {
  return new Promise((resolve, reject) => {
    instance.post(`/consultar`, query).then(
      (res) => {
        resolve(res.data);
      },
      (err) => {
        reject(false);
      }
    );
  });
}

async function consultarSearch(query) {
  return new Promise((resolve, reject) => {
    instance.post(`consultarSearch`, query).then(
      (res) => {
        resolve(res.data);
      },
      (err) => {
        reject(false);
      }
    );
  });
}

export default {
  show,
  find,
  create,
  update,
  showDetalles,
  anular,
  sendSunat,
  download,
  reporte,
  consultar,
  consultarSearch,
  downloadExternal
};
