import React, { useEffect, useState, useContext } from "react";
import { useNavigate, useLocation } from "react-router-dom";
import { useForm } from "react-hook-form";
import { Form, Button, Message, Segment } from "semantic-ui-react";
import { yupResolver } from "@hookform/resolvers/yup";
import bancoService from "../../services/bancos";
import FormFielController from "../FormFieldController";
import schema from "./schema";
import handleBackendErrors from "../../libs/handleBackendErrors";
import EmpresaContext from "../../context/EmpresaContext";
import FormFielDropdownSarch from "../DropdownSearch2";
import setting from "../../libs/settingDropdowns";

const FormularioBanco = ({ state }) => {
  let navigate = useNavigate();
  let location = useLocation();
  const { empresa } = useContext(EmpresaContext);

  const [isLoadding, setIsloadding] = useState(false);
  const [isBackendError, setIsBackendError] = useState(false);
  const {
    control,
    handleSubmit,
    formState: { errors },
    reset,
    setValue,
    watch,
  } = useForm({ resolver: yupResolver(schema), defaultValues: state });

  const [data, setData] = useState();
  const watcher = watch();
  useEffect(() => {
    (async () => {
      if (!!state && state.id) {
        let data = await bancoService.find(state.id);
        return setData(data.data);
      } else {
        return false;
      }
    })();
  }, []);

  const close = (moneda) => {
    navigate(location.state.backgroundLocation.pathname, {
      state: { reload: { moneda_id: moneda } },
    });
  };

  const onSubmit = async (data) => {
    setIsloadding(true);
    if (!!data && data.id) {
      // update
      const update = await bancoService.update(data, data.id);
      close(update);
      // verificar si es la empresa actual en el contexto del la instancia de local/empresa
    } else {
      //create
      const create = await bancoService.create(data);
      setIsloadding(false);
      if (create?.errors) {
        setIsBackendError(handleBackendErrors(create));
      } else {
        setIsBackendError(null);
        close(create);
      }
      return create;
    }
  };
  return (
    <Form onSubmit={handleSubmit(onSubmit)} noValidate>
      <Form.Group>
        <FormFielDropdownSarch
          model={setting.listado_bancos}
          empresaContexto={empresa}
          control={control}
          setValue={setValue}
          venta={watcher}
          width={16}
        />
        <FormFielController
          name={"numero_cuenta"}
          title={"Nmero de Cuenta"}
          control={control}
          errors={errors}
          width={16}
        />
      </Form.Group>

      {Object.keys(errors)?.length ? (
        <>
          <Message
            color="orange"
            header="Advertencia"
            list={[
              "Verifique que todos los campos requeridos esten completos.",
            ]}
          />
          <Segment>{JSON.stringify(errors, null, 2)}</Segment>
        </>
      ) : (
        <></>
      )}

      {isBackendError?.length && (
        <Message
          color="red"
          header="Verifique los siguientes campos"
          list={isBackendError}
        />
      )}

      <Button
        loading={isLoadding}
        disabled={isLoadding}
        content="Guardar"
        primary
      />
      <Button
        as="a"
        content="Deshacer Cambios"
        secondary
        onClick={() => {
          reset(state);
        }}
      />
    </Form>
  );
};

export default FormularioBanco;
