import { useEffect, useContext, useState } from "react";
import {
  Label,
  Button,
  Grid,
  Menu,
  Card,
  Icon,
  List,
  Table,
  Feed,
  Confirm,
  Dropdown,
  Modal,
} from "semantic-ui-react";
import { Link, useLocation, useNavigate } from "react-router-dom";
import Moment from "react-moment";
import Filter from "../../components/Filter";
import EmpresaContext from "../../context/EmpresaContext";
import qm from "../../libs/filterQueryMaker";
import ventaService from "../../services/ventas";
import productoService from "../../services/productos";
import precioService from "../../services/precio";
import FilterCliente from "../../components/cliente/filter";
import { updated } from "../../libs/updated";
import { LabelUpdate } from "../../components/LabelUpdate";
import Importador from "../../components/producto/Importador";
import useWindowSize from "../../hooks/useWindowSize";

import InputSearch from "../../components/InputSearch";

// import Spreadsheet from "react-spreadsheet";

import Localbase from "localbase";

const Productos = () => {
  let navigate = useNavigate();
  const db = new Localbase("db");
  const { windowSize: size } = useWindowSize();

  const location = useLocation();
  const { empresa } = useContext(EmpresaContext);
  const [actualData, setActualData] = useState([]);
  const [selected, setSelected] = useState(null);
  const [ventas, setVentas] = useState([]);

  const [searchInput, setSearchInput] = useState(false);

  const [openModalImportadorFivip, setOpenModalImportadorFivip] =
    useState(false);
  const [isConfirm, setIsConfirm] = useState({
    show: false,
    message: "Eliminar Precio",
  });

  const [query, setQuery] = useState({
    page: 0,
    limit: 14,
    order: { id: "ASC" },
  });

  const getVentaDetalles = async (producto) => {
    const query = {};
    query.filter = [["producto_id", "=", producto.id]];
    query.limit = 25;
    query.order = { id: "ASC" };
    const ventas = await ventaService.showDetalles(
      empresa.empresa.id,
      empresa.id,
      1,
      query
    );
    setVentas(ventas.data);
  };

  const get = async (reload = false, qm = false) => {
    const empresa_id = empresa.empresa.id;
    // const local_id = empresa.id;
    const fetch = await productoService.show("producto/" + empresa_id, qm);
    console.log({ fetch });

    setActualData(fetch);
  };

  const handleDeleteFilter = (index) => {
    let q = query;
    q.filter.splice(index, 1);
    setQuery({ ...q });
  };

  const handleSetContacto = (producto) => {
    setSelected(producto);
    getVentaDetalles(producto);
  };

  const handleNewProducto = () => {
    navigate("/welcome/productos/create", {
      state: {
        backgroundLocation: location,
        model: {
          empresa_id: empresa.empresa.id,
          local_id: empresa.id,
          afecto: 1,
        },
        empresa,
      },
    });
  };

  const handleEditProducto = () => {
    navigate("/welcome/productos/" + selected.id, {
      state: {
        backgroundLocation: location,
        model: selected,
        empresa,
      },
    });
  };

  const handleNewPrecio = () => {
    navigate("/welcome/precios/create", {
      state: {
        backgroundLocation: location,
        model: {
          local_id: empresa.id,
          empresa_id: empresa.empresa.id,
          producto_id: selected.id,
          costo_compra: selected.precio_compra,
          presentacion_id: 49,
        },
        empresa,
      },
    });
  };

  const handleEditPrecio = (precio) => {
    navigate("/welcome/precios/" + precio.id, {
      state: {
        backgroundLocation: location,
        model: precio,
        empresa,
      },
    });
  };

  // const handleDeletePrecio = (precio) => {
  //   setIsConfirm({
  //     show: !isConfirm.show,
  //     message: "Eliminar Precio",
  //     precio_id: precio.id,
  //   });
  // };

  const confimAccepted = async () => {
    const body = await precioService.destroy(isConfirm.precio.id);
    console.log(body);
  };

  // useEffect(() => {
  //   const qs = qm.do(query);
  //   console.log(qs);
  //   get(false, qs);
  // }, [query]);

  useEffect(() => {
    db.collection("productos")
      .get()
      .then((productos) => {
        const items = productos.sort(function (a, b) {
          return b.id - a.id;
        });
        setActualData(items);
        get(1);
      });

    return () => {
      setActualData([]);
    };
  }, []);

  useEffect(() => {
    if (location?.state?.reload && location?.state?.reload?.producto_id) {
      db.collection("productos")
        .get()
        .then((productos) => {
          setActualData(productos.reverse());
          get(1);

          let productoSelected = productos.find(
            (v) => v.id === location.state.reload.producto_id.data.id
          );

          setSelected(productoSelected);
          location.state = null;
        });

      // const qs = qm.do(query);
      // get(false, qs);
      // setSelected({ ...location.state.reload.producto_id });
      // location.state = null;
    }

    if (location?.state?.reload && location?.state?.reload?.precio_id) {
      // const qs = qm.do(query);
      // get(false, qs);

      if (location?.state?.reload?.push) {
        const selectedMutation = selected;
        const selectedPrecioMutation = selected.precios || [];
        selectedPrecioMutation.push(location.state.reload.precio_id);
        selectedMutation.precios = selectedPrecioMutation;
        setSelected({ ...selectedMutation });
      } else {
        const selectedPrecioMutation = selected?.precios.map((v, k) => {
          if (v.id === location.state.reload.precio_id.id) {
            return location.state.reload.precio_id;
          } else {
            return v;
          }
        });

        if (selectedPrecioMutation) {
          const selectedMutation = selected;
          selectedMutation.precios = selectedPrecioMutation;

          setSelected({ ...selectedMutation });
        }
      }

      location.state = null;
    }
  }, [location?.state]);

  useEffect(() => {
    (async () => {
      const productos = await db.collection("productos").get();

      if (!!searchInput) {
        const result = productos.filter((list) => {
          if (list.nombre.indexOf(searchInput) >= 0) {
            return list;
          }
        });

        setActualData(result);
      } else {
        setActualData(productos);
      }
    })();
  }, [searchInput]);

  return (
    <>
      {size.width > 700 && (
        <>
          <Grid.Column width={8}>
            <Grid.Row>
              <List relaxed="very" style={{ marginTop: "1rem" }}>
                <List.Item>
                  <List.Content floated="left">
                    <InputSearch
                      searchInput={searchInput}
                      setSearchInput={setSearchInput}
                      placeholder="Buscar Producto"
                    />
                  </List.Content>
                  <List.Content floated="right">
                    {/* <Dropdown
                      text="Importar"
                      icon="upload"
                      floating
                      labeled
                      button
                      className="button ui mini icon"
                    >
                      <Dropdown.Menu>
                        <Dropdown.Item>
                          Descargar Plantilla de Productos Excel
                        </Dropdown.Item>
                        <Dropdown.Item>
                          Importar desde Plantilla de Productos Excel
                        </Dropdown.Item>
                        <Dropdown.Item
                          onClick={() =>
                            setOpenModalImportadorFivip(
                              !openModalImportadorFivip
                            )
                          }
                        >
                          Importador Fivip
                        </Dropdown.Item>
                      </Dropdown.Menu>
                    </Dropdown> */}
                    <Button
                      size={"mini"}
                      color={"teal"}
                      onClick={handleNewProducto}
                    >
                      Nuevo Producto
                    </Button>
                  </List.Content>
                </List.Item>
              </List>
            </Grid.Row>

            <Grid.Row>
              <List relaxed="very" style={{ marginTop: "1rem" }}>
                <List.Item>
                  <List.Content floated="left">
                    {query?.filter?.map((el, index) => (
                      <Label
                        as="a"
                        key={index}
                        onClick={() => {
                          handleDeleteFilter(index);
                        }}
                      >
                        {el[0]}
                        <Icon name="delete" key={index} />
                      </Label>
                    ))}
                  </List.Content>
                </List.Item>
              </List>
            </Grid.Row>

            <Grid.Row>
              <Grid.Column
                style={{
                  overflowY: "scroll",
                  height: "83vh",
                  marginTop: "1rem",
                }}
              >
                <Table celled selectable>
                  <Table.Header>
                    <Table.Row>
                      <Table.HeaderCell>Codigo</Table.HeaderCell>
                      <Table.HeaderCell>Descripcin</Table.HeaderCell>
                      <Table.HeaderCell>Codigo de Barras</Table.HeaderCell>
                      <Table.HeaderCell>Marca</Table.HeaderCell>
                      <Table.HeaderCell>Presentacin</Table.HeaderCell>
                      <Table.HeaderCell>Categora</Table.HeaderCell>
                      <Table.HeaderCell>Sub Categoria</Table.HeaderCell>
                    </Table.Row>
                  </Table.Header>

                  <Table.Body>
                    {actualData?.map((v, index) => {
                      return (
                        <Table.Row
                          onClick={() => {
                            handleSetContacto(v);
                          }}
                        >
                          <Table.Cell width={1}>{v.codigo}</Table.Cell>
                          <Table.Cell width={6}>
                            {v.nombre}
                            &nbsp;
                            {updated(v.updated_at) && (
                              <LabelUpdate index={index} />
                            )}
                          </Table.Cell>
                          <Table.Cell width={2}>{v?.codigo_barras}</Table.Cell>
                          <Table.Cell width={2}>{v?.marca?.nombre}</Table.Cell>
                          <Table.Cell width={1}>
                            {v?.presentacion?.nombre}
                          </Table.Cell>
                          <Table.Cell width={2}>
                            {v?.categoria?.nombre}
                          </Table.Cell>
                          <Table.Cell width={2}>
                            {v?.subcategoria?.nombre}
                          </Table.Cell>
                        </Table.Row>
                      );
                    })}
                  </Table.Body>
                </Table>
              </Grid.Column>
            </Grid.Row>
          </Grid.Column>

          <Grid.Column width={8}>
            {selected && selected.id && (
              <>
                <Card.Group
                  doubling
                  itemsPerRow={1}
                  stackable
                  style={{ marginTop: "5vh" }}
                >
                  <Card>
                    <Card.Content>
                      <>
                        <Card.Header>
                          {selected?.nombre}{" "}
                          {updated(selected?.updated_at) && (
                            <LabelUpdate index={selected?.id} />
                          )}
                        </Card.Header>
                        <Card.Description>
                          <Table basic="very">
                            <Table.Body>
                              <Table.Row>
                                <Table.Cell>
                                  <b>Cdigo:</b>
                                  <br />
                                  {selected?.codigo}
                                </Table.Cell>
                                <Table.Cell>
                                  <b>Cdigo de Barras:</b>
                                  <br />
                                  {selected?.codigo_barras}
                                </Table.Cell>
                                <Table.Cell>
                                  <b>Marca:</b>
                                  <br />
                                  {selected?.marca?.nombre}
                                </Table.Cell>
                                <Table.Cell>
                                  <b>Linea:</b>
                                  <br />
                                  {selected?.linea?.nombre}
                                </Table.Cell>
                              </Table.Row>
                              <Table.Row>
                                <Table.Cell>
                                  <b>Afecto:</b>
                                  <br />
                                  {selected?.afecto ? "Si" : "No"}
                                </Table.Cell>
                                <Table.Cell>
                                  <b>Precio de Compra:</b>
                                  <br />
                                  {selected?.moneda?.simbolo}{" "}
                                  {selected?.precio_compra}
                                </Table.Cell>
                                <Table.Cell>
                                  <b>Peso:</b>
                                  <br />
                                  {selected?.peso} KG
                                </Table.Cell>
                                <Table.Cell>&nbsp;</Table.Cell>
                              </Table.Row>
                              <Table.Row>
                                <Table.Cell>
                                  <b>% IGV:</b>
                                  <br />
                                  {selected?.igv}
                                </Table.Cell>
                                <Table.Cell>
                                  <b>% Detraccin:</b>
                                  <br />
                                  {selected?.por_detraccion}
                                </Table.Cell>
                                <Table.Cell>
                                  <b>% ISC:</b>
                                  <br />
                                  {selected?.por_isc}
                                </Table.Cell>
                                <Table.Cell> &nbsp;</Table.Cell>
                              </Table.Row>
                            </Table.Body>
                          </Table>
                        </Card.Description>
                      </>
                    </Card.Content>

                    <Card.Content extra>
                      <Button primary onClick={handleEditProducto}>
                        Editar
                      </Button>
                    </Card.Content>
                  </Card>

                  <Card>
                    <Card.Content
                      style={{ overflowY: "scroll", height: "24vh" }}
                    >
                      <Card.Header>
                        Precios de Venta
                        <Button
                          floated="right"
                          icon
                          labelPosition="left"
                          primary
                          size="small"
                          onClick={handleNewPrecio}
                          style={{ marginBottom: "1vh" }}
                        >
                          <Icon name="currency" />
                          Nuevo Precio
                        </Button>
                      </Card.Header>
                      <Card.Description>
                        <Table basic="very">
                          <Table.Header>
                            <Table.Row>
                              <Table.HeaderCell>
                                <abbr title="Presentacion Unitaria">PST.</abbr>
                              </Table.HeaderCell>
                              <Table.HeaderCell>
                                <abbr title="Utlidad">UTL.</abbr>
                              </Table.HeaderCell>
                              <Table.HeaderCell>
                                <abbr title="Precio de Compra sin IGV">
                                  COSTO
                                </abbr>
                              </Table.HeaderCell>
                              <Table.HeaderCell>
                                <abbr title="Precio de Venta sin IGV">
                                  GRAV.
                                </abbr>
                              </Table.HeaderCell>
                              <Table.HeaderCell>
                                <abbr title="Valor del IGV">IGV</abbr>
                              </Table.HeaderCell>
                              <Table.HeaderCell>
                                <abbr title="Precio de Venta">PV.</abbr>
                              </Table.HeaderCell>

                              <Table.HeaderCell></Table.HeaderCell>
                            </Table.Row>
                          </Table.Header>

                          <Table.Body>
                            {selected?.precios?.map((p, index) => (
                              <Table.Row>
                                <Table.Cell>
                                  {p.presentacion.nombre} &nbsp;
                                  {updated(p.updated_at) && (
                                    <LabelUpdate index={index} />
                                  )}{" "}
                                </Table.Cell>
                                <Table.Cell>% {p?.por_utilidad}</Table.Cell>
                                <Table.Cell>
                                  {selected.moneda?.simbolo} {p?.costo_compra}
                                </Table.Cell>
                                <Table.Cell>
                                  {p?.moneda?.simbolo} {p.costo_venta}
                                </Table.Cell>
                                <Table.Cell>
                                  {p?.moneda?.simbolo} {p.igv}
                                </Table.Cell>
                                <Table.Cell>
                                  <b>
                                    {p?.moneda?.simbolo} {p.precio_venta}
                                  </b>
                                </Table.Cell>

                                <Table.Cell>
                                  <Button.Group size="mini">
                                    <Button
                                      icon={"pencil"}
                                      onClick={() => {
                                        handleEditPrecio(p);
                                      }}
                                    />
                                    {/* <Button
                                  color="red"
                                  icon={"times"}
                                  onClick={() => {
                                    handleDeletePrecio(p);
                                  }}
                                /> */}
                                  </Button.Group>
                                </Table.Cell>
                              </Table.Row>
                            ))}
                          </Table.Body>

                          <Table.Footer fullWidth>
                            <Table.Row>
                              <Table.HeaderCell />
                              <Table.HeaderCell colSpan="6"></Table.HeaderCell>
                            </Table.Row>
                          </Table.Footer>
                        </Table>
                      </Card.Description>
                    </Card.Content>
                  </Card>
                </Card.Group>

                <Card.Group></Card.Group>
              </>
            )}

            <Confirm
              open={isConfirm.show}
              content={isConfirm.message}
              onCancel={() => {
                setIsConfirm({ show: !isConfirm.show });
              }}
              onConfirm={() => {
                confimAccepted();
              }}
            />
          </Grid.Column>
        </>
      )}

      {size.width < 699 && (
        <>
          {!selected && (
            <Grid.Column width={16}>
              <Grid.Row>
                <List relaxed="very" style={{ marginTop: "1rem" }}>
                  <List.Item>
                    <List.Content floated="left">
                      <Button
                        style={{ marginLeft: 2 }}
                        size={"small"}
                        onClick={handleNewProducto}
                      >
                        Nuevo
                      </Button>
                      {/* <Dropdown
                        icon="upload"
                        button
                        className="button ui small icon"
                      >
                        <Dropdown.Menu>
                          <Dropdown.Item>
                            Descargar Plantilla de Productos Excel
                          </Dropdown.Item>
                          <Dropdown.Item>
                            Importar desde Plantilla de Productos Excel
                          </Dropdown.Item>
                          <Dropdown.Item
                            onClick={() =>
                              setOpenModalImportadorFivip(
                                !openModalImportadorFivip
                              )
                            }
                          >
                            Importador Fivip
                          </Dropdown.Item>
                        </Dropdown.Menu>
                      </Dropdown> */}
                    </List.Content>

                    <List.Content floated="right">
                      <InputSearch
                        searchInput={searchInput}
                        setSearchInput={setSearchInput}
                        placeholder={"Buscar Producto"}
                      />
                    </List.Content>
                  </List.Item>
                </List>
              </Grid.Row>

              <Grid.Row>
                <List relaxed="very" style={{ marginTop: "1rem" }}>
                  <List.Item>
                    <List.Content floated="left">
                      {query?.filter?.map((el, index) => (
                        <Label
                          as="a"
                          key={index}
                          onClick={() => {
                            handleDeleteFilter(index);
                          }}
                        >
                          {el[0]}
                          <Icon name="delete" key={index} />
                        </Label>
                      ))}
                    </List.Content>
                  </List.Item>
                </List>
              </Grid.Row>

              <Grid.Row>
                <Grid.Column
                  style={{
                    overflowY: "scroll",
                    height: "83vh",
                    marginTop: "1rem",
                  }}
                >
                  <Table celled selectable>
                    <Table.Body>
                      {actualData?.map((v, index) => {
                        return (
                          <Table.Row
                            onClick={() => {
                              handleSetContacto(v);
                            }}
                          >
                            <Table.Cell width={1}>{v.codigo}</Table.Cell>
                            <Table.Cell width={6}>
                              {v.nombre}
                              &nbsp;
                              {updated(v.updated_at) && (
                                <LabelUpdate index={index} />
                              )}
                            </Table.Cell>
                            <Table.Cell width={2}>
                              {v?.codigo_barras}
                            </Table.Cell>
                            <Table.Cell width={2}>
                              {v?.marca?.nombre}
                            </Table.Cell>
                            <Table.Cell width={1}>
                              {v?.presentacion?.nombre}
                            </Table.Cell>
                            <Table.Cell width={2}>
                              {v?.categoria?.nombre}
                            </Table.Cell>
                            <Table.Cell width={2}>
                              {v?.subcategoria?.nombre}
                            </Table.Cell>
                          </Table.Row>
                        );
                      })}
                    </Table.Body>
                  </Table>
                </Grid.Column>
              </Grid.Row>
            </Grid.Column>
          )}

          <Grid.Column width={16}>
            {selected && selected.id && (
              <>
                <Grid.Row style={{ marginTop: 10, marginLeft: 12 }}>
                  <span className="float left">
                    <Button
                      size="mini"
                      icon="arrow circle left"
                      content="Regresar"
                      onClick={async () => {
                        await setSelected(undefined);
                      }}
                    />
                  </span>
                </Grid.Row>
                <Grid.Row>
                  <Card.Group
                    doubling
                    itemsPerRow={1}
                    stackable
                    style={{ marginTop: "5vh" }}
                  >
                    <Card>
                      <Card.Content>
                        <>
                          <Card.Header>
                            {selected?.nombre}
                            {updated(selected?.updated_at) && (
                              <LabelUpdate index={selected?.id} />
                            )}
                          </Card.Header>
                          <Card.Description>
                            <Table basic="very" unstackable>
                              <Table.Body>
                                <Table.Row>
                                  <Table.Cell>
                                    <b>Cdigo:</b>
                                    <br />
                                    {selected?.codigo}
                                  </Table.Cell>
                                  <Table.Cell>
                                    <b>Cdigo de Barras:</b>
                                    <br />
                                    {selected?.codigo_barras}
                                  </Table.Cell>
                                  <Table.Cell>
                                    <b>Marca:</b>
                                    <br />
                                    {selected?.marca?.nombre}
                                  </Table.Cell>
                                  <Table.Cell>
                                    <b>Linea:</b>
                                    <br />
                                    {selected?.linea?.nombre}
                                  </Table.Cell>
                                </Table.Row>
                                <Table.Row>
                                  <Table.Cell>
                                    <b>Afecto:</b>
                                    <br />
                                    {selected?.afecto ? "Si" : "No"}
                                  </Table.Cell>
                                  <Table.Cell>
                                    <b>Precio de Compra:</b>
                                    <br />
                                    {selected?.moneda?.simbolo}{" "}
                                    {selected?.precio_compra}
                                  </Table.Cell>
                                  <Table.Cell>
                                    <b>Peso:</b>
                                    <br />
                                    {selected?.peso} KG
                                  </Table.Cell>
                                  <Table.Cell>&nbsp;</Table.Cell>
                                </Table.Row>
                                <Table.Row>
                                  <Table.Cell>
                                    <b>% IGV:</b>
                                    <br />
                                    {selected?.igv}
                                  </Table.Cell>
                                  <Table.Cell>
                                    <b>% Detraccin:</b>
                                    <br />
                                    {selected?.por_detraccion}
                                  </Table.Cell>
                                  <Table.Cell>
                                    <b>% ISC:</b>
                                    <br />
                                    {selected?.por_isc}
                                  </Table.Cell>
                                  <Table.Cell> &nbsp;</Table.Cell>
                                </Table.Row>
                              </Table.Body>
                            </Table>
                          </Card.Description>
                        </>
                      </Card.Content>
                      <Card.Content extra>
                        <Button primary onClick={handleEditProducto}>
                          Editar
                        </Button>
                      </Card.Content>
                    </Card>

                    <Card>
                      <Card.Content
                        style={{ overflowY: "scroll", height: "24vh" }}
                      >
                        <Card.Header>
                          Precios de Venta
                          <Button
                            floated="right"
                            icon
                            labelPosition="left"
                            primary
                            size="small"
                            onClick={handleNewPrecio}
                            style={{ marginBottom: "1vh" }}
                          >
                            <Icon name="currency" />
                            Nuevo Precio
                          </Button>
                        </Card.Header>
                        <Card.Description>
                          <Table basic="very" unstackable>
                            <Table.Header>
                              <Table.Row>
                                <Table.HeaderCell>
                                  <abbr title="Presentacion Unitaria">
                                    PST.
                                  </abbr>
                                </Table.HeaderCell>
                                <Table.HeaderCell>
                                  <abbr title="Utlidad">UTL.</abbr>
                                </Table.HeaderCell>
                                <Table.HeaderCell>
                                  <abbr title="Precio de Compra sin IGV">
                                    COSTO
                                  </abbr>
                                </Table.HeaderCell>
                                <Table.HeaderCell>
                                  <abbr title="Precio de Venta sin IGV">
                                    GRAV.
                                  </abbr>
                                </Table.HeaderCell>
                                <Table.HeaderCell>
                                  <abbr title="Valor del IGV">IGV</abbr>
                                </Table.HeaderCell>
                                <Table.HeaderCell>
                                  <abbr title="Precio de Venta">PV.</abbr>
                                </Table.HeaderCell>

                                <Table.HeaderCell></Table.HeaderCell>
                              </Table.Row>
                            </Table.Header>

                            <Table.Body>
                              {selected?.precios?.map((p, index) => (
                                <Table.Row>
                                  <Table.Cell>
                                    {p.presentacion.nombre} &nbsp;
                                    {updated(p.updated_at) && (
                                      <LabelUpdate index={index} />
                                    )}{" "}
                                  </Table.Cell>
                                  <Table.Cell>% {p?.por_utilidad}</Table.Cell>
                                  <Table.Cell>
                                    {selected.moneda?.simbolo} {p?.costo_compra}
                                  </Table.Cell>
                                  <Table.Cell>
                                    {p?.moneda?.simbolo} {p.costo_venta}
                                  </Table.Cell>
                                  <Table.Cell>
                                    {p?.moneda?.simbolo} {p.igv}
                                  </Table.Cell>
                                  <Table.Cell>
                                    <b>
                                      {p?.moneda?.simbolo} {p.precio_venta}
                                    </b>
                                  </Table.Cell>

                                  <Table.Cell>
                                    <Button.Group size="mini">
                                      <Button
                                        icon={"pencil"}
                                        onClick={() => {
                                          handleEditPrecio(p);
                                        }}
                                      />
                                      {/* <Button
                                  color="red"
                                  icon={"times"}
                                  onClick={() => {
                                    handleDeletePrecio(p);
                                  }}
                                /> */}
                                    </Button.Group>
                                  </Table.Cell>
                                </Table.Row>
                              ))}
                            </Table.Body>

                            <Table.Footer fullWidth>
                              <Table.Row>
                                <Table.HeaderCell />
                                <Table.HeaderCell colSpan="6"></Table.HeaderCell>
                              </Table.Row>
                            </Table.Footer>
                          </Table>
                        </Card.Description>
                      </Card.Content>
                    </Card>
                  </Card.Group>

                  <Card.Group></Card.Group>
                </Grid.Row>
              </>
            )}

            <Confirm
              open={isConfirm.show}
              content={isConfirm.message}
              onCancel={() => {
                setIsConfirm({ show: !isConfirm.show });
              }}
              onConfirm={() => {
                confimAccepted();
              }}
            />
          </Grid.Column>
        </>
      )}

      <Modal
        dimmer={"blurring"}
        open={openModalImportadorFivip}
        onClose={() => setOpenModalImportadorFivip(!openModalImportadorFivip)}
      >
        <Modal.Header>
          Selecciona uno  varios paquetes de Productos
        </Modal.Header>
        <Modal.Content>
          <Importador close={setOpenModalImportadorFivip} />
        </Modal.Content>
        <Modal.Actions>
          <Button
            onClick={() =>
              setOpenModalImportadorFivip(!openModalImportadorFivip)
            }
          >
            Cerrar
          </Button>
        </Modal.Actions>
      </Modal>
    </>
  );
};

export default Productos;
