import instance from "./_instance";
import Localbase from "localbase";
const qs = require("qs");

// Create instance called instance

const db = new Localbase("db");
db.config.debug = false;

function show(
  endPoint,
  query = { limit: 10000, order: { id: "DESC" } },
  page = 0
) {
  if (!!!query.limit) {
    query = {};
    query.limit = 10000;
    query.order = { id: "DESC" };
  }

  const str = qs.stringify(query);
  return new Promise((ok, reject) => {
    instance.get(`${endPoint}?page=${page}&${str}`).then(
      (res) => {
        db.collection("productos")
          .set(res.data.data)
          .then((productos) => {
            ok(productos.data);
          });
      },
      (bad) => {
        reject(false);
      }
    );
  });
}

function find(id) {
  return new Promise((resolve, reject) => {
    instance.get("empresa/" + id).then((res) => {
      resolve(res.data);
    });
  });
}

function create(data) {
  return new Promise((resolve, reject) => {
    instance
      .post("producto", data)
      .then(async (res) => {
        const body = await db.collection("productos").add(res.data);
        resolve(body.data);
      })
      .catch((error) => {
        if (error.response) {
          resolve(error.response.data);
        }
      });
  });
}

function update(data, id) {
  return new Promise((resolve, reject) => {
    instance
      .put("producto/" + data.empresa_id + "/" + id, data)
      .then(async (res) => {
        const body = await db
          .collection("productos")
          .doc({ id: res.data.id })
          .update(res.data);
        resolve(body.data);
      });
  });
}

function importadorFivip(data, type) {
  return new Promise((resolve, reject) => {
    instance
      .get(
        "producto/importar/" +
          data.empresa_id +
          "/" +
          data.empresa.id +
          "/" +
          type
      )
      .then((res) => {
        resolve(res.data);
      });
  });
}

export default { show, find, create, update, importadorFivip };
