import React, { useEffect, useState, useContext } from "react";
import { useNavigate, useLocation } from "react-router-dom";
import { useForm } from "react-hook-form";
import { Form, Button, Message, Input } from "semantic-ui-react";
import { yupResolver } from "@hookform/resolvers/yup";
import clienteSevice from "../../services/cliente";
import FormFielController from "../FormFieldController";
import FormFielDropdownSarch from "../DropdownSearch2";
import EmpresaContext from "../../context/EmpresaContext";
import setting from "../../libs/settingDropdowns";

import schema from "./schema";

import handleBackendErrors from "../../libs/handleBackendErrors";

const FormularioCliente = ({ state }) => {
  let navigate = useNavigate();
  let location = useLocation();
  const { empresa } = useContext(EmpresaContext);
  const [isLoadding, setIsloadding] = useState(false);
  const [isSearchSunat, setIsSearchSunat] = useState(false);
  const [isUbigeoSearch, setIsUbigeoSearch] = useState(false);
  const [isBackendError, setIsBackendError] = useState(false);

  const close = (cliente) => {
    navigate(location.state.backgroundLocation.pathname, {
      state: { reload: { cliente_id: cliente } },
    });
  };

  const onSubmit = async (data) => {
    setIsloadding(true);
    // para update
    if (!!data && data.id) {
      const update = await clienteSevice.update(data, data.id);
      if (update?.errors) {
        setIsBackendError(handleBackendErrors(update));
      } else {
        // db.collection("contactos").add(data);
        // console.log("debugger");
        setIsBackendError(null);
        close(update);
      }
    } else {
      //para create
      clienteSevice.create(data).then(
        (create) => {
          setIsloadding(false);
          if (create?.errors) {
            setIsBackendError(handleBackendErrors(create));
          } else {
            setIsBackendError(null);
            close(create);
            return create;
          }
        },
        (errors) => {
          close(data);
        }
      );
    }
  };

  const {
    control,
    handleSubmit,
    formState: { errors },
    setValue,
    watch,
    reset,
  } = useForm({ resolver: yupResolver(schema), defaultValues: state });

  const watcher = watch();

  const getDataSunat = async () => {
    setIsSearchSunat(true);

    if (watcher?.tipo_documento_identy_id === 3) {
      /***cuando es dni */
      clienteSevice
        .getDni({
          data: watcher.numero_documento,
        })
        .then(
          (res) => {
            setValue("nombre", res.data.ane_alias);
            setValue("nombre_comercial", res.data.ane_alias);

            setIsUbigeoSearch(true);
            setIsSearchSunat(false);
            return true;
          },
          (error) => {
            setIsSearchSunat(false);
            return true;
          }
        );
    }

    if (watcher?.tipo_documento_identy_id === 4) {
      /***cuando es ruc */

      clienteSevice.getRuc({ data: watcher.numero_documento }).then(
        (res) => {
          setValue("nombre", res.data.razonSocial);
          setValue("nombre_comercial", res.data.razonSocial);
          setValue("direccion", res.data.direccion);
          setValue("ubigeo_id", parseInt(res.data.ubi_id));
          setValue("ubigeo", {
            id: res.data.ubi_id,
            provincia: res.data.provincia,
            distrito: res.data.distrito,
            departamento: res.data.departamento,
          });

          setIsUbigeoSearch(true);
          setIsSearchSunat(false);
          return true;
        },
        (error) => {
          setIsSearchSunat(false);
          return true;
        }
      );
    }
  };

  return (
    <Form onSubmit={handleSubmit(onSubmit)} noValidate>
      <Form.Group>
        <FormFielDropdownSarch
          model={setting.tipo_documento_identy}
          empresaContexto={empresa}
          control={control}
          setValue={setValue}
          width={7}
        />
      </Form.Group>

      <Form.Group>
        <FormFielController
          name={"numero_documento"}
          title={"Numero de Documento"}
          control={control}
          errors={errors}
          width={7}
        />
        {watcher.tipo_documento_identy_id === 4 ||
        watcher.tipo_documento_identy_id === 3 ? (
          <Form.Field>
            <Button
              primary
              style={{ marginTop: "22px" }}
              onClick={(e) => {
                e.preventDefault();
                getDataSunat();
              }}
              loading={isSearchSunat}
            >
              Buscar en Sunat
            </Button>
          </Form.Field>
        ) : (
          <></>
        )}
      </Form.Group>
      <Form.Group widths={2}>
        <FormFielController
          name={"nombre"}
          title={"Nombre"}
          control={control}
          errors={errors}
          width={7}
        />
        <FormFielController
          name={"nombre_comercial"}
          title={"Nombre Comercial"}
          control={control}
          errors={errors}
          width={8}
        />
      </Form.Group>

      <Form.Group widths={2}>
        <FormFielController
          name={"direccion"}
          title={"Direccion"}
          control={control}
          errors={errors}
          width={15}
        />
      </Form.Group>

      <Form.Group>
        {!isUbigeoSearch ? (
          <FormFielDropdownSarch
            model={setting.ubigeo}
            empresaContexto={empresa}
            control={control}
            setValue={setValue}
            venta={watcher}
          />
        ) : (
          <Form.Field
            width={5}
            control={Input}
            label={"Ubigeo"}
            value={`${watcher.ubigeo.provincia} ${watcher.ubigeo.distrito} ${watcher.ubigeo.departamento}`}
            placeholder="Ubigeo"
            type={"text"}
            readOnly
          />
        )}
      </Form.Group>

      <Form.Group widths={3}>
        <FormFielController
          type={"number"}
          name={"telefono"}
          title={"Telefono Celular | WhatApp"}
          control={control}
          errors={errors}
          width={5}
        />
        <FormFielController
          type={"number"}
          name={"celular"}
          title={"Telefono Segundario"}
          control={control}
          errors={errors}
          width={5}
        />
        <FormFielController
          name={"email"}
          title={"Email"}
          control={control}
          errors={errors}
          width={5}
        />
      </Form.Group>

      {Object.keys(errors)?.length ? (
        <Message
          color="orange"
          header="Advertencia"
          list={["Verifique que todos los campos requeridos esten completos."]}
        />
      ) : (
        <></>
      )}

      {isBackendError?.length && (
        <Message
          color="red"
          header="Verifique los siguientes campos"
          list={isBackendError}
        />
      )}

      <br />
      <br />
      <br />
      <br />
      <Button primary disabled={!!isLoadding}>
        Guardar
      </Button>
      <Button
        style={{ marginTop: "1vh" }}
        as="a"
        content="Deshacer Cambios"
        onClick={() => {
          reset(state);
        }}
      />
    </Form>
  );
};
export default FormularioCliente;
