import React, { useState, useEffect, useContext } from "react";
import {
  Button,
  Card,
  Container,
  Grid,
  Header,
  Label,
  List,
  Segment,
} from "semantic-ui-react";
import moneda from "../../services/moneda";

import EmpresaContext from "../../context/EmpresaContext";
import { Link, useLocation } from "react-router-dom";

const Utilidades = () => {
  const location = useLocation();
  const { empresa } = useContext(EmpresaContext);
  const [monedas, setMonedas] = useState([]);

  let getMonedas = async (empresa_id) => {
    let monedas = await moneda.show(empresa_id);
    setMonedas(monedas.data);
  };

  useEffect(() => {
    getMonedas(empresa.empresa.id);
  }, [location.state?.reload?.moneda_id]);

  return (
    <>
      <Container>
        <Grid.Column width={16} style={{ marginTop: "10px", padding: 10 }}>
          <Grid.Row>
            <Header as="h1">Utilidad</Header>
          </Grid.Row>
          <Grid.Row style={{ marginTop: "10px" }}>
            <Card.Group>
              <Card>
                <Card.Content>
                  <Card.Header>
                    <Header>Monedas</Header>
                    <Link
                      className="button ui mini"
                      to={"/welcome/moneda/create"}
                      state={{
                        backgroundLocation: location,
                        model: {
                          local_id: empresa.id,
                          empresa_id: empresa.empresa.id,
                        },
                      }}
                    >
                      Nuevo
                    </Link>
                  </Card.Header>
                </Card.Content>
                <Card.Content>
                  <List divided verticalAlign="middle">
                    {monedas.map((v, index) => (
                      <List.Item key={index}>
                        <List.Content floated="right">
                          <Link
                            className="button ui mini"
                            to={"/welcome/moneda/" + v.id}
                            state={{
                              backgroundLocation: location,
                              model: v,
                              empresaContexto: empresa,
                            }}
                          >
                            Editar
                          </Link>
                        </List.Content>
                        <List.Content>
                          <Label circular color={""}>
                            {v.simbolo}
                          </Label>
                          &nbsp;&nbsp;
                          {v.nombre}
                        </List.Content>
                      </List.Item>
                    ))}
                  </List>
                </Card.Content>
              </Card>
            </Card.Group>
          </Grid.Row>
        </Grid.Column>
      </Container>
    </>
  );
};

export default Utilidades;
