import { useEffect, useContext, useState } from "react";
import { Form, Header, Icon, Button, Table, Popup } from "semantic-ui-react";
import settingDropdowns from "../../libs/settingDropdowns";
import FormFielDropdownSarch from "../DropdownSearch2";
import FormFielController from "../FormFieldController";
import { useForm } from "react-hook-form";
import EmpresaContext from "../../context/EmpresaContext";
import Moment from "react-moment";
import { toast } from "react-toastify";
import useWindowSize from "../../hooks/useWindowSize";
import currency from "../../libs/currency";

const FormularioPago = ({
  editionMode,
  pagos,
  moneda,
  setChangeFather,
  total_operacion,
}) => {
  const { empresa } = useContext(EmpresaContext);
  const { windowSize: size } = useWindowSize();

  const {
    control,
    formState: { errors },
    setValue,
    watch,
  } = useForm({
    defaultValues: {
      fecha_pago: new Date()
        .toLocaleDateString("es-CL")
        .split("-")
        .reverse()
        .join("-"),
      total_comision: 0,
    },
  });

  const watcher = watch(); // you can also target specific fields by their names
  const [totalPago, setTotalPago] = useState(0);

  useEffect(() => {
    (async () => {
      if (!!pagos.length) {
        setTotalPago(
          pagos
            .map((v, index) => v.total_pago)
            .reduce(
              (accumulator, curr) => parseFloat(accumulator) + parseFloat(curr)
            )
        );
      } else {
        setTotalPago(0);
      }
      await setChangeFather("pago_status", totalPago);
    })();

    return () => false;
  }, [pagos, setChangeFather, totalPago]);

  const validate = (model) => {
    const required = [
      "numero_operacion",
      "fecha_pago",
      "total_comision",
      "total_pago",
      "metodo_pago_id",
      "metodo",
      "banco_nombre",
      "banco_id",
      "numero_operacion",
    ];

    const aproved = [];

    const tt =
      parseFloat(totalPago) +
      parseFloat(model.total_pago) -
      parseFloat(total_operacion);

    if (tt > 0.01) {
      toast.error("el limite de pago ha exedido");
      return false;
    }

    for (let i = 0; i < required.length; i++) {
      const e = required[i];
      const check = Object.keys(model).indexOf(e);
      if (check !== -1) {
        aproved.push(e);
      }
    }

    if (aproved.length === required.length) {
      return true;
    } else {
      return false;
    }
  };

  const addPay = async (e) => {
    e.preventDefault();

    if (validate(watcher)) {
      try {
        const model = watcher;
        model.listado_banco_id = watcher.banco_nombre.listado_banco_id;
        pagos.push(model);
        await setChangeFather("pagos", pagos);
      } catch (error) {
        await setChangeFather("pagos", [watcher]);
      }

      await setValue("numero_operacion", 0);
      await setValue(
        "fecha_pago",
        new Date().toLocaleDateString("es-CL").split("-").reverse().join("-")
      );
      await setValue("total_comision", 0);
      await setValue("total_pago", 0);
    }
  };

  const deletePay = async (e, index) => {
    // e.preventDefault();
    pagos.splice(index, 1);
    await setChangeFather("pagos", pagos);
  };

  return (
    <>
      <Header as="h5">
        <Icon name="credit card" />
        <Header.Content>Pagar</Header.Content>
      </Header>
      <Form>
        <Form.Group>
          {!editionMode ? (
            <Form.Input
              fluid
              label="Metodo de Pago"
              placeholder="Selecione"
              value={""}
              readOnly
              width={4}
            />
          ) : (
            <FormFielDropdownSarch
              model={settingDropdowns.metodo_pagos}
              empresaContexto={empresa}
              control={control}
              setValue={setValue}
              width={4}
            />
          )}
          {!editionMode ? (
            <Form.Input
              fluid
              label="Cuentas Bancos"
              placeholder="Selecione"
              value={""}
              readOnly
              width={4}
            />
          ) : (
            <FormFielDropdownSarch
              model={settingDropdowns.cuentas_bancos}
              empresaContexto={empresa}
              control={control}
              setValue={setValue}
              width={4}
            />
          )}
          {!editionMode ? (
            <Form.Input
              fluid
              label="Numero de Operacin"
              placeholder="Selecione"
              value={""}
              readOnly
              width={4}
            />
          ) : (
            <FormFielController
              name={"numero_operacion"}
              title={"Numero de Operacin"}
              control={control}
              errors={errors}
              type="number"
              width={4}
            />
          )}
        </Form.Group>
        <Form.Group>
          {!editionMode ? (
            <Form.Input
              fluid
              label="Fecha de Pago"
              placeholder="Selecione"
              value={""}
              readOnly
              width={4}
            />
          ) : (
            <FormFielController
              name={"fecha_pago"}
              title={"Fecha de Pago"}
              control={control}
              errors={errors}
              type="date"
              width={4}
            />
          )}
          {!editionMode ? (
            <Form.Input
              fluid
              label="Comisin"
              placeholder="Selecione"
              value={""}
              readOnly
              width={4}
            />
          ) : (
            <FormFielController
              name={"total_comision"}
              title={"Comisin"}
              control={control}
              errors={errors}
              type="number"
              width={4}
            />
          )}
          {!editionMode ? (
            <Form.Input
              fluid
              label="Total Pago"
              placeholder="Selecione"
              value={""}
              readOnly
              width={4}
            />
          ) : (
            <FormFielController
              name={"total_pago"}
              title={"Total Pago"}
              control={control}
              errors={errors}
              type="number"
              width={4}
            />
          )}

          <Form.Field width={3}>
            <Button
              as={"a"}
              fluid
              color="teal"
              style={{ marginTop: "23px" }}
              disabled={!editionMode}
              onClick={addPay}
            >
              <Button.Content>Agregar Pago</Button.Content>
            </Button>
          </Form.Field>
        </Form.Group>
      </Form>

      <Table compact unstackable>
        <Table.Header>
          <Table.Row>
            <Table.HeaderCell>Fecha</Table.HeaderCell>
            <Table.HeaderCell>Banco</Table.HeaderCell>
            <Table.HeaderCell>Metodo de Pago</Table.HeaderCell>
            {size.width > 700 && (
              <Table.HeaderCell>Numero de Operacin</Table.HeaderCell>
            )}
            {size.width > 700 && <Table.HeaderCell>Comisin</Table.HeaderCell>}
            <Table.HeaderCell>Total</Table.HeaderCell>
            <Table.HeaderCell>Accin</Table.HeaderCell>
          </Table.Row>
        </Table.Header>
        {/* <pre>{JSON.stringify(watcher, null, 2)}</pre> */}
        <Table.Body>
          {pagos?.map((v, index) => {
            return (
              <Table.Row key={index}>
                <Table.Cell>
                  <Moment format="DD/MM/YYYY">{v?.fecha_pago}</Moment>
                </Table.Cell>
                <Table.Cell>
                  {v?.banco_nombre?.nombre} ****
                  {v?.banco?.numero_cuenta.substr(-4)}
                </Table.Cell>
                <Table.Cell>{v?.metodo?.nombre}</Table.Cell>
                {size.width > 700 && (
                  <Table.Cell>{v?.numero_operacion}</Table.Cell>
                )}
                {size.width > 700 && (
                  <Table.Cell>
                    {moneda?.simbolo} {currency(Number(v?.total_comision))}
                    {" ("}
                    {moneda?.iso}
                    {") "}
                  </Table.Cell>
                )}
                <Table.Cell>
                  {moneda?.simbolo}
                  {currency(Number(v?.total_pago))}
                </Table.Cell>
                <Table.Cell textAlign={"right"}>
                  <Button
                    circular
                    as="a"
                    size="mini"
                    onClick={(e) => {
                      deletePay(e, index);
                    }}
                    disabled={!editionMode}
                    icon="trash"
                  />
                </Table.Cell>
              </Table.Row>
            );
          })}
          <Table.Row>
            <Table.Cell />
            <Table.Cell />
            <Table.Cell />
            {size.width > 700 && <Table.Cell />}
            {size.width > 700 && <Table.Cell />}
            <Table.Cell>
              <Popup
                trigger={
                  <b>
                    {moneda?.simbolo}
                    {currency(Number(totalPago))}
                    {" ("}
                    {moneda?.iso}
                    {") "}
                  </b>
                }
                header="Totales"
                content={
                  <>
                    <p>
                      Pagado {moneda?.simbolo}
                      {currency(Number(totalPago))}
                    </p>
                    <p>
                      Total Operacin {moneda?.simbolo}
                      {currency(Number(total_operacion))}
                      {" ("}
                      {moneda?.iso}
                      {") "}
                    </p>
                    <p>
                      Total Pendiente {moneda?.simbolo}{" "}
                      {currency(total_operacion - totalPago)}
                      {" ("}
                      {moneda?.iso}
                      {") "}
                    </p>
                  </>
                }
                on={["hover", "click"]}
                position="bottom center"
              />
            </Table.Cell>
            <Table.Cell>
              <b>
                {moneda?.simbolo} {currency(total_operacion || 0)}
                {" ("}
                {moneda?.iso}
                {") "}
              </b>
            </Table.Cell>
          </Table.Row>
        </Table.Body>
      </Table>
    </>
  );
};

export default FormularioPago;
