import React, { useEffect, useState, useContext } from "react";
import { useNavigate, useLocation } from "react-router-dom";
import { useForm } from "react-hook-form";
import { Form, Button, Message } from "semantic-ui-react";
import { yupResolver } from "@hookform/resolvers/yup";
import localSevice from "../../services/local";
import FormFielController from "../FormFieldController";
import schema from "./schema";
import handleBackendErrors from "../../libs/handleBackendErrors";
import EmpresaContext from "../../context/EmpresaContext";

const FormularioLocal = ({ state }) => {
  console.log(state);

  let navigate = useNavigate();
  let location = useLocation();
  const { empresa, setEmpresa } = useContext(EmpresaContext);
  const [isLoadding, setIsloadding] = useState(false);
  const [isBackendError, setIsBackendError] = useState(false);
  const {
    control,
    handleSubmit,
    formState: { errors },
  } = useForm({ resolver: yupResolver(schema), defaultValues: state });

  const [data, setData] = useState();
  useEffect(
    async () => {
      if (!!state && state.id) {
        let data = await localSevice.find(state.id);
        return setData(data);
      } else {
        return false;
      }
    },
    { data }
  );

  const close = () => {
    navigate(location.state.backgroundLocation.pathname, {
      state: { reload: { empresa: true } },
    });
  };

  const onSubmit = async (data) => {
    setIsloadding(true);
    if (!!data && data.id) {
      // update
      const update = await localSevice.update(data, data.id);
      localStorage.removeItem("empresa");
      setEmpresa(null);
      close();
      // verificar si es la empresa actual en el contexto del la instancia de local/empresa
    } else {
      //create
      const create = await localSevice.create(data);
      setIsloadding(false);
      if (create?.errors) {
        setIsBackendError(handleBackendErrors(create));
      } else {
        setIsBackendError(null);
        close();
      }
      return create;
    }
  };
  return (
    <Form onSubmit={handleSubmit(onSubmit)} noValidate>
      <FormFielController
        name={"nombre"}
        title={"Nombre"}
        control={control}
        errors={errors}
        width={16}
      />
      <Form.Group>
        <FormFielController
          name={"direccion"}
          title={"Direccion"}
          control={control}
          errors={errors}
          width={16}
        />
        <FormFielController
          name={"ubigeo_id"}
          title={"Ubigeo"}
          control={control}
          errors={errors}
          width={16}
        />
      </Form.Group>
      {Object.keys(errors)?.length ? (
        <Message
          color="orange"
          header="Advertencia"
          list={["Verifique que todos los campos requeridos esten completos."]}
        />
      ) : (
        <></>
      )}

      {isBackendError?.length && (
        <Message
          color="red"
          header="Verifique los siguientes campos"
          list={isBackendError}
        />
      )}

      <Button loading={isLoadding} secondary>
        Guardar
      </Button>
    </Form>
  );
};
export default FormularioLocal;
