import instance from "./_instance";
// Create instance called instance

function show() {
  return new Promise((ok, reject) => {
    instance.get("/empresa").then(
      (res) => {
        ok(res.data);
      },
      (bad) => {
        reject(new Error("bad request"));
      }
    );
  });
}

function find(id) {
  return new Promise((resolve, reject) => {
    instance.get("empresa/" + id).then((res) => {
      resolve(res.data);
    });
  });
}

function create(data) {
  return new Promise((resolve, reject) => {
    instance
      .post("empresa/create", data)
      .then((res) => {
        resolve(res.data);
      })
      .catch((error) => {
        if (error.response) {
          resolve(error.response.data);
        }
      });
  });
}

function update(data, id) {
  return new Promise((resolve, reject) => {
    instance.put("empresa/" + id, data).then((res) => {
      resolve(res.data);
    });
  });
}

function updateLogo(empresa_id, file) {
  const formData = new FormData();
  formData.append("file", file);
  return new Promise((resolve, reject) => {
    instance
      .post(`empresa/logo/${empresa_id}`, formData, {
        "content-type": file.type,
      })
      .then((res) => {
        resolve(res.data);
      })
      .catch((error) => {
        if (error.response) {
          resolve(error.response.data);
        }
      });
  });
}

function updateCertificado(empresa_id, file) {
  const formData = new FormData();
  formData.append("file", file);
  return new Promise((resolve, reject) => {
    instance
      .post(`empresa/certificado/${empresa_id}`, formData, {
        "content-type": file.type,
      })
      .then((res) => {
        resolve(res.data);
      })
      .catch((error) => {
        if (error.response) {
          resolve(error.response.data);
        }
      });
  });
}

function unlockCertificado(empresa_id, pass) {
  return new Promise((resolve, reject) => {
    instance
      .post(`empresa/unlock/${empresa_id}`, { pass })
      .then((res) => {
        resolve(res.data);
      })
      .catch((error) => {
        if (error.response) {
          resolve(error.response.data);
        }
      });
  });
}

export default {
  show,
  find,
  create,
  update,
  updateLogo,
  updateCertificado,
  unlockCertificado,
};
