import React, { useState } from "react";
const Context = React.createContext({});

export function EmpresaContextProvider({ children }) {
  const [impresion, setImpresion] = useState(
    JSON.parse(localStorage.getItem("print_setting")) || { size: "A4" }
  );
  const [logo, setLogo] = useState(
    JSON.parse(window.localStorage.getItem("logo"))
  );
  const [empresa, setEmpresa] = useState(
    JSON.parse(window.localStorage.getItem("empresa"))
  );

  return (
    <Context.Provider
      value={{
        empresa,
        setEmpresa,
        logo,
        setLogo,
        impresion,
        setImpresion,
      }}
    >
      {children}
    </Context.Provider>
  );
}

export default Context;
