import { useEffect, useState, useRef, createRef, useContext } from "react";
import {
  Form,
  Grid,
  Container,
  List,
  Button,
  Icon,
  Modal,
  Table,
  Input,
  Label,
  Confirm,
  Menu,
  Ref,
  Popup,
  Dropdown,
  Checkbox,
  Segment,
} from "semantic-ui-react";
import FormFielDropdownSarch from "../DropdownSearch2";
import FormFielController from "../FormFieldController";
import formatFecha from "../../libs/formatFecha";
import Calcular from "../../libs/calcularPrecios";
// import Dropdown from "../../services/dropdown";
import useWindowSize from "../../hooks/useWindowSize";
import SearchProducto from "../SearchProducto";
import { useNavigate, useLocation } from "react-router-dom";
import ventaSevice from "../../services/ventas";
import handleBackendErrors from "../../libs/handleBackendErrors";
import { toast } from "react-toastify";
import QRCode from "qrcode.react";

import ventaContext from "../../context/VentaContext";
import FormularioPago from "./formulario_pago";
import CriptoJs from "crypto-js";
import currency from "../../libs/currency";

const FormularioProductoHandle = () => {
  return (
    <Form>
      <Form.Group></Form.Group>
    </Form>
  );
};

const FormHeaderVentas = ({
  editionMode,
  watcher,
  settingDropdowns,
  empresa,
  selected,
  handleChangeVenta,
  control,
  setValue,
  errors,
}) => {
  const changeSerie = ({ options, value }) => {
    // const selected = options[value - 1];
    const selected = options.find((v, k) => v.value === value);
    setValue("correlativo", selected?.model?.correlativo || 1);
  };
  return (
    <Form>
      <Form.Group>
        {!editionMode ? (
          <Form.Input
            fluid
            label="Serie"
            placeholder="Seleccione Serie"
            value={watcher?.serie?.descripcion + "-" + watcher?.serie?.serie}
            readOnly
            width={4}
          />
        ) : (
          <FormFielDropdownSarch
            model={settingDropdowns.serie}
            moreRouteParams={{
              tipo_operacion_id: selected?.serie?.tipo_operacion_id,
            }}
            empresaContexto={empresa}
            control={control}
            venta={selected}
            handleChangeVenta={handleChangeVenta}
            setValue={setValue}
            width={4}
            onListenChange={changeSerie}
          />
        )}
        {!editionMode ? (
          <Form.Input
            fluid
            label="Correlativo"
            placeholder="Selecione"
            value={watcher?.correlativo}
            readOnly
            width={2}
          />
        ) : (
          <FormFielController
            name={"correlativo"}
            title={"Correlativo"}
            control={control}
            errors={errors}
            type="number"
            width={2}
          />
        )}
      </Form.Group>
      <Form.Group>
        {!editionMode ? (
          <Form.Input
            fluid
            label="Almacen"
            placeholder="Selecione"
            value={watcher?.almacen?.nombre}
            readOnly
            width={4}
          />
        ) : (
          <FormFielDropdownSarch
            model={settingDropdowns.almacen}
            empresaContexto={empresa}
            control={control}
            venta={selected}
            handleChangeVenta={handleChangeVenta}
            setValue={setValue}
            width={4}
          />
        )}
        {!editionMode ? (
          <Form.Input
            fluid
            label="Condicin de Pago"
            placeholder="Selecione"
            value={watcher?.forma_pago?.nombre}
            readOnly
            width={3}
          />
        ) : (
          <FormFielDropdownSarch
            model={settingDropdowns.forma_pago}
            empresaContexto={empresa}
            control={control}
            venta={selected}
            handleChangeVenta={handleChangeVenta}
            setValue={setValue}
            width={3}
          />
        )}
        {!editionMode ? (
          <Form.Input
            fluid
            label="Fecha de Emisin"
            placeholder="Selecione"
            value={formatFecha.format(watcher?.fecha_emision)}
            readOnly
            width={3}
          />
        ) : (
          <FormFielController
            name={"fecha_emision"}
            title={"Fecha Emisin"}
            control={control}
            errors={errors}
            type="date"
            width={3}
          />
        )}

        {!editionMode ? (
          <Form.Input
            fluid
            label="Fecha de Registro"
            placeholder="Selecione"
            value={formatFecha.format(watcher?.fecha_registro)}
            readOnly
            width={3}
          />
        ) : (
          <FormFielController
            name={"fecha_registro"}
            title={"Fecha Registro"}
            control={control}
            errors={errors}
            type="date"
            width={3}
          />
        )}
        {!editionMode ? (
          <Form.Input
            fluid
            label="Moneda"
            placeholder="Selecione"
            value={watcher?.moneda.nombre}
            readOnly
            width={3}
          />
        ) : (
          <FormFielDropdownSarch
            model={settingDropdowns.moneda}
            empresaContexto={empresa}
            control={control}
            venta={selected}
            handleChangeVenta={handleChangeVenta}
            setValue={setValue}
            width={3}
          />
        )}
      </Form.Group>
    </Form>
  );
};

const ModalHeader = ({
  openModal,
  setOpenModal,
  type,
  watcher,
  editionMode,
  setEditionMode,
  settingDropdowns,
  empresa,
  selected,
  handleChangeVenta,
  changeSerie,
  control,
  setValue,
  errors,
}) => {
  const { handleDefaultVenta } = useContext(ventaContext);

  return (
    <Modal size={"small"} open={openModal} onClose={() => setOpenModal(false)}>
      <Modal.Content>
        {type === "INSERT_PRODUCT" && (
          <Form>
            <Form.Group>
              <Input size="mini" />
            </Form.Group>
          </Form>
        )}

        {type === "header" && (
          <FormHeaderVentas
            editionMode={editionMode}
            watcher={watcher}
            settingDropdowns={settingDropdowns}
            empresa={empresa}
            selected={selected}
            handleChangeVenta={handleChangeVenta}
            changeSerie={changeSerie}
            control={control}
            setValue={setValue}
            errors={errors}
          />
        )}

        {type === "item" && (
          <FormHeaderVentas
            editionMode={editionMode}
            watcher={watcher}
            settingDropdowns={settingDropdowns}
            empresa={empresa}
            selected={selected}
            handleChangeVenta={handleChangeVenta}
            changeSerie={changeSerie}
            control={control}
            setValue={setValue}
            errors={errors}
          />
        )}
      </Modal.Content>
      <Modal.Actions>
        <Checkbox
          toggle
          checked={editionMode}
          onChange={() => {
            setEditionMode(!editionMode);
          }}
          label="Permitir Editar"
        />
        <Button as={"a"} onClick={() => handleDefaultVenta()}>
          Ajustar
        </Button>

        <Button color="blue" onClick={() => setOpenModal(false)}>
          Cerrar
        </Button>
      </Modal.Actions>
    </Modal>
  );
};

const FormularioMovil = ({
  control,
  handleSubmit,
  errors,
  setValue,
  reset,
  watcher,
  editionMode,
  setEditionMode,
  settingDropdowns,
  empresa,
  setEmpresa,
  impresion,
  selected,
  handleChangeVenta,
  state,
  formatPrint,
  setFormatPrint,
}) => {
  const { setDocumento, ventaDefaultContent } = useContext(ventaContext);
  const [itemSelected, setItemSelected] = useState(false);
  const [openModal, setOpenModal] = useState(false);
  const [type, setType] = useState(null);
  const [isOpenItem, setIsOpenItem] = useState({ state: false, id: undefined });
  const { windowSize: size } = useWindowSize();
  const SearchProductosRef = useRef();
  const [isLoadding, setIsloadding] = useState(false);
  const [precioSelected, setPrecioSelected] = useState([{ key: 0 }]);
  const [qrCodeBase64, setQrCodeBase64] = useState(false);
  const [openConfimationListPrice, setOpenConfimationListPrice] =
    useState(false);
  const [isBackendError, setIsBackendError] = useState(false);

  const [addItemStatus, setAddItemStatus] = useState(false);
  const [modoDescuento, setModoDescuento] = useState(false);

  const [isOpenProduct, setIsOpenProduct] = useState(false);
  const [isOpenPagos, setIsOpenPagos] = useState(false);

  let dropdownItem = createRef();
  let navigate = useNavigate();
  let location = useLocation();

  const [productoChoose, setProductoChoose] = useState({
    model: { precio_venta: 0, cantidad: 0, descuento: 0 },
  });

  const calcular = new Calcular(
    "venta",
    empresa.empresa.limite_detraccion,
    empresa.empresa.por_detraccion,
    empresa.empresa.por_percepcion,
    null,
    18,
    8,
    watcher.id
  );

  const clearPresentationItem = () => {
    const element = dropdownItem.current.querySelector(
      '[aria-selected="true"]'
    );
    if (element) {
      dropdownItem.current.querySelector(".clear").click();
    }
  };

  const valid = ({ productoChoose, itemSelected }) => {
    return new Promise((res, reject) => {
      if (
        !!productoChoose &&
        !!itemSelected &&
        !!itemSelected.model &&
        !!productoChoose.model.costo_venta &&
        !!productoChoose.model.cantidad
      ) {
        res({ productoChoose, itemSelected });
      } else {
        reject(false);
      }
    });
  };

  const handleKeyPressTab = (e) => {
    if (e.key === "Tab" || e.key === "Enter") {
      addItem();
      clearPresentationItem();
      openSearchProducto();
    }
  };

  const addItem = async () => {
    setAddItemStatus(true);
    await valid({ productoChoose, itemSelected }).then(
      (res) => {
        const item = productoChoose.model;
        item.codigo = productoChoose.model.codigo;
        item.monto_igv = productoChoose.model.igv;
        item.precio_unitario = productoChoose.model.costo_venta;
        item.monto_precio_unitario = productoChoose.model.costo_venta;
        item.producto = {
          id: itemSelected.model.id,
          nombre: itemSelected.model.nombre,
          peso: itemSelected.model.peso,
          afecto: itemSelected.model.afecto,
        };

        if (!!!item.precios.length) {
          setOpenConfimationListPrice(true);
        }

        const lista = watcher.detalles || [];

        lista.push(item);
        setValue("detalles", lista);
        watchDetalles(watcher.detalles);
        setIsOpenProduct(false);
        setAddItemStatus(false);
        setPrecioSelected(false);
        setProductoChoose({
          model: { precio_venta: 0, cantidad: 0, descuento: 0 },
        });
      },
      (err) => {
        toast.error("complete el formulario de ingreso de producto");
        setAddItemStatus(false);
      }
    );
  };

  const watchDetalles = (detalles, descuento = 0) => {
    const totales = calcular.totales(detalles, descuento);
    Object.keys(totales).map((v) => {
      setValue(v, totales[v]);
      return v;
    });

    // .then((totales) => {
    // Object.keys(totales).map((v, index) => {
    //   setValue(v, totales[v]);
    //   // });
    // });
  };

  const deleteItem = (key) => {
    const lista = watcher.detalles;
    lista.splice(key, 1);
    setValue("detalles", lista);
    watchDetalles(watcher.detalles, watcher.total_descuento_global);
    // console.log("delete firts", lista);
  };

  const handleChangeVentaItemList = (element, index) => {
    console.log(element, index);
    const items = watcher.detalles;
    const item = watcher.detalles[index];

    if (element.name === "cantidad") {
      item.cantidad = element.value || undefined;
    }

    if (element.name === "precio_venta") {
      items[index].precio_venta = element.value || undefined;
    }
    item.is_detail = true;
    const newItem = calcular.item(items[index], item);
    console.log(newItem);
    items[index] = newItem;
    setValue("detalles", items);

    watchDetalles(items, 0);
    // calcular.totales(items);
  };

  const handleOpenModal = (type) => {
    setType(type);
    setOpenModal(true);
  };

  const openSearchProducto = () => {
    SearchProductosRef.current.open();
    // inputRef.current.open();
  };

  const handleProductoChoose = (e, element) => {
    if (!!!element) {
      return false;
    }

    if (element.name === "precio_venta") {
      if (!!!productoChoose && !!!productoChoose.model) {
        productoChoose.model.cantidad = 0;
      }
    }

    const input = {};
    input[element.name] = element.value;
    const model = { ...productoChoose.model, ...input };
    // console.log(">>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>");
    // console.log(itemSelected.model);
    // console.log(model);
    // console.log(">>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>");
    const newModel = calcular.item(itemSelected.model, model);

    // READY

    // console.log(
    //   model.costo_venta,
    //   model.por_utilidad,
    //   model.igv,
    //   model.monto_base_igv,
    //   model.monto_igv
    // );
    const item = productoChoose;
    item.model = newModel;

    setProductoChoose({ ...item });
  };

  const handleProductoChooseDescuentoEfectivo = (e, { name, value }) => {
    const input = {};
    input[name] = Number(value);
    const model = { ...productoChoose.model, ...input };
    const combineModel = { ...itemSelected.model, ...model };
    let newModel = calcular.descuentoEfectivo(combineModel);
    const item = productoChoose;
    item.model = newModel;
    item.model = { ...item.model, ...input };
    setProductoChoose({ ...item });
    // handleProductoChoose(e, {name,value});
  };

  const handleProductoChooseDescuentoPorcentaje = (e, { name, value }) => {
    const input = {};
    input[name] = Number(value);
    const model = { ...productoChoose.model, ...input };
    const combineModel = { ...itemSelected.model, ...model };

    const newModel = calcular.descuentoPorcentaje(combineModel);
    const item = productoChoose;
    item.model = newModel;
    item.model = { ...item.model, ...input };
    setProductoChoose({ ...item });
    // handleProductoChoose(e, { name, value });
  };

  const close = () => {
    if (!!!location.state?.backgroundLocation) {
      setIsloadding(false);
      return false;
    }

    navigate(location.state.backgroundLocation.pathname, {
      state: { reload: { ventas: true } },
    });
  };
  const updateCorrelativo = () => {
    setValue("correlativo", parseFloat(watcher?.correlativo) + 1);
  };

  const resetFormAfterSave = () => {
    updateCorrelativo();
    setValue("detalles", []);
    setValue("pagos", []);
    setValue("total_afecto", 0);
    setValue("total_descuento_global", 0);
    setValue("total_descuento_lineal", 0);
    setValue("total_detraccion", 0);
    setValue("total_documento", 0);
    setValue("total_documento_fin", 0);
    setValue("total_exonerado", 0);
    setValue("total_igv", 0);
    setValue("total_inafecto", 0);
    setValue("total_isc", 0);
    setValue("total_percepcion", 0);
    setValue("total_peso", 0);
  };

  const onSubmit = async (data) => {
    if (!!data && data.id) {
      setIsloadding(true);
      // update
      const update = await toast.promise(ventaSevice.update(data, data.id), {
        pending: "Actualizando...",
        success: "Documento Actualizado ",
        error: "Error de documento ",
      });

      if (!!!state) {
        handleChangeVenta(data);
        if (empresa.empresa.id === update.id) {
          empresa.empresa = update;
          await setEmpresa(empresa);
          await setIsloadding(false);
          close();
        } else {
          close();
          setIsloadding(false);
        }
      } else {
        setIsloadding(false);
      }
      if (impresion?.after_save) {
        setFormatPrint({ status: !formatPrint?.status, from: "update" });
      }
      // verificar si es la empresa actual en el contexto del la instancia de local/empresa
    } else {
      //create
      // const create = await ventaSevice.create(data);

      const create = await toast.promise(ventaSevice.create(data), {
        pending: "Guardando...",
        success: "Documento Guardado ",
        error: "Error de documento ",
      });

      setIsloadding(false);
      if (create?.errors) {
        if (state?.id) {
          handleChangeVenta(data);
        }
        setIsBackendError(handleBackendErrors(create));
      } else {
        // resetFormAfterSave();
        /**Clear All Form Except (Serie, Cliente ,) */
        /**END Clear */
        if (impresion?.after_save) {
          setFormatPrint({ status: !formatPrint?.status, from: "create" });
        } else {
          resetFormAfterSave();
        }
        setIsBackendError(null);
        close();
      }
      return create;
    }
  };

  useEffect(() => {
    if (!!itemSelected) {
      const precios = itemSelected?.model?.precios.map((e, index) => {
        const obj = {
          key: index,
          text: e?.presentacion?.abbr,
          value: e?.id,
          model: e,
        };
        return obj;
      });
      setPrecioSelected(precios);

      setIsOpenProduct(true);
    }

    return () => {
      setPrecioSelected(false);
    };
  }, [itemSelected]);

  useEffect(() => {
    if (selected) {
      setTimeout(() => {
        setFormatPrint({ status: false });
        const qrCodeCanvas = document?.querySelector("canvas");
        setQrCodeBase64(qrCodeCanvas?.toDataURL("image/jpg", 0.3));
        setDocumento(selected);

        console.log(qrCodeCanvas?.toDataURL("image/jpg", 0.3));
      }, 500);
    }
  }, []);

  return (
    <Container style={{ marginTop: "1vh" }}>
      <Form onSubmit={handleSubmit(onSubmit)} noValidate>
        <Grid>
          <Grid.Row style={{ marginTop: -6 }}>
            <Grid.Column>
              <span
                class="ui card fluid"
                onClick={() => {
                  handleOpenModal("header");
                }}
              >
                <div class="content">
                  <div class="header">
                    {!!watcher?.serie?.serie && !!watcher?.correlativo ? (
                      <span>
                        {watcher?.serie?.descripcion} {watcher?.serie?.serie}{" "}
                        {watcher?.correlativo}
                      </span>
                    ) : (
                      <span> Seleccione Documento y Serie</span>
                    )}
                  </div>
                  <div class="meta"> {watcher?.forma_pago?.nombre}</div>
                </div>
              </span>
            </Grid.Column>
          </Grid.Row>
          {/* <Grid.Row>
          <Grid.Column>
            <pre>{JSON.stringify(watcher,null,2)}</pre>
          </Grid.Column>
          </Grid.Row> */}
          <Grid.Row style={{ marginTop: -23 }}>
            <Grid.Column>
              {!editionMode ? (
                <Form.Input
                  fluid
                  label="Cliente"
                  placeholder="Selecione"
                  value={watcher?.cliente?.nombre}
                  readOnly
                  width={16}
                />
              ) : (
                <FormFielDropdownSarch
                  model={settingDropdowns.clientes}
                  empresaContexto={empresa}
                  control={control}
                  venta={selected}
                  handleChangeVenta={handleChangeVenta}
                  setValue={setValue}
                  width={16}
                />
              )}
            </Grid.Column>
          </Grid.Row>
          {editionMode && (
            <Grid.Row style={{ marginTop: -20 }}>
              <Grid.Column width={16}>
                <div class="ui form">
                  <div class="fields">
                    <div class="field w-full">
                      <SearchProducto
                        empresaContexto={empresa}
                        model={settingDropdowns.producto}
                        setItemSelected={setItemSelected}
                        ref={SearchProductosRef}
                      />
                    </div>
                  </div>
                </div>
                {!!isOpenProduct && (
                  <Modal
                    size={"mini"}
                    open={true}
                    onClose={() => setIsOpenProduct(false)}
                  >
                    <Modal.Header>{itemSelected.text}</Modal.Header>
                    <Modal.Content>
                      <Form>
                        <Form.Field width={16}>
                          <Ref innerRef={dropdownItem}>
                            <Dropdown
                              fluid
                              clearable
                              options={precioSelected}
                              onChange={(e, element) => {
                                const item = element?.options?.find(
                                  (item) => element?.value === item.value
                                );
                                setProductoChoose(item);
                              }}
                              selection
                            />
                          </Ref>
                        </Form.Field>

                        <Form.Field width={16}>
                          <Popup
                            content="Precio de Venta (Con IGV Incluido)"
                            trigger={
                              <Input
                                label={
                                  !productoChoose?.model?.val_descuento
                                    ? { icon: "money" }
                                    : {
                                        tag: true,
                                        content: currency(
                                          productoChoose?.model?.precio_venta -
                                            productoChoose?.model?.val_descuento
                                        ),
                                        color: "teal",
                                      }
                                }
                                labelPosition={
                                  !productoChoose?.model?.val_descuento
                                    ? "right corner"
                                    : "right"
                                }
                                type="number"
                                name="precio_venta"
                                value={productoChoose?.model?.precio_venta}
                                onChange={handleProductoChoose}
                                placeholder="Precio"
                              />
                            }
                          />
                        </Form.Field>
                        <Form.Field width={16}>
                          <Popup
                            content="Cantidad de Items"
                            trigger={
                              <Input
                                label={{ icon: "boxes" }}
                                labelPosition="right corner"
                                type="number"
                                name="cantidad"
                                value={productoChoose?.model?.cantidad}
                                placeholder="Cantidad"
                                onChange={handleProductoChoose}
                              />
                            }
                          />
                        </Form.Field>
                        <Form.Field width={16}>
                          <Popup
                            content={`Descuento en ${
                              !modoDescuento ? "Efectivo" : "Porcentaje"
                            }`}
                            trigger={
                              <div>
                                {modoDescuento ? (
                                  <Input
                                    type="number"
                                    name="por_descuento"
                                    value={productoChoose?.model?.por_descuento}
                                    placeholder="Descuento Porcentaje"
                                    onChange={
                                      handleProductoChooseDescuentoPorcentaje
                                    }
                                    onKeyDown={handleKeyPressTab}
                                  >
                                    <input />
                                    <Button
                                      icon={"percent"}
                                      style={{ marginLeft: "10px" }}
                                      onClick={(e) => {
                                        e.preventDefault();
                                        setModoDescuento(!modoDescuento);
                                      }}
                                    />
                                  </Input>
                                ) : (
                                  <Input
                                    type="number"
                                    name="val_descuento"
                                    value={productoChoose?.model?.val_descuento}
                                    placeholder="Descuento Valor"
                                    onChange={
                                      handleProductoChooseDescuentoEfectivo
                                    }
                                    onKeyDown={handleKeyPressTab}
                                  >
                                    <input />
                                    <Button
                                      icon={"money"}
                                      style={{ marginLeft: "10px" }}
                                      onClick={(e) => {
                                        e.preventDefault();
                                        setModoDescuento(!modoDescuento);
                                      }}
                                    />
                                  </Input>
                                )}
                              </div>
                            }
                          />
                        </Form.Field>
                      </Form>
                    </Modal.Content>
                    <Modal.Actions>
                      <Button
                        primary
                        onClick={addItem}
                        disabled={addItemStatus}
                      >
                        Agregar
                      </Button>
                      <Button
                        as={"a"}
                        onClick={() =>
                          setIsOpenItem({
                            state: false,
                            id: undefined,
                          })
                        }
                      >
                        Cerrar
                      </Button>
                    </Modal.Actions>
                  </Modal>
                )}
              </Grid.Column>
            </Grid.Row>
          )}

          {!!isOpenPagos && (
            <Modal
              size={"mini"}
              open={true}
              onClose={() => setIsOpenPagos(false)}
            >
              <Modal.Content>
                <FormularioPago
                  editionMode={editionMode}
                  pagos={watcher.pagos}
                  moneda={watcher.moneda}
                  setChangeFather={setValue}
                  total_operacion={watcher.total_documento_fin}
                />
                <Button primary onClick={() => setIsOpenPagos(false)}>
                  Cerrar
                </Button>
              </Modal.Content>
            </Modal>
          )}

          <Grid.Row style={{ marginBottom: "15vh", marginTop: -20 }}>
            <Grid.Column>
              <Table unstackable>
                <Table.Body>
                  {watcher?.detalles?.map((item, index) => {
                    return (
                      <Table.Row key={index}>
                        <Table.Cell width={8}>
                          {item.producto.nombre}
                        </Table.Cell>
                        <Table.Cell width={3} textAlign="right">
                          <Label basic size="tiny">
                            {item.cantidad} X {watcher.moneda.simbolo}
                            {item.precio_venta}
                          </Label>
                        </Table.Cell>
                        <Table.Cell width={5} textAlign="right">
                          {isOpenItem.state && isOpenItem.id === index && (
                            <Modal
                              size={"mini"}
                              open={true}
                              onClose={() =>
                                setIsOpenItem({ state: false, id: undefined })
                              }
                            >
                              <Modal.Header>
                                {item.producto.nombre}
                              </Modal.Header>
                              <Modal.Content>
                                <Form>
                                  <form class="ui form">
                                    <div class="unstackable two fields">
                                      <div class="field">
                                        <label>Cantidad</label>
                                        <div class="ui input">
                                          <Input
                                            placeholder="Cantidad"
                                            value={item?.cantidad}
                                            onChange={(e, element) =>
                                              handleChangeVentaItemList(
                                                element,
                                                index
                                              )
                                            }
                                            name={"cantidad"}
                                            disabled={!editionMode}
                                          />
                                        </div>
                                      </div>
                                      <div class="field">
                                        <label>Precio de Venta</label>
                                        <div class="ui input">
                                          {!item.val_descuento ? (
                                            item.precio_venta !== null ? (
                                              <Input
                                                value={item.precio_venta}
                                                onChange={(e, element) =>
                                                  handleChangeVentaItemList(
                                                    element,
                                                    index
                                                  )
                                                }
                                                name={"precio_venta"}
                                                disabled={!editionMode}
                                              />
                                            ) : (
                                              (0).toFixed(2)
                                            )
                                          ) : (
                                            <>
                                              <s>
                                                {item.precio_venta
                                                  ? currency(
                                                      Number(item?.precio_venta)
                                                    )
                                                  : (0).toFixed(2)}
                                              </s>
                                              <span
                                                style={{
                                                  marginLeft: "10px",
                                                }}
                                              >
                                                {item.precio_venta
                                                  ? currency(
                                                      Number(
                                                        item?.precio_venta -
                                                          item.val_descuento
                                                      )
                                                    )
                                                  : (0).toFixed(2)}
                                              </span>
                                            </>
                                          )}
                                        </div>
                                      </div>
                                    </div>
                                  </form>
                                </Form>
                              </Modal.Content>
                              <Modal.Actions>
                                <Button
                                  onClick={() =>
                                    setIsOpenItem({
                                      state: false,
                                      id: undefined,
                                    })
                                  }
                                >
                                  Cerrar
                                </Button>
                              </Modal.Actions>
                            </Modal>
                          )}
                          <Button
                            disabled={!editionMode}
                            icon
                            size="mini"
                            onClick={async (e) => {
                              e.preventDefault();
                              await setIsOpenItem({ state: true, id: index });
                            }}
                          >
                            <Icon name="pencil" />
                          </Button>
                          <Button
                            icon
                            size="mini"
                            onClick={() => {
                              deleteItem(index);
                            }}
                            disabled={!editionMode}
                          >
                            <Icon name="trash" />
                          </Button>
                        </Table.Cell>
                      </Table.Row>
                    );
                  })}
                </Table.Body>
              </Table>
            </Grid.Column>
          </Grid.Row>
          <Grid.Row>
            <Segment className="hidden" style={{ marginBottom: "20vh" }}>
              <QRCode
                value={`${
                  process.env.NODE_ENV !== "development "
                   ? process.env.REACT_APP_PRODUCTION_PUBLIC_FRONT
                   :process.env.REACT_APP_DEVELOPER_PUBLIC_FRONT
                }/comprobante?tk=${CriptoJs.AES.encrypt(
                  JSON.stringify({
                    empresa_id: empresa.empresa.id,
                    local_id: empresa.id,
                    tipo_operacion_id: watcher.tipo_operacion_id,
                    serie_id: watcher?.serie?.id,
                    serie: watcher?.serie?.serie,
                    correlativo: watcher?.correlativo,
                    total_documento_fin: watcher?.total_documento_fin,
                  }),
                  "S2bBho4gEUOok7o3IFiUu4BMcGhz5wR2Eu35YTks"
                ).toString()}`}
              />
            </Segment>
 
          </Grid.Row>
        </Grid>

        <ModalHeader
          openModal={openModal}
          setOpenModal={setOpenModal}
          type={type}
          control={control}
          handleSubmit={handleSubmit}
          errors={errors}
          setValue={setValue}
          reset={reset}
          watcher={watcher}
          editionMode={editionMode}
          setEditionMode={setEditionMode}
          settingDropdowns={settingDropdowns}
          empresa={empresa}
          selected={selected}
          handleChangeVenta={handleChangeVenta}
        />
        {size.height > 480 && (
          <Menu vertical fixed="bottom" fluid>
            <Table striped unstackable size="small" color="blue">
              <Table.Body>
                <Table.Row>
                  <Table.Cell width={5}>
                    <p>IGV 18%</p>
                    <p>Total Documento</p>
                  </Table.Cell>
                  <Table.Cell width={6} textAlign="center"></Table.Cell>
                  <Table.Cell width={5} textAlign="right">
                    <p>
                      {watcher?.moneda?.simbolo} {currency(watcher?.total_igv)}
                      {" ("}
                      {watcher?.moneda?.iso}
                      {") "}
                    </p>
                    <p>
                      {watcher?.moneda?.simbolo}{" "}
                      {currency(watcher?.total_documento)}
                      {" ("}
                      {watcher?.moneda?.iso}
                      {") "}
                    </p>
                  </Table.Cell>
                </Table.Row>
                <Table.Row>
                  <Table.Cell />
                  <Table.Cell textAlign="center">
                    <Button
                      loading={isLoadding}
                      disabled={isLoadding}
                      content="Guardar"
                      primary
                      fluid
                    />
                  </Table.Cell>
                  <Table.Cell textAlign="right">
                    {watcher.tipo_operacion_id === 1 && (
                      <Button
                        as={"a"}
                        floated="right"
                        icon={"money"}
                        content={
                          watcher?.moneda?.simbolo +
                          currency(
                            watcher.total_documento_fin - watcher.pago_status
                          )
                        }
                        color={
                          watcher.forma_pago_id === 1
                            ? ""
                            : watcher.pago_status < watcher.total_documento_fin
                            ? "orange"
                            : watcher.pago_status ===
                              watcher.total_documento_fin
                            ? ""
                            : "blue"
                        }
                        onClick={() => setIsOpenPagos(true)}
                      />
                    )}
                  </Table.Cell>
                </Table.Row>
              </Table.Body>
            </Table>
          </Menu>
        )}
      </Form>
    </Container>
  );
};

export default FormularioMovil;
