require("dotenv");
const Path = (type = "any") => {
  if (type === "any") {
    if (process.env.NODE_ENV === "development ") {
      return process.env.REACT_APP_FULLPATH_DEVELOPER;
    } else {
      return process.env.REACT_APP_FULLPATH_PRODUCTION;
    }
  }
  if (type === "public") {
    if (process.env.NODE_ENV === "development ") {
      return process.env.REACT_APP_DEVELOPER_PUBLIC;
    } else {
      return process.env.REACT_APP_PRODUCTION_PUBLIC;
    }
  }
};

export default Path;
