import React, { useEffect, useState, useContext } from "react";
import { useNavigate, useLocation } from "react-router-dom";
import { useForm, Controller } from "react-hook-form";
import {
  Form,
  Button,
  Message,
  TextArea,
  Label,
  Divider,
  Segment,
} from "semantic-ui-react";
import { yupResolver } from "@hookform/resolvers/yup";
import empresaSevice from "../../services/empresa";
import FormFielController from "../FormFieldController";
import FormFielFileController from "../FormFieldFileController";
import FormFielDropdownSarch from "../DropdownSearch2";
import EmpresaContext from "../../context/EmpresaContext";
import schema from "./schema";
import handleBackendErrors from "../../libs/handleBackendErrors";
import settingDropdowns from "../../libs/settingDropdowns";

const FormularioEmpresa = ({ state }) => {
  let navigate = useNavigate();
  let location = useLocation();
  const { empresa, setEmpresa } = useContext(EmpresaContext);
  const [isLoadding, setIsloadding] = useState(false);
  const [isBackendError, setIsBackendError] = useState(false);
  const [isCheckValidation, setCheckValidation] = useState(false);
  const {
    control,
    handleSubmit,
    setValue,
    formState: { errors },
    watch,
  } = useForm({ resolver: yupResolver(schema), defaultValues: state });
  const watcher = watch();
  const [data, setData] = useState();
  useEffect(() => {
    (async () => {
      if (!!state) {
        let data = await empresaSevice.find(state.id);
        return setData(data);
      } else {
        return false;
      }
    })();
  }, []);

  const handleUnlockCertificado = async (e) => {
    e.preventDefault();
    setCheckValidation(true);
    const pass = watcher.certificado_clave;
    const body = await empresaSevice.unlockCertificado(
      empresa.empresa.id,
      pass
    );
    setValue("certificado_validate", body.status);
    setCheckValidation(false);
  };
  const close = () => {
    navigate(location.state.backgroundLocation.pathname, {
      state: { reload: { empresa: true } },
    });
  };

  const onSubmit = async (data) => {
    setIsloadding(true);
    if (!!data && data.id) {
      // update
      const update = await empresaSevice.update(data, data.id);
      if (empresa.empresa.id === update.id) {
        empresa.empresa = update;
        window.localStorage.removeItem("empresa");
        window.localStorage.setItem("empresa", JSON.stringify(empresa));
        setEmpresa(empresa);
        setIsloadding(false);
        close();
      } else {
        close();
      }
      // verificar si es la empresa actual en el contexto del la instancia de local/empresa
    } else {
      //create
      const create = await empresaSevice.create(data);
      setIsloadding(false);
      if (create?.errors) {
        setIsBackendError(handleBackendErrors(create));
      } else {
        setIsBackendError(null);
        close();
      }
      return create;
    }
  };
  return (
    <Form onSubmit={handleSubmit(onSubmit)} noValidate>
      <Form.Group>
        <FormFielDropdownSarch
          model={settingDropdowns.paises}
          empresaContexto={empresa}
          control={control}
          venta={watcher}
          setValue={setValue}
          width={8}
        />
      </Form.Group>
      {/* <pre>{JSON.stringify(watcher, null, 2)}</pre> */}
      {watcher.country_id && (
        <>
          <Form.Group widths={1}>
            <FormFielController
              name={"ruc"}
              title={
                watcher.country_id === 3
                  ? "RUS"
                  : watcher.country_id === 2
                  ? "RIF"
                  : watcher.country_id === 1
                  ? "RUC"
                  : ""
              }
              control={control}
              errors={errors}
              width={8}
            />
            {watcher.id && (
              <Form.Field>
                <FormFielFileController
                  type={"imagen"}
                  name={"logo"}
                  title={"Logo de la Empresa"}
                  value={watcher.logo}
                  service={empresaSevice.updateLogo}
                  setValue={setValue}
                  isContextEmpresa={true}
                />
              </Form.Field>
            )}
          </Form.Group>
          <Form.Group widths={2}>
            <FormFielController
              name={"nombre"}
              title={"Nombre"}
              control={control}
              errors={errors}
              width={8}
            />
            <FormFielController
              name={"nombre_comercial"}
              title={"Nombre Comercial"}
              control={control}
              errors={errors}
              width={8}
            />
          </Form.Group>
          <Form.Group widths={4}>
            <FormFielController
              name={"direccion"}
              title={"Direccion"}
              control={control}
              errors={errors}
              width={8}
            />

            <FormFielDropdownSarch
              model={settingDropdowns.ubigeo}
              empresaContexto={empresa}
              control={control}
              venta={watcher}
              setValue={setValue}
              width={8}
            />
          </Form.Group>
          <Form.Group widths={4}>
            <FormFielController
              name={"telefono"}
              title={"Telefono Fijo"}
              control={control}
              errors={errors}
              width={4}
            />
            <FormFielController
              name={"celular"}
              title={"Telefono Celular"}
              control={control}
              errors={errors}
              width={4}
            />
            <FormFielController
              name={"email"}
              title={"Email"}
              control={control}
              errors={errors}
              width={4}
            />
            <FormFielController
              name={"decimales"}
              title={"Decimales"}
              control={control}
              errors={errors}
              width={4}
            />
          </Form.Group>
          <Divider />
          {watcher.country_id === 1 && (
            <>
              <Form.Group widths={5}>
                <Form.Field width={6}>
                  <FormFielFileController
                    type={"file"}
                    icon={"expeditedssl"}
                    name={"certificado"}
                    title={"Certificado Digital"}
                    value={watcher.certificado}
                    color={
                      watcher.certificado_validate === 0
                        ? "secundary"
                        : watcher.certificado_validate === 1
                        ? "red"
                        : watcher.certificado_validate === 2
                        ? "green"
                        : "secundary"
                    }
                    service={empresaSevice.updateCertificado}
                    empresaContexto={empresa}
                    setValue={setValue}
                    isContextEmpresa={true}
                  />
                </Form.Field>

                <FormFielController
                  name={"certificado_clave"}
                  title={"Contrasea del Certificado Digital"}
                  control={control}
                  errors={errors}
                  width={6}
                  type={"password"}
                />
                <Form.Field>
                  <label>&nbsp;</label>
                  <Button
                    fluid
                    content="Validar Certificado"
                    onClick={handleUnlockCertificado}
                    loading={isCheckValidation}
                  />
                </Form.Field>
              </Form.Group>
              <Form.Group widths={5}>
                <FormFielController
                  name={"usuario_sunat"}
                  title={"Usuario Sunat"}
                  control={control}
                  errors={errors}
                  width={6}
                />
                <FormFielController
                  name={"usuario_sunat_clave"}
                  title={"Clave de Usuario Sunat"}
                  control={control}
                  errors={errors}
                  width={6}
                  type={"password"}
                />

                {/* <FormFielController
          name={"country_id"}
          title={"Pais"}
          control={control}
          errors={errors}
          width={4}
        /> */}
              </Form.Group>
              <Divider />
            </>
          )}
          <Form.Group widths={5}>
            <FormFielController
              name={"igv"}
              title={
                watcher.country_id === 3
                  ? "IVA"
                  : watcher.country_id === 2
                  ? "IVA"
                  : watcher.country_id === 1
                  ? "IGV"
                  : ""
              }
              control={control}
              errors={errors}
              width={3}
            />
            <FormFielController
              name={"agente"}
              title={"Agente"}
              control={control}
              errors={errors}
              width={3}
            />
            <FormFielController
              name={"por_percepcion"}
              title={"% Percepcin"}
              control={control}
              errors={errors}
              width={3}
            />
            <FormFielController
              name={"por_retencion"}
              title={"% Retencion"}
              control={control}
              errors={errors}
              width={3}
            />
          </Form.Group>
          <Divider />
          <Form.Group widths={16}>
            <FormFielController
              name={"por_detraccion"}
              title={"% Detraccin"}
              control={control}
              errors={errors}
              width={3}
            />
            <FormFielController
              name={"limite_detraccion"}
              title={"Limite Detraccin"}
              control={control}
              errors={errors}
              width={3}
            />
            <FormFielController
              name={"cuenta_detraccion"}
              title={"Cuenta Detraccin"}
              control={control}
              errors={errors}
              width={5}
            />
          </Form.Group>
          <Form.Group widths={16}>
            <Form.Field width={16}>
              <Controller
                name={"detalle"}
                control={control}
                render={({ field: { onChange, onBlur, value } }) => (
                  <>
                    <label>Comentario de Pie de Pgina</label>
                    <TextArea
                      rows={12}
                      value={value}
                      onBlur={onBlur}
                      onChange={onChange}
                      placeholder="Comentarios de pie de pagina de las impresiones del modulo de ventas"
                    />
                  </>
                )}
              />
            </Form.Field>
          </Form.Group>
          <Divider />
          <Form.Group roup widths={16}>
            <FormFielController
              name={"smtp_mail_host"}
              title={"Host"}
              control={control}
              errors={errors}
              width={3}
            />
            <FormFielController
              name={"smtp_mail_port"}
              title={"Puerto"}
              control={control}
              errors={errors}
              width={3}
            />
            <FormFielController
              name={"smtp_mail_service"}
              title={"Servicio"}
              control={control}
              errors={errors}
              width={3}
            />
            <FormFielController
              name={"smtp_mail_auth_user"}
              title={"Usuario"}
              control={control}
              errors={errors}
              width={3}
            />
            <FormFielController
              name={"smtp_mail_auth_password"}
              title={"Contrasea"}
              type={"password"}
              control={control}
              errors={errors}
              width={3}
            />
          </Form.Group>
          {Object.keys(errors)?.length ? (
            <Message
              color="orange"
              header="Advertencia"
              list={[
                "Verifique que todos los campos requeridos esten completos.",
              ]}
            />
          ) : (
            <></>
          )}
          {isBackendError?.length && (
            <Message
              color="red"
              header="Verifique los siguientes campos"
              list={isBackendError}
            />
          )}
          <Button loading={isLoadding} secondary>
            Guardar
          </Button>
        </>
      )}
    </Form>
  );
};
export default FormularioEmpresa;
