import { useContext } from "react";
import { toast } from "react-toastify";
import { Card, Button } from "semantic-ui-react";
import EmpresaContext from "../../context/EmpresaContext";
import productosService from "../../services/productos";
const Importador = ({ close }) => {
  const { empresa } = useContext(EmpresaContext);

  const importar = async (type) => {
    close(false);
    const res = await toast.promise(
      productosService.importadorFivip(empresa, type),
      {
        pending: "Importando...",
        success: "Productos Instalados ",
        error: "Error de Importacin ",
      }
    );
    console.log(res);
  };

  return (
    <Card.Group>
      <Card>
        <Card.Content>
          <Card.Header>Ferreteria</Card.Header>
          <Card.Meta>Cantidad +3000 Productos</Card.Meta>
          <Card.Description>
            Contiene: Productos de Tuberias, Drywall, Contruccin de Marcas como
          </Card.Description>
        </Card.Content>
        <Card.Content extra>
          <Button
            basic
            color="blue"
            onClick={() => {
              importar("ferreteria");
            }}
          >
            Importar
          </Button>
        </Card.Content>
      </Card>
      <Card>
        <Card.Content>
          <Card.Header>Market</Card.Header>
          <Card.Meta>Cantidad +1000 Productos</Card.Meta>
          <Card.Description>
            Contiene: Productos Comestibles, Primera Necesidad, Productos de
            Limpieza
          </Card.Description>
        </Card.Content>
        <Card.Content extra>
          <Button
            basic
            color="blue"
            onClick={() => {
              importar("markert");
            }}
          >
            Importar
          </Button>
        </Card.Content>
      </Card>
      <Card>
        <Card.Content>
          <Card.Header>Botica</Card.Header>
          <Card.Meta>Cantidad +1500 Productos</Card.Meta>
          <Card.Description>
            Contiene: Productos Farmaceuticos, Boticas, Articulos de Primera
            Necesidad, Marcas, Componentes
          </Card.Description>
        </Card.Content>
        <Card.Content extra>
          <Button
            basic
            color="blue"
            onClick={() => {
              importar("markert");
            }}
          >
            Importar
          </Button>
        </Card.Content>
      </Card>
      {/* <Card>
        <Card.Content>
          <Card.Header>Jenny Lawrence</Card.Header>
          <Card.Meta>New User</Card.Meta>
          <Card.Description>
            Jenny requested permission to view your contact details
          </Card.Description>
        </Card.Content>
        <Card.Content extra>
          <div className="ui two buttons">
            <Button basic color="blue">
              Approve
            </Button>
          </div>
        </Card.Content>
      </Card> */}
    </Card.Group>
  );
};

export default Importador;
