import instance from "./_instance";
// Create instance called instance

instance.defaults.headers.common["Authorization"] = !!localStorage.getItem(
  "jwt"
)
  ? "Bearer " + localStorage.getItem("jwt")
  : null;

function Login(form) {
  return new Promise((ok, err) => {
    instance.post("/login", form).then(
      (res) => {
        instance.defaults.headers.common["Authorization"] =
          "Bearer " + res.data[0].token;
        ok(res.data[0].token);
      },
      (bad) => {
        err(new Error(bad));
      }
    );
  });
}

function Register(form) {
  return new Promise((ok, err) => {
    instance.post("/register", form).then(
      (res) => {
        instance.defaults.headers.common["Authorization"] =
          "Bearer " + res.data[0].token;
        ok(res.data[0].token);
      },
      (bad) => {
        err(new Error(bad));
      }
    );
  });
}

function Me(token = false) {
  instance.defaults.headers.common["Authorization"] = "Bearer " + token;
  return new Promise((resolve, reject) => {
    instance.get("/me").then((res) => {
      resolve(res.data);
    });
  });
}

function Out() {
  return new Promise((resolve, reject) => {
    instance.get("/logout").then((res) => {
      resolve(res.data);
    });
  });
}

export default { Login, Me, Out, Register };
