import _ from "lodash";
import { useState, useContext, useEffect } from "react";
import { useNavigate, Link, useLocation } from "react-router-dom";

import {
  Button,
  Divider,
  Item,
  Label,
  Icon,
  Dimmer,
  Loader,
  Card,
  Image,
  Grid,
  Container,
  List,
  Segment,
  Header,
} from "semantic-ui-react";
import EmpresaContext from "../../context/EmpresaContext";

import placeholderimgen from "../../assets/placeholder.png";
import empresaSevice from "../../services/empresa";
import LocalService from "../../services/local";
import Path from "../../services/_path";
require("dotenv");

const Welcome = () => {
  const location = useLocation();
  const navigate = useNavigate();
  const PATH_PUBLIC = Path("public");
  const { empresa, setEmpresa } = useContext(EmpresaContext);
  const [openModalEmpresa, setOpenModalEmpresa] = useState(false);
  const [isLoadding, setIsLoadding] = useState(false);
  const [instancias, setInstancias] = useState([]);

  useEffect(
    () => {
      getLocals();
    },
    { instancias }
  );
  const getLocals = async () => {
    setIsLoadding(true);
    const empresas = await LocalService.show();
    setIsLoadding(false);
    return setInstancias(empresas);
  };
  const handleSucursal = async (selected, id) => {
    const local = await LocalService.find(id);
    setEmpresa(local);
    window.window.localStorage.setItem("empresa", JSON.stringify(local));
    navigate({ pathname: "/workspace/app" });
  };

  const handleEditLocal = () => {
    setOpenModalEmpresa(!openModalEmpresa);
  };

  const sourceImage = (path) => {
    try {
      const img = require(path);
      return img;
    } catch (error) {
      return placeholderimgen;
    }
  };

  if (!!location?.state && location?.state?.reload?.empresa) {
    getLocals();
    location.state = null;
  }

  return (
    <Container>
      <Grid.Row style={{ marginTop: "1rem", marginBottom: "1rem" }}>
        {!!instancias?.length && (
          <Link
            to={`/welcome/empresas/create`}
            state={{ backgroundLocation: location }}
          >
            <Button primary floated="right" style={{ marginBottom: "1rem" }}>
              Nueva Empresa
            </Button>
          </Link>
        )}
      </Grid.Row>
      <Grid.Row>
        {isLoadding ? (
          <Dimmer active inverted>
            <Loader size="large">Cargando...</Loader>
          </Dimmer>
        ) : instancias?.length ? (
          <Item.Group divided>
            {instancias.map((empresa, index) => (
              <Item key={index}>
                <Item.Image
                  style={{ maxWidth: 100 }}
                  src={`${PATH_PUBLIC}/storage/${empresa.id}/${empresa.logo}`}
                />
                <Item.Content>
                  <Item.Header as="a">{empresa.nombre}</Item.Header>
                  <Item.Meta>
                    <Link
                      to={`/welcome/empresas/${empresa.id}`}
                      state={{
                        backgroundLocation: location,
                        model: empresa,
                      }}
                    >
                      <Button size="mini" icon="pencil"></Button>
                    </Link>
                    <Link
                      to={`/welcome/locals/create`}
                      state={{
                        backgroundLocation: location,
                        model: { empresa_id: empresa.id },
                      }}
                    >
                      <Button size="mini">
                        <Icon name="plus" />
                        Local | Sucursal
                      </Button>
                    </Link>
                  </Item.Meta>
                  <Item.Extra>
                    <Label color="green">Activo</Label>
                  </Item.Extra>
                  <Item.Description>
                    <List divided verticalAlign="middle">
                      {empresa.locales.map((local) => (
                        <List.Item>
                          <List.Content floated="right">
                            <Button
                              size="small"
                              primary
                              onClick={() => {
                                handleSucursal(empresa, local.id);
                              }}
                            >
                              Ingresar
                            </Button>
                          </List.Content>
                          <List.Content>
                            <Link
                              to={`/welcome/locals/${local.id}`}
                              state={{
                                backgroundLocation: location,
                                model: local,
                              }}
                            >
                              <Button
                                size="mini"
                                icon="pencil"
                                onClick={handleEditLocal}
                              ></Button>
                            </Link>
                            {local.nombre}
                          </List.Content>
                        </List.Item>
                      ))}
                    </List>
                  </Item.Description>
                </Item.Content>
              </Item>
            ))}
          </Item.Group>
        ) : (
          <Segment placeholder>
            <Header icon>
              <Icon name="plus square outline" />
              Para continuar
            </Header>
            <Link
              to={`/welcome/empresas/create`}
              state={{ backgroundLocation: location }}
            >
              <Button primary style={{ marginBottom: "1rem" }}>
                Registra nueva empresa
              </Button>
            </Link>
            {/* <Button primary></Button> */}
          </Segment>
        )}

        {/* {isLoadding ? (
        <Dimmer active inverted>
          <Loader size="large">Cargando...</Loader>
        </Dimmer>
      ) : instancias?.length ? (
        instancias.map((empresa, index) => (
          <Item.Group divided key={index}>
            <Item key={index}>
              <Item.Image
                id={`logo-${empresa.id}`}
                src={`${PATH_PUBLIC}/storage/${empresa.id}/${empresa.logo}`}
              />
              <Item.Content>
                <Item.Header as="a">{empresa.nombre}</Item.Header>
                <Link
                  to={`/welcome/locals/create`}
                  state={{
                    backgroundLocation: location,
                    model: { empresa_id: empresa.id },
                  }}
                >
                  <Button floated="right" size="mini">
                    <Icon name="plus" />
                  </Button>
                </Link>

                <Link
                  to={`/welcome/empresas/${empresa.id}`}
                  state={{
                    backgroundLocation: location,
                    model: empresa,
                  }}
                >
                  <Button floated="right" size="mini" icon="pencil"></Button>
                </Link>
                <Item.Meta>
                  {empresa.nombre_comercial} - Ruc {empresa.ruc}
                </Item.Meta>
                <Item.Extra>
                  <Label>Activo</Label>
                </Item.Extra>
                {empresa.locales.map((local) => (
                  <>
                    <Divider />
                    <Item.Extra key={local.id}>
                      <Button
                        size="mini"
                        primary
                        floated="right"
                        onClick={() => {
                          handleSucursal(empresa, local.id);
                        }}
                      >
                        Ingresar
                        <Icon name="right chevron" />
                      </Button>
                      <Link
                        to={`/welcome/locals/${local.id}`}
                        state={{
                          backgroundLocation: location,
                          model: local,
                        }}
                      >
                        <Button
                          size="mini"
                          icon="pencil"
                          color="teal"
                          floated="right"
                          onClick={handleEditLocal}
                        ></Button>
                      </Link>
                      <Item.Header as="h4">{local.nombre}</Item.Header>
                    </Item.Extra>
                  </>
                ))}
              </Item.Content>
            </Item>
          </Item.Group>
        ))
      ) : (
        <> settings no contenido posees empresa </>
      )} */}
      </Grid.Row>
    </Container>
  );
};

export default Welcome;
