import React from "react";
import { useState } from "react";
import { Navigate } from "react-router-dom";
import {
  Button,
  Form,
  Grid,
  Header,
  Message,
  Segment,
} from "semantic-ui-react";

import useUser from "../hooks/useUser";

const Login = () => {
  const [form, setForm] = useState({
    email: "",
    password: "",
  });

  const { login, isLogged, isLoginLoading, hasLoginError } = useUser();

  const handleForm = (e) => {
    const input = {};
    input[e.target.name] = e.target.value;
    setForm({ ...form, ...input });
  };

  const handleLogin = () => {
    login(form);
  };

  return (
    <Grid textAlign="center" style={{ height: "100vh" }} verticalAlign="middle">
      <Grid.Column style={{ maxWidth: 450 }}>
        <Header as="h2" color="teal" textAlign="center">
          Log-in to your account
        </Header>
        <Form size="large" loading={isLoginLoading}>
          <Segment stacked>
            <Form.Input
              fluid
              icon="user"
              iconPosition="left"
              placeholder="E-mail address"
              name="email"
              onChange={handleForm}
              value={form?.email}
            />
            <Form.Input
              fluid
              icon="lock"
              iconPosition="left"
              placeholder="Password"
              type="password"
              name="password"
              onChange={handleForm}
              value={form?.password}
            />
            {isLogged && <Navigate to="/welcome/empresas" />}
            <Button color="teal" fluid size="large" onClick={handleLogin}>
              Login
            </Button>
          </Segment>
        </Form>
        {hasLoginError && (
          <Message
            error
            header="Error!"
            content="Verifique que haya escrito bien sus credenciales."
          />
        )}
        <Message>
          New to us? <a href="#!">Registrate</a>
        </Message>
      </Grid.Column>
    </Grid>
  );
};
export default Login;
