import React, { useEffect } from "react";
import {
  StyleSheet,
  Font,
  Document,
  Page,
  Text,
  View,
  Image,
} from "@react-pdf/renderer";
import writtenNumber from "written-number";
import formatFecha from "../libs/formatFecha";
import Path from "../services/_path";
require("dotenv");

const ViewFormatA4 = ({ empresa, status, qrCodeBase64, impresion }) => {
console.log({empresa})
  debugger

  const tableHeader = {
    fontSize: "8px",
  };

  const columns = {
    padding: "5",
  };

  const textPadding = {
    padding: "2",
  };
  const textPaddingTicker = {
    padding: "1px",
  };
  const capitalize = (text) => {
    return text.charAt(0).toUpperCase() + text.slice(1);
  };

  return impresion.size === "A4" ? (
    <Document>
      <Page
        size={"A4"}
        style={{
          paddingTop: "35",
          paddingBottom: "65",
          paddingHorizontal: "35",
          fontSize: "9px",
        }}
      >
        {/**First Level Header */}
        <View
          style={{
            display: "flex",
            flexDirection: "row",
            justifyContent: "space-between",
          }}
          fixed
        >
          <View>
            <Image
              source={{
                uri: Path() + "/logo/" + empresa.empresa.id,
              }}
              style={{ width: "100px", maxWidth: "100px", maxHeigh: "100px" }}
            />
          </View>
          <View style={{ padding: "10px", fontSize: "7px" }}>
            <Text style={textPadding}>
              {empresa?.empresa?.nombre_comercial}
            </Text>
            <Text style={textPadding}>{empresa?.empresa?.direccion}</Text>
            <Text style={textPadding}>
              Email: {empresa?.empresa?.email} Tlf: {empresa?.empresa?.telefono}{" "}
              Cel.{empresa?.empresa?.celular}{" "}
            </Text>
          </View>
          <View
            style={{
              border: "1px",
              padding: "10px",
              alignItems: "center",
            }}
          >
            <Text>RUC {empresa?.empresa?.ruc}</Text>
            <Text>{status?.serie?.descripcion}</Text>
            <Text>
              {status?.serie?.serie} {status?.correlativo}
            </Text>
          </View>
        </View>

        {/**Secondary Level Document Details */}
        <View
          style={{
            display: "flex",
            flexDirection: "row",
            alignItems: "space-between",
            border: "1px",
            padding: "5",
            marginTop: "5",
          }}
          fixed
        >
          <View
            style={{
              display: "flex",
              flexDirection: "column",
              justifyContent: "",
              minWidth: "50%",
              maxWidth: "50%",
              fontSize: "7px",
              alignItems: "flex-start",
            }}
          >
            <Text style={textPadding}>
              Cliente: {status?.cliente?.nombre_comercial}
            </Text>
            <Text style={textPadding}>
              {status?.cliente?.tipo_documento_identy_id === 1
                ? "DNI"
                : status?.cliente?.tipo_documento_identy_id === 2
                ? "RUC"
                : "Documento"}
              : {status?.cliente?.numero_documento}
            </Text>
            <Text style={textPadding}>
              Direccin: {status?.cliente?.direccion}
            </Text>
            <Text style={textPadding}>
              Ubigeo: {status?.cliente?.ubigeo?.provincia}{" "}
              {status?.cliente?.ubigeo.distrito}{" "}
              {status?.cliente?.ubigeo.departamento}
            </Text>
          </View>

          <View
            style={{
              display: "flex",
              flexDirection: "column",
              justifyContent: "space-between",
              minWidth: "50%",
              maxWidth: "50%",
              fontSize: "7px",
            }}
          >
            <Text style={textPadding}>
              Fecha Emi: {formatFecha.format(status?.fecha_emision || "")}
            </Text>
            <Text style={textPadding}>
              Fecha Reg: {formatFecha.format(status?.fecha_registro || "")}
            </Text>
            <Text style={textPadding}>Condicin: Crdito</Text>
            <Text style={textPadding}>&nbsp;</Text>
          </View>
        </View>
        {/**3er Level Table Header */}
        <View
          style={{
            display: "flex",
            flexDirection: "row",
            alignItems: "space-evenly",
            marginTop: "5px",
          }}
          fixed
        >
          <View
            style={{
              ...columns,
              ...{
                width: 50,
                borderBottom: "1px",
                borderLeft: "1px",
                borderTop: "1px",
              },
            }}
          >
            <Text
              style={{
                ...tableHeader,
                ...{ textAlign: "center" },
              }}
            >
              Cdigo
            </Text>
          </View>

          <View
            style={{
              ...columns,
              ...{
                width: 310,
                borderBottom: "1px",
                borderLeft: "1px",
                borderTop: "1px",
              },
            }}
          >
            <Text
              style={{
                ...tableHeader,
              }}
            >
              Descripcion
            </Text>
          </View>

          <View
            style={{
              ...columns,
              ...{
                width: 50,
                borderBottom: "1px",
                borderLeft: "1px",
                borderTop: "1px",
              },
            }}
          >
            <Text
              style={{
                ...tableHeader,
                ...{ textAlign: "right" },
              }}
            >
              Cant.
            </Text>
          </View>

          <View
            style={{
              ...columns,
              ...{
                width: 70,
                borderBottom: "1px",
                borderLeft: "1px",
                borderTop: "1px",
              },
            }}
          >
            <Text
              style={{
                ...tableHeader,
                ...{ textAlign: "right" },
              }}
            >
              Present.
            </Text>
          </View>

          <View
            style={{
              ...columns,
              ...{
                width: 70,
                borderBottom: "1px",
                borderLeft: "1px",
                borderTop: "1px",
              },
            }}
          >
            <Text
              style={{
                ...tableHeader,
                ...{ textAlign: "right" },
              }}
            >
              Precio
            </Text>
          </View>
          <View
            style={{
              ...columns,
              ...{
                width: 70,
                borderBottom: "1px",
                borderLeft: "1px",
                borderTop: "1px",
                borderRight: "1px",
              },
            }}
          >
            <Text
              style={{
                ...tableHeader,
                ...{ textAlign: "right" },
              }}
            >
              Importe
            </Text>
          </View>
        </View>
        {status?.detalles.map((e, i, arr) => {
          const last = arr.length;
          return (
            <View
              key={i}
              style={{
                display: "flex",
                flexDirection: "row",
                marginBottom: "1",
                fontSize: "9px",
              }}
            >
              <View
                style={{
                  ...columns,
                  ...{
                    width: 50,
                    marginTop: "-1px",
                    borderLeft: "1px",
                    borderBottom: last === i + 1 ? "1px" : 0,
                  },
                }}
              >
                <Text
                  style={{
                    ...tableHeader,
                    ...{ textAlign: "center" },
                  }}
                >
                  {e?.producto?.codigo}
                </Text>
              </View>

              <View
                style={{
                  ...columns,
                  ...{
                    width: 310,
                    marginTop: "-1px",
                    borderLeft: "1px",
                    borderBottom: last === i + 1 ? "1px" : 0,
                  },
                }}
              >
                <Text
                  style={{
                    ...tableHeader,
                  }}
                >
                  {e?.producto?.nombre}
                </Text>
              </View>

              <View
                style={{
                  ...columns,
                  ...{
                    width: 50,
                    marginTop: "-1px",
                    borderLeft: "1px",
                    borderBottom: last === i + 1 ? "1px" : 0,
                  },
                }}
              >
                <Text
                  style={{
                    ...tableHeader,
                    ...{ textAlign: "right" },
                  }}
                >
                  {e?.cantidad}
                </Text>
              </View>

              <View
                style={{
                  ...columns,
                  ...{
                    width: 70,
                    marginTop: "-1px",
                    borderLeft: "1px",
                    borderBottom: last === i + 1 ? "1px" : 0,
                  },
                }}
              >
                <Text
                  style={{
                    ...tableHeader,
                    ...{ textAlign: "right" },
                  }}
                >
                  {e?.presentacion?.nombre}
                </Text>
              </View>

              <View
                style={{
                  ...columns,
                  ...{
                    width: 70,
                    marginTop: "-1px",
                    borderLeft: "1px",
                    borderBottom: last === i + 1 ? "1px" : 0,
                  },
                }}
              >
                <Text
                  style={{
                    ...tableHeader,
                    ...{ textAlign: "right" },
                  }}
                >
                  {e?.precio_unitario?.toFixed(3)}
                </Text>
              </View>
              <View
                style={{
                  ...columns,
                  ...{
                    width: 70,
                    marginTop: "-2px",
                    borderLeft: "1px",
                    borderRight: "1px",
                    borderBottom: last === i + 1 ? "1px" : 0,
                  },
                }}
              >
                <Text
                  style={{
                    ...tableHeader,
                    ...{ textAlign: "right" },
                  }}
                >
                  {(e?.cantidad * e?.precio_unitario).toFixed(3)}
                </Text>
              </View>
            </View>
          );
        })}

        {/**Last Level Footer */}
        <View
          style={{
            display: "flex",
            justifyContent: "space-between",
            flexDirection: "row",
            marginBottom: "1",
            fontSize: "9px",
            padding: "10",
          }}
        >
          <View>
            <Text style={textPadding}>
              Son:{" "}
              {capitalize(
                writtenNumber(status?.total_documento_fin, { lang: "es" })
              )}{" "}
              con 00/100 {status?.moneda?.nombre.toLowerCase()}
            </Text>

            <Image
              source={{ uri: qrCodeBase64 }}
              style={{ width: "80px", marginBottom: "10px" }}
            />

            {status?.detalle && (
              <Text style={textPadding}>
                {`Observacin: 
              ${status?.detalle}`}
              </Text>
            )}

            {status?.ubigeo_partida && (
              <Text style={textPadding}>
                {`Partida: ${status?.ubigeo_partida?.provincia} ${status?.ubigeo_partida.distrito} ${status?.ubigeo_partida.distrito} - ${status?.direccion_partida}`}
              </Text>
            )}

            {status?.ubigeo_destino && (
              <Text style={textPadding}>
                {`Destino: ${status?.ubigeo_destino?.provincia} ${status?.ubigeo_destino.distrito} ${status?.ubigeo_destino.distrito} - ${status?.direccion_destino}`}
              </Text>
            )}

            {status?.transportista_id && (
              <Text style={textPadding}>
                {`Transportista:
                Razn Social: ${status?.transportista.nombre_comercial}
                Documento: ${status?.transportista.numero_documento}
                Telfono ${status?.transportista.telefono}`}
              </Text>
            )}

            {status?.vehiculo_id && (
              <Text style={textPadding}>
                {`Vehiculo:
                Marca: ${status?.vehiculo.marca}
                Placa: ${status?.vehiculo.placa}`}
              </Text>
            )}

            {status?.motivos_sunat_guia_remision && (
              <Text style={textPadding}>
                {`Motivo: ${status?.motivos_sunat_guia_remision.descripcion}`}
              </Text>
            )}

            {status?.documento_relacion &&
              status?.documento_relacion.map((vv, k) => (
                <Text style={textPadding}>
                  {`Documento Relacionado: ${
                    (vv?.documento_relacion.uniqueserie).split("-")[2]
                  } ${vv?.documento_relacion.correlativo}`}
                </Text>
              ))}

            {status?.numero_expediente && (
              <Text style={textPadding}>
                {`Numero de Expediente: ${status?.numero_expediente}`}
              </Text>
            )}

            {status?.orden_compra && (
              <Text style={textPadding}>
                {`Orden de Compra: ${status?.orden_compra}`}
              </Text>
            )}

            {status?.codigo_unidad_ejecutora && (
              <Text style={textPadding}>
                {`Cdigo de unidad Ejecutora: ${status?.codigo_unidad_ejecutora}`}
              </Text>
            )}

            {status?.numero_proceso_seleccion && (
              <Text style={textPadding}>
                {`Numero de Proceso de Seleccin: ${status?.numero_proceso_seleccion}`}
              </Text>
            )}

            {status?.numero_contrato && (
              <Text style={textPadding}>
                {`Numero de Contrato: ${status?.numero_contrato}`}
              </Text>
            )}

            {empresa?.empresa?.detalle && (
              <Text style={textPadding}>{empresa?.empresa?.detalle} </Text>
            )}
          </View>

          <View
            style={{
              fontSize: "8px",
              flexDirection: "row",
              justifyContent: "space-between",
            }}
          >
            <View style={{ flexDirection: "column", width: "50px" }}>
              {status?.tipo_operacion_id === 3 && (
                <Text style={{ ...textPadding, ...{ borderBottom: "1px" } }}>
                  Peso
                </Text>
              )}
              <Text style={{ ...textPadding, ...{ borderBottom: "1px" } }}>
                Afecto
              </Text>
              <Text style={{ ...textPadding, ...{ borderBottom: "1px" } }}>
                Inafecto
              </Text>
              <Text style={{ ...textPadding, ...{ borderBottom: "1px" } }}>
                ISC
              </Text>
              <Text style={{ ...textPadding, ...{ borderBottom: "1px" } }}>
                IGV
              </Text>
              <Text style={{ ...textPadding, ...{ borderBottom: "1px" } }}>
                Total
              </Text>
              <Text style={{ ...textPadding, ...{ borderBottom: "1px" } }}>
                Percepcin
              </Text>
              <Text style={{ ...textPadding, ...{ borderBottom: "1px" } }}>
                Total
              </Text>
            </View>
            <View
              style={{
                flexDirection: "column",
                width: "80px",
                textAlign: "right",
              }}
            >
              {status?.tipo_operacion_id === 3 && (
                <Text style={{ ...textPadding, ...{ borderBottom: "1px" } }}>
                  {status?.total_peso?.toFixed(3)}
                </Text>
              )}
              <Text style={{ ...textPadding, ...{ borderBottom: "1px" } }}>
                {status?.moneda?.simbolo} {status?.total_afecto?.toFixed(3)}
              </Text>
              <Text style={{ ...textPadding, ...{ borderBottom: "1px" } }}>
                {status?.moneda?.simbolo} {status?.total_inafecto?.toFixed(3)}
              </Text>
              <Text style={{ ...textPadding, ...{ borderBottom: "1px" } }}>
                {status?.moneda?.simbolo} {status?.total_isc?.toFixed(3)}
              </Text>
              <Text style={{ ...textPadding, ...{ borderBottom: "1px" } }}>
                {status?.moneda?.simbolo} {status?.total_igv?.toFixed(3)}
              </Text>
              <Text style={{ ...textPadding, ...{ borderBottom: "1px" } }}>
                {status?.moneda?.simbolo} {status?.total_documento?.toFixed(3)}
              </Text>
              <Text style={{ ...textPadding, ...{ borderBottom: "1px" } }}>
                {status?.moneda?.simbolo} {status?.total_percepcion?.toFixed(3)}
              </Text>
              <Text style={{ ...textPadding, ...{ borderBottom: "1px" } }}>
                {status?.moneda?.simbolo}{" "}
                {status?.total_documento_fin?.toFixed(3)}
              </Text>
            </View>
            {/* <Text style={textPadding}>
              Afecto: {status?.moneda?.simbolo}{" "}
              {status?.total_afecto.toFixed(3)}
            </Text>
            <Text style={textPadding}>
              Inafecto: {status?.moneda?.simbolo}{" "}
              {status?.total_inafecto.toFixed(3)}
            </Text>
            <Text style={textPadding}>
              ISC: {status?.moneda?.simbolo} {status?.total_isc.toFixed(3)}
            </Text>
            <Text style={textPadding}>
              IGV/IVA: {status?.moneda?.simbolo} {status?.total_igv}{" "}
            </Text>
            <Text style={textPadding}>
              Total Descuentos: {status?.moneda?.simbolo}{" "}
              {status?.total_descuento.toFixed(3)}
            </Text>
            <Text style={textPadding}>
              Total Documento: {status?.moneda?.simbolo}{" "}
              {status?.total_documento.toFixed(3)}
            </Text>
            <Text style={textPadding}>
              Percepcin: {status?.moneda?.simbolo}{" "}
              {status?.total_percepcion.toFixed(3)}
            </Text>
            <Text style={textPadding}>
              Total a Pagar: {status?.moneda?.simbolo}{" "}
              {status?.total_documento_fin.toFixed(3)}
            </Text> */}
          </View>
        </View>
        <View
          style={{
            position: "absolute",
            fontSize: 12,
            bottom: 30,
            left: 0,
            right: 0,
            textAlign: "center",
            color: "grey",
          }}
          fixed
        >
          <Text
            style={{ color: "black", fontSize: "10px", top: 50 }}
            render={({ pageNumber, totalPages }) =>
              `${pageNumber} / ${totalPages}
              `
            }
          ></Text>
          <Text style={{ fontSize: "7px" }}>
            Consulta este comprobante en FivipSystem.com/comprobante
          </Text>

          <Image
            source={{
              uri: Path() + "/logo/footer_fivip",
            }}
            style={{
              width: "100px",
              maxWidth: "100px",
              maxHeigh: "100px",
              left: 465,
            }}
          />
        </View>
      </Page>
    </Document>
  ) : impresion.size === "88mm" ? (
    <Document>
      <Page
        size={[72.1, 297]}
        style={{
          fontSize: "3px",
        }}
      >
        <View
          style={{ maxWidth: "72.1 dpi", maxHeight: "297 dpi", padding: "3px" }}
        >
          <View
            style={{
              alignItems: "center",
              borderBottom: "0.15px",
            }}
          >
            <Image
              source={{
                uri: Path() + "/logo/" + empresa.empresa.id,
              }}
              style={{ width: "30px", maxWidth: "100px", maxHeigh: "100px" }}
            />
            <Text>{empresa?.empresa?.nombre_comercial}</Text>
            <Text>{empresa?.empresa?.direccion}</Text>

            <Text>Email: {empresa?.empresa?.email}</Text>
            <Text>Tlf: {empresa?.empresa?.telefono}</Text>
            <Text>Cel: {empresa?.empresa?.celular}</Text>
            <Text>RUC: {empresa?.empresa?.ruc}</Text>
            <Text>
              Fecha Emisin: {formatFecha.format(status.fecha_emision)}
            </Text>
            <Text>Condicin: {status.forma_pago.nombre}</Text>
          </View>

          {status?.detalles.map((e, i, arr) => {
            return (
              <View>
                <Text style={{ marginTop: "1px" }}>
                  {e?.producto.codigo} {e?.producto.nombre}
                </Text>

                <View
                  style={{
                    display: "flex",
                    flexDirection: "row",
                    justifyContent: "space-between",
                    marginTop: "1px",
                  }}
                >
                  <Text>
                    Cant. {e?.cantidad} {e?.presentacion.abbr}
                  </Text>
                  <Text>
                    {status.moneda.simbolo}{" "}
                    {(e?.monto_precio_unitario).toFixed(3)}
                  </Text>
                  <Text>
                    {status.moneda.simbolo}{" "}
                    {(e?.monto_precio_unitario * e?.cantidad)?.toFixed(3)}
                  </Text>
                </View>
              </View>
            );
          })}
        </View>

        <View
          style={{
            display: "flex",
            justifyContent: "space-between",
            flexDirection: "row",
            fontSize: "3px",
          }}
        >
          <View></View>

          <View
            style={{
              fontSize: "3px",
              flexDirection: "row",
              justifyContent: "space-between",
            }}
          >
            <View
              style={{
                flexDirection: "column",
                width: "20px",
                fontSize: "3px",
              }}
            >
              <Text style={textPaddingTicker}>Afecto</Text>
              <Text style={textPaddingTicker}>Inafecto</Text>
              <Text style={textPaddingTicker}>ISC</Text>
              <Text style={textPaddingTicker}>IGV</Text>
              <Text style={textPaddingTicker}>Total</Text>
              <Text style={textPaddingTicker}>Percepcin</Text>
              <Text style={textPaddingTicker}>Total</Text>
            </View>
            <View
              style={{
                flexDirection: "column",
                width: "25px",
                textAlign: "right",
                fontSize: "3px",
              }}
            >
              <Text style={textPaddingTicker}>
                {status?.moneda?.simbolo} {status?.total_afecto?.toFixed(3)}
              </Text>
              <Text style={textPaddingTicker}>
                {status?.moneda?.simbolo} {status?.total_inafecto?.toFixed(3)}
              </Text>
              <Text style={textPaddingTicker}>
                {status?.moneda?.simbolo} {status?.total_isc?.toFixed(3)}
              </Text>
              <Text style={textPaddingTicker}>
                {status?.moneda?.simbolo} {status?.total_igv?.toFixed(3)}
              </Text>
              <Text style={textPaddingTicker}>
                {status?.moneda?.simbolo} {status?.total_documento?.toFixed(3)}
              </Text>
              <Text style={textPaddingTicker}>
                {status?.moneda?.simbolo} {status?.total_percepcion?.toFixed(3)}
              </Text>
              <Text style={textPaddingTicker}>
                {status?.moneda?.simbolo}{" "}
                {status?.total_documento_fin?.toFixed(3)}
              </Text>
            </View>
          </View>
        </View>
        <View style={{ alignItems: "center" }}>
          <Text style={textPadding}>
            Son:{" "}
            {capitalize(
              writtenNumber(status?.total_documento_fin, { lang: "es" })
            )}{" "}
            con 00/100 {status?.moneda?.nombre.toLowerCase()}
          </Text>

          <Image
            source={{ uri: qrCodeBase64 }}
            style={{ width: "18px", marginBottom: "3px" }}
          />

          {status?.detalle && (
            <Text style={textPadding}>
              {`Observacin: 
              ${status?.detalle}`}
            </Text>
          )}

          <Text style={{ fontSize: "2px" }}>
            Consulta este comprobante en FivipSystem.com/comprobante
          </Text>

          <Image
            source={{
              uri: Path() + "/logo/footer_fivip",
            }}
            style={{
              width: "15px",
              maxWidth: "100px",
              maxHeigh: "100px",
            }}
          />
        </View>
      </Page>
    </Document>
  ) : (
    <Document>
      <Page size={"A4"}>
        <Text>Contenido no disponible</Text>
      </Page>
    </Document>
  );
};

// // Font.register({
// //   family: "Oswald",
// //   src: "https://fonts.gstatic.com/s/oswald/v13/Y_TKV6o8WovbUd3m_X9aAA.ttf",
// // });

// const styles = StyleSheet.create({
//   title: {
//     fontSize: "9px",
//     textAlign: "center",
//   },
// });
export default ViewFormatA4;
