import { useContext } from "react";
import { Form, Header, Segment, Button } from "semantic-ui-react";
import FormFielController from "../FormFieldController";
import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
import EmpresaContext from "../../context/EmpresaContext";
import schema from "./schema_report";

import formatFecha from "../../libs/formatFecha";
import ventas from "../../services/ventas";

const FormularioReportVenta = () => {
  const empresa = useContext(EmpresaContext);
  const {
    control,
    handleSubmit,
    formState: { errors },
    setValue,
    reset,
    watch,
  } = useForm({
    resolver: yupResolver(schema),
    defaultValues: {
      _desde: formatFecha.formatToInputSemantic(new Date()),
      _hasta: formatFecha.formatToInputSemantic(new Date()),
      _anulado: true,
      _ventas: true,
      _cotizacion: false,
      _guia_remision: false,
      _nota_credito: false,
    },
  });

  const watcher = watch();

  const param = {
    _ventas: 1,
    _cotizacion: 2,
    _guia_remision: 3,
    _nota_credito: 4,
    _anulado: 5,
  };

  const getReport = async () => {
    const query = {};
    let keys = Object.keys(watcher);

    query.filter = [["tipo_operacion_id", "in", []]];

    for (let i = 0; i < keys.length; i++) {
      const e = watcher[keys[i]];
      if (keys[i] === "_desde") {
        query.filter.push(["fecha_emision", ">=", watcher[keys[i]]]);
      } else if (keys[i] === "_hasta") {
        query.filter.push(["fecha_emision", "<=", watcher[keys[i]]]);
      } else {
        console.log(e, keys[i]);
        if (e) {
          query.filter[0][2].push(param[keys[i]]);
        }
      }
    }

    await ventas.reporte(empresa.empresa, query);
  };

  return (
    <Form>
      <Form.Group>
        <FormFielController
          name={"_desde"}
          title={"Desde"}
          control={control}
          errors={errors}
          type="date"
          width={5}
        />
        <FormFielController
          name={"_hasta"}
          title={"Hasta"}
          control={control}
          errors={errors}
          type="date"
          width={5}
        />
      </Form.Group>

      <Header as={"h3"}>Tipo de Documento</Header>

      <Form.Group>
        <Form.Checkbox
          label="Documentos de Ventas"
          name="_ventas"
          checked={watcher._ventas}
          onChange={() => {
            setValue("_ventas", !watcher._ventas);
          }}
        />
        <Form.Checkbox
          label="Cotizaciones"
          name="_cotizacion"
          checked={watcher._cotizacion}
          onChange={() => {
            setValue("_cotizacion", !watcher._cotizacion);
          }}
        />
        <Form.Checkbox
          label="Guia de Remisin"
          name="_guia_remision"
          checked={watcher._guia_remision}
          onChange={() => {
            setValue("_guia_remision", !watcher._guia_remision);
          }}
        />

        <Form.Checkbox
          label="Nota de Crdito"
          name="_nota_credito"
          checked={watcher._nota_credito}
          onChange={() => {
            setValue("_nota_credito", !watcher._nota_credito);
          }}
        />

        <Form.Checkbox
          label="Anulados"
          name="_anulado"
          checked={watcher._anulado}
          onChange={() => {
            setValue("_anulado", !watcher._anulado);
          }}
        />
      </Form.Group>
      {/* 
      <Segment>
        <pre>{JSON.stringify(watcher, null, 2)}</pre>
      </Segment> */}

      <div style={{ marginTop: "5vh" }}>
        <Button
          content="Generar Reporte"
          fluid
          primary
          onClick={() => getReport()}
        />
      </div>
    </Form>
  );
};
export default FormularioReportVenta;
