import React, { useState } from "react";
import { Input, Dropdown, Label, Icon, Segment } from "semantic-ui-react";

const Filter = ({ query, setQuery, child }) => {
  const [opened, setOpened] = useState(false);
  const handleClick = () => {
    setOpened(!opened);
  };
  return (
    <>
      <Dropdown
        onClick={() => {
          handleClick();
        }}
        text="Filtrar"
        button
        labeled
        icon="filter"
        open={opened}
        className="icon mini"
      >
        <Dropdown.Menu
          onClick={(e) => {
            e.stopPropagation();
          }}
          style={{ width: "45vh" }}
        >
          <div style={{ padding: "15px" }}>{child}</div>
        </Dropdown.Menu>
      </Dropdown>
    </>
  );
};

export default Filter;
