import instance from "./_instance";
// Create instance called instance

function show() {
  return new Promise((ok, reject) => {
    instance.get("/local").then(
      (res) => {
        ok(res.data);
      },
      (bad) => {
        reject(new Error("bad request"));
      }
    );
  });
}

function find(id) {
  return new Promise((resolve, reject) => {
    instance.get("local/" + id).then((res) => {
      resolve(res.data);
    });
  });
}

function create(data) {
  return new Promise((resolve, reject) => {
    instance
      .post("precio", data)
      .then((res) => {
        resolve(res.data);
      })
      .catch((error) => {
        if (error.response) {
          resolve(error.response.data);
        }
      });
  });
}

function update(data, empresa_id, id) {
  return new Promise((resolve, reject) => {
    instance.put("precio/" + empresa_id + "/" + id, data).then((res) => {
      resolve(res.data);
    });
  });
}

function destroy(data, empresa_id, id) {
  instance.delete("precio/" + empresa_id + "/" + id, data).then((res) => {
    return res.data;
  });
}

export default { show, find, create, update, destroy };
