import instance from "./_instance";
import qs from "qs";
import axios from "axios";

import Localbase from "localbase";
// Create instance called instance

const db = new Localbase("db");
db.config.debug = false;

function show(empresa_id, query = { order: { id: "DESC" } }) {
  const str = qs.stringify(query);
  return new Promise((ok, reject) => {
    instance.get(`/cliente/${empresa_id}?${str}`).then(
      async (res) => {
        // console.log(">>RESPUESTA DEL SHOW ", res.data.data);
        db.collection("contactos")
          .set(res.data.data)
          .then((contactos) => {
            ok(contactos.data);
          });
      },
      (bad) => {
        reject(false);
      }
    );
  });
}

function find(empresa_id, id) {
  return new Promise((resolve, reject) => {
    instance.get("cliente/" + empresa_id + "/" + id).then((res) => {
      resolve(res.data);
    });
  });
}

function create(data) {
  return new Promise((resolve, reject) => {
    instance
      .post("cliente", data)
      .then(async (res) => {
        db.collection("contactos")
          .set(res.data)
          .then((_contacto) => {
            console.log(_contacto);
            return resolve(res.data);
          });
        resolve(res.data);
      })
      .catch(async (error) => {
        debugger;
        //catch error de validacion de backend online
        if (error.response) {
          console.log(error.response);
          resolve(error.response.data);
        } else {
          alert("error desconexion");
        }
      });
  });
}

function update(data, id) {
  return new Promise((resolve, reject) => {
    instance
      .put("cliente/" + id, data)
      .then(async (res) => {
        await db
          .collection("contactos")
          .doc({ id: res.data.id })
          .update(res.data);

        resolve(res.data);
      })
      .catch((error) => {
        resolve(data);
      });
  });
}

function getDni(data) {
  return new Promise((resolve, reject) => {
    axios.post("https://api.nubefa.com/api/dni", data).then(
      (res) => {
        resolve(res);
      },
      (err) => {
        reject(false);
      }
    );
  });
}

function getRuc(data) {
  return new Promise((resolve, reject) => {
    axios.post("https://api.nubefa.com/api/ruc", data).then(
      (res) => {
        resolve(res);
      },
      (err) => {
        reject(false);
      }
    );
  });
}

export default { show, find, create, update, getDni, getRuc };
