import React, { useContext, useEffect, useState } from "react";

import { Dropdown, Button, Form, Segment, Icon } from "semantic-ui-react";
import { Link, useLocation, useNavigate } from "react-router-dom";
import { Controller, get } from "react-hook-form";

import dropdownService from "../services/dropdown";

const parseData = (data, key) => {
  if (data && !!!data[0]) {
    return false;
  }
  const res = data?.map((e, index) => {
    if (key.concat) {
      const arr = key.concat.map((i) => {
        if (i === "email") return "<" + e[i] + ">";
        return e[i];
      });

      return {
        text: arr.join(" "),
        value: Number(e[key?.id]),
        key: index,
        model: e,
      };
    } else {
      return {
        text: e[key?.text],
        value: Number(e[key?.id]),
        key: index,
        model: e,
      };
    }
  });

  return res;
};

const getOptions = async (
  empresa,
  key,
  endPoint,
  params,
  searchQuery = null,
  moreRouteParams,
  venta
) => {
  let query;

  if (params?.empresa_id) {
    endPoint += "/" + empresa.empresa.id;
  }

  if (params?.local_id) {
    console.log(empresa.id);
    endPoint += "/" + empresa.id;
  }

  if (endPoint.search("undefined") > 0 ? true : false) {
  }

  if (moreRouteParams) {
    const values = Object.keys(moreRouteParams).map((v) => {
      return venta[v];
    });
    for (let i = 0; i < values.length; i++) {
      endPoint += "/" + values[i];
    }
  }

  if (typeof searchQuery === "string" && searchQuery.length < 3) {
    return false;
  }

  if (typeof searchQuery === "string" && searchQuery.length > 3) {
    query = { filter: [[key.text, "like", "%" + searchQuery + "%"]] };
  }

  if (typeof searchQuery === "object") {
    query = searchQuery;
  }

  query = searchQuery ? query : null;

  const response = await dropdownService.show(endPoint, query);
  let data = await parseData(response.data, key);

  if (endPoint.search("/vehiculo") > 0) {
    console.log(key, endPoint, params, searchQuery, moreRouteParams);
  }

  return data;
};

const FormFielDropdownSarch = ({
  model,
  moreRouteParams,
  empresaContexto,
  control,
  venta,
  setValue,
  width,
  autoFocus,
  onListenChange,
}) => {
  //console.log(control._options.defaultValues);

  const navigate = useNavigate();
  const location = useLocation();

  const [state, setState] = useState({
    isFetching: false,
    multiple: true,
    searchQuery: null,
    value: "",
    options: [],
  });

  const handleEdit = (empresaContexto, id) => {
    id = Number(id);
    const find = state.options.find((element) => {
      return element.value === id;
    });

    navigate(model.link + id, {
      state: {
        backgroundLocation: location,
        model: find.model,
        empresaContexto,
      },
    });
  };

  const handleSearchChange = async (e, { searchQuery }) => {
    setState({ isFetching: true });
    const options = await getOptions(
      empresaContexto,
      model.keys,
      model.endPoint,
      model.params,
      searchQuery,
      moreRouteParams,
      venta
    );
    setState({ ...state, ...{ options, isFetching: false } });
  };

  const get = async () => {
    let options = await getOptions(
      empresaContexto,
      model.keys,
      model.endPoint,
      model.params,
      {
        limit: 20,
        order: { id: "DESC" },
      },
      moreRouteParams,
      venta
    );

    if (!!venta && venta[model?.name_model]) {
      const choose = await parseData([venta[model?.name_model]], model.keys);
      /**verificar que el choose se encuentre en los resultados */
      const find = options.find((res) => {
        if (res.value === choose[0].value) {
          return true;
        } else {
          return false;
        }
      });
      if (find) {
        return options;
      } else {
        choose[0].key = options.length;
        options.push(choose[0]);
        return options;
      }
    }
    return options;
  };

  useEffect(() => {
    get().then(
      (options) => {
        setState({ ...state, ...{ options, isFetching: false } });
      },
      (err) => {
        console.log(err);
        alert(
          "efecto fallo posiblemente la respuesta no esta en formato adecuado [data]=>{}"
        );
      }
    );
  }, []);

  useEffect(() => {
    if (!!location?.state && location?.state?.reload) {
      console.log(location.state, "- Has changed");
      console.log(location.state, location.state.reload);
      const data = parseData([location.state?.reload[model.name]], model.keys);
      setState({ options: data });

      setValue(model.name, location?.state?.reload[model.name]?.id);
      setValue(model.name_model, location?.state?.reload[model?.name]);
    }
  }, [location.state?.reload?.[[model.name]]]);

  return (
    <Controller
      name={model.name}
      control={control}
      render={({ field: { onChange, onBlur, value } }) => (
        <Form.Field width={width || 5}>
          <label>
            {model.title}{" "}
            {!location?.state?.backgroundLocation &&
              model.link &&
              !!state?.options?.length &&
              !!value && (
                <Icon
                  onClick={() => {
                    handleEdit(empresaContexto, value, setValue);
                  }}
                  name="pencil"
                  size="small"
                />
              )}
          </label>
          <Dropdown
            searchInput={{ autoFocus }}
            clearable
            options={state.options}
            placeholder={model.placeholder}
            onChange={(e, element) => {
              setValue(model.name, element.value);
              const selec = state.options.find((e) => {
                return e.value === element.value;
              });
              setValue(model?.name_model, selec?.model);
              if (onListenChange) {
                onListenChange(element);
              }
            }}
            onSearchChange={handleSearchChange}
            loading={state.isFetching}
            search
            value={parseInt(value)}
            selection
            noResultsMessage={
              model.link ? (
                <Link
                  to={model.link + "create"}
                  state={{
                    backgroundLocation: location,
                    model: {
                      local_id: empresaContexto.id,
                      empresa_id: empresaContexto.empresa.id,
                    },
                  }}
                >
                  Agregar Nuevo
                </Link>
              ) : (
                "Sin Resultados"
              )
            }
          />
        </Form.Field>
      )}
    />
  );
};
export default FormFielDropdownSarch;
