import React, { useContext } from "react";
import { Button, Label } from "semantic-ui-react";
import EmpresaContext from "../context/EmpresaContext";
import Path from "../services/_path";
require("dotenv");

const FormFielFileController = ({
  type,
  name,
  icon,
  title,
  value,
  color,
  service,
  setValue,
  isContextEmpresa, // para decirle que afecta al contexto de la empresa y se hara una mutacion
}) => {
  const { empresa, setEmpresa } = useContext(EmpresaContext);

  const fileInputRef = React.createRef();

  const PATH_PUBLIC = Path("public");

  const fileChange = async (e) => {
    const file = e.target.files[0];
    const body = await service(empresa.empresa.id, file);
    setValue(name, body.success);

    empresa.empresa.certificado = body.success;

    if (name === "certificado") {
      setValue("certificado_validate", 0);
    }

    if (name === "logo") {
      setValue("logo", body.success);
    }

    if (isContextEmpresa) {
      setEmpresa(empresa);
    }

    console.log(empresa);
  };

  return (
    <>
      {type === "imagen" && (
        <>
          <label>{title}</label>
          <Label
            as="a"
            onClick={(e) => {
              e.preventDefault();
              fileInputRef.current.click();
            }}
          >
            <img
              src={`${PATH_PUBLIC}/storage/${empresa?.empresa?.id}/${value}`}
              alt="logo_empresa"
            />{" "}
            {value}
          </Label>
        </>
      )}

      {type === "file" && (
        <>
          <label>{title}</label>
          <Button
            as={"a"}
            color={color}
            fluid
            content={value || "Certificado"}
            labelPosition="left"
            icon={icon || "file"}
            onClick={(e) => {
              e.preventDefault();
              fileInputRef.current.click();
            }}
          />
        </>
      )}
      <input ref={fileInputRef} hidden type="file" onChange={fileChange} />
    </>
  );
};
export default FormFielFileController;
