import { useEffect, useState } from "react";
import {
  Container,
  Grid,
  Header,
  Statistic,
  Icon,
  Card,
} from "semantic-ui-react";
import Localbase from "localbase";
import { Link, useNavigate } from "react-router-dom";
import currency from "../../libs/currency";
const Dashboard = () => {
  const db = new Localbase("db");
  const [ventas, setVentas] = useState([]);

  const [nroVentas, setNroVentas] = useState(0);
  const [nroCobroPendiente, setNroCobroPendiente] = useState(0);

  const [totalVentas, setTotalVentas] = useState(null);
  const [totalVentasMensuales, setTotalVentasMensuales] = useState(null);
  const [porCobros, setPorCobros] = useState(null);
  const [totalCobros, setTotalCobros] = useState(null);

  const navigate = useNavigate();

  const resolverStadisticaTotalVentasMensuales = async (ventas) => {
    const mensuales = [];
    let year = new Date().getFullYear();
    let mes = new Date().getMonth();
    let day = new Date().getDate();

    let days = Array(day)
      .fill(new Date(year, mes, 1))
      .map((v, i) =>
        new Date(new Date(v).setDate(v.getDate() + i))
          .toLocaleDateString("es-CL")
          .slice(0, 10)
      );

    for (let i = 0; i < ventas.length; i++) {
      const el = ventas[i];
      if (el.tipo_operacion_id === 1) {
        if (
          days.indexOf(
            new Date(el.fecha_emision).toLocaleDateString("es-CL").substr(0, 10)
          ) !== -1
        ) {
          console.log(">>>>>>>______", el.tipo_operacion_id);
          mensuales.push(el);
        }
      }
    }

    await setNroVentas(mensuales.length);

    let res = resolverStadisticaTotalVentas(mensuales);

    await setTotalVentasMensuales(res);
  };

  const resolverStadisticaTotalVentas = (ventas) => {
    const res = {};
    res.title = "Ventas";
    res.mensaje = "Ultimos 25 Documento";
    res.detalles = {};

    for (let i = 0; i < ventas.length; i++) {
      const el = ventas[i];
      if (el.tipo_operacion_id === 1) {
        if (!res.detalles.hasOwnProperty(el.moneda.nombre)) {
          res.detalles[el.moneda.nombre] = {
            iso: el.moneda.iso,
            simbolo: el.moneda.simbolo,
            total: el.total_documento_fin,
          };
        } else {
          res.detalles[el.moneda.nombre].total += el.total_documento_fin;
        }
      }
    }
    return res;
  };

  const resolverStadisticaTotalCobros = (ventas) => {
    const res = {};
    res.title = "Cobros";
    res.mensaje = "Ultimos 25 Documento";
    res.detalles = {};

    let nroCobrosPendientes = 0;

    for (let i = 0; i < ventas.length; i++) {
      const el = ventas[i];
      if (!res.detalles.hasOwnProperty(el.moneda.nombre)) {
        res.detalles[el.moneda.nombre] = {
          iso: el.moneda.iso,
          simbolo: el.moneda.simbolo,
          total: el.total_documento_fin - el.pago_status,
        };
        nroCobrosPendientes++;
      } else {
        res.detalles[el.moneda.nombre].total +=
          el.total_documento_fin - el.pago_status;
        nroCobrosPendientes++;
      }
    }

    setNroCobroPendiente(nroCobrosPendientes);
    return res;
  };

  const getVentas = () => {
    db.collection("ventas")
      .get()
      .then(async (ventas) => {
        let items = ventas.filter((v, k) => {
          let _fecha = new Date(v.fecha_emision)
            .toLocaleDateString("es-CL")
            .substr(0, 10);

          if (
            _fecha === new Date().toLocaleDateString("es-CL").substr(0, 10) &&
            v.tipo_operacion_id === 1
          ) {
            return v;
          }
        });
        await setVentas(items);

        let resp = resolverStadisticaTotalVentas(items);
        let ress = resolverStadisticaTotalVentasMensuales(ventas);

        setTotalVentas(resp);
        console.log({ items });
      });
  };

  const getPendientesPorCobrar = () => {
    db.collection("ventas")
      .get()
      .then(async (ventas) => {
        let items = ventas.filter((v, k) => {
          if (
            v.tipo_operacion_id === 1 &&
            v.forma_pago_id === 2 &&
            v.total_documento_fin - v.pago_status > 0.001
          ) {
            console.log(
              "correlativo" + v.correlativo,
              v.moneda.simbolo,
              v.total_documento_fin,
              v.pago_status,
              v.total_documento_fin - v.pago_status
            );
            return v;
          }
        });
        await setPorCobros(items);
        let resolve = resolverStadisticaTotalCobros(items);
        await setTotalCobros(resolve);
      });
  };

  const handlePorPagar = () => {
    navigate("/workspace/ventas", {
      state: { porcobrar: true },
    });
  };

  useEffect(() => {
    getVentas();
    getPendientesPorCobrar();
  }, []);

  return (
    <Container>
      <Grid.Column style={{ marginTop: "10px", padding: 10 }}>
        <Grid.Row>
          <Header as="h1">Inicio</Header>
        </Grid.Row>
        <br />
        <Grid.Row>
          <Header as="h4">Dia de Hoy</Header>
        </Grid.Row>
        <Grid.Row>
          <Statistic.Group
            size="tiny"
            widths={
              totalVentas ? 1 + Object.keys(totalVentas?.detalles).length : 1
            }
          >
            <Statistic>
              <Statistic.Value>{ventas.length}</Statistic.Value>
              <Statistic.Label>Ventas </Statistic.Label>
            </Statistic>

            {!!totalVentas &&
              Object.keys(totalVentas.detalles).map((moneda, index) => (
                <Statistic key={index}>
                  <Statistic.Value text>
                    {totalVentas.detalles[moneda].simbolo}{" "}
                    {currency(totalVentas.detalles[moneda].total)}
                    {" ("}
                    {totalVentas.detalles[moneda].iso}
                    {") "}
                  </Statistic.Value>
                  <Statistic.Label>{String(moneda)}</Statistic.Label>
                </Statistic>
              ))}
          </Statistic.Group>
        </Grid.Row>
        <Grid.Row>
          <Card.Group>
            <Card fluid>
              <Card.Content>
                <Card.Header>Mensuales</Card.Header>
                <Card.Meta>Ventas {nroVentas}</Card.Meta>

                {!!totalVentasMensuales &&
                  Object.keys(totalVentasMensuales.detalles).map((v, index) => (
                    <Card.Description
                      key={index}
                      style={{ textAlign: "right" }}
                    >
                      {totalVentasMensuales.detalles[v].simbolo}{" "}
                      {currency(totalVentasMensuales.detalles[v].total)}
                      {" ("}
                      {totalVentasMensuales.detalles[v].iso}
                      {") "}
                    </Card.Description>
                  ))}
              </Card.Content>
            </Card>

            <Card fluid as={"a"} onClick={handlePorPagar}>
              <Card.Content>
                <Card.Header content="Cuentas Pendientes por Cobro" />
                <Card.Meta content={"Ventas " + nroCobroPendiente} />
                {!!totalCobros &&
                  !!!totalCobros.length &&
                  Object.keys(totalCobros?.detalles).map((v, index) => (
                    <Card.Description
                      key={index}
                      style={{ textAlign: "right" }}
                    >
                      {totalCobros.detalles[v].simbolo}{" "}
                      {totalCobros.detalles[v].total?.toFixed(2)}
                      {" ("}
                      {totalCobros.detalles[v].iso}
                      {") "}
                    </Card.Description>
                  ))}
              </Card.Content>
            </Card>
          </Card.Group>
        </Grid.Row>
      </Grid.Column>
    </Container>
  );
};

export default Dashboard;
