import moment from "moment";

export default {
  format: function (date) {
    let ss = date.split(" ");
    let s = ss[0].split("-");
    return `${s[2]}/${s[1]}/${s[0]}`;
  },
  /**
   *
   * @param {new Date('')} date
   * @return string date
   */
  formatToInputSemantic: function (date) {
    return moment(date).format('YYYY-MM-DD');
  },
};
